
import { Component } from "vue-property-decorator";
import Product from "../../../interfaces/Product";
import { Getter, namespace } from "vuex-class";
import Carousel from "@/components/AppSimulator/UI/Carousel.vue";
import { Navigation } from "@/interfaces/Store";
import { ConfiguratorState, FormData } from "@/interfaces/ConfigurationsType";
import { mixins } from "vue-class-component";
import WishlistAware from "@/mixins/WishlistAware";
import ImageDisplayer from "@/mixins/ImageDisplayer";
import { SimulatorState } from "@/interfaces/Simulator";

const configuration = namespace("configuration");
const simulator = namespace("simulator");
@Component({
  components: {
    Carousel,
  },
})
export default class DetailPage extends mixins(WishlistAware, ImageDisplayer) {
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @simulator.State((state: SimulatorState) => state.products)
  allProducts!: Product[];
  @Getter appDarkMode!: boolean;
  selectedItems: Product | null = null;

  mounted() {
    this.$emit("initComponent");
  }

  get activeProduct(): any | null {
    if (this.selectedItems) return this.selectedItems;

    const defaultProduct = this.allProducts;
    const nav: Navigation = this.$store.getters["currentNavigation"];

    this.selectedItems = nav.context as Product;
    return nav.name === "detail"
      ? nav.context
        ? (nav.context as Product)
        : defaultProduct[0]
      : null;
  }

  get buttonClass(): string {
    return "add-to-cart" + (this.appDarkMode ? " is-dark" : "");
  }

  get availableMessage(): string {
    if (this.activeProduct !== null) {
      return this.activeProduct.quantity_available === 0
        ? "TERMINATED"
        : "IN STOCK";
    }

    return "TERMINATED";
  }

  get buttonText(): string {
    if (this.activeProduct !== null) {
      return this.activeProduct.quantity_available === 0
        ? "Notify when available"
        : "Add to cart";
    }

    return "Notify when available";
  }

  get isDiscount(): boolean {
    if (this.activeProduct !== null) {
      return (
        this.activeProduct.full_price !== this.activeProduct.discounted_price
      );
    } else {
      return false;
    }
  }

  get images() {
    return this.activeProduct.images
      ? this.activeProduct.images.map((d) => ({ image: d.image_medium }))
      : [
          {
            image: this.activeProduct.image
              ? this.previewImage(this.activeProduct.image)
              : null,
          },
        ];
  }

  get description() {
    return this.activeProduct.description_short
      ? this.stripAndTruncateHtml(this.activeProduct.description_short)
      : "Description";
  }

  stripAndTruncateHtml = (html) => {
    const div = document.createElement("DIV");
    div.innerHTML = html;
    const toTruncate = div.textContent || div.innerText || "";
    return toTruncate.substring(0, 40) + "...";
  };
}
