import Vue from "vue";
import { ValidationProvider, ValidationObserver } from "vee-validate";
import { extend, configure } from "vee-validate";
import { required, max, email } from "vee-validate/dist/rules";
import i18n from "@/plugins/translations";

Vue.component("ValidationProvider", ValidationProvider);
Vue.component("ValidationObserver", ValidationObserver);

configure({
  defaultMessage: (field, values) => {
    return i18n.t(`validation.${values._rule_}`, values) as string;
  },
});
extend("required", required);
extend("max", max);
extend("email", email);

extend("langRequired", {
    validate(value, params) {

        const array = [] as boolean[];

        params.forEach((lang) => {
          array.push(!(value[lang] === "" || value[lang] === null || value[lang] === undefined));
        });

        return array.every((d) => d);
    },
    message(field) {
        return field + ' ' + i18n.t('validation.requestInAllLang')
    },
  });


  extend("imageLangRequired", {
    validate(value, params) {

        const array = [] as boolean[];

        params.forEach((lang) => {
          array.push(value[lang] === "" || value[lang] === null || value[lang] === undefined ? false : true);
        });

        return array.every((d) => d === true);

        // return array.every((d) => d === true);
    },
    message(field) {
        return field + ' ' + i18n.t('validation.requestInAllLang')
    },
  });

extend("imageRequired", (value) => {
  return !!(value && value.url && value.name);
});

extend("relatedRequired", (value) => {
  return !!(value && value.id && value.name);
});

extend("multilangLen", {
  validate(value, params) {
    console.log(value, params);
    let retval = true;
    if (typeof value == "object" && value !== null) {
      const p = params as string[];
      const min = Math.max(+p[0], 0);
      const max = Math.max(+p[1], 0);

      for (const [lang, text] of Object.entries(value)) {
        if (!text) {
          continue;
        }

        const len = ("" + text).length;
        retval = retval && len >= min && len <= max;
        if (!retval) {
          break;
        }
      }
    }

    return retval;
  },
  message(field, params) {
    const p = params as string[];
    const min = Math.max(+p[0], 0);
    const max = Math.max(+p[1], 0);
    return i18n.t("validation.multilangLen", {
      _field_: field,
      min,
      max,
    }) as string;
  },
});
