
import { Component, Vue, ModelSync } from "vue-property-decorator";
import DialogModal from "@/components/AppSettings/UI/DialogModal.vue";
import { DialogModalConfig } from "@/interfaces/DialogModal";

@Component({
  components: {
    DialogModal,
  },
})
export default class DeleteDialogModal extends Vue {
  @ModelSync("item", "change", { type: Object }) value!: any;

  get showDeleteDialog(): boolean {
    return this.value != null;
  }

  get config(): DialogModalConfig {
    const message = this.$t("deleteDialogMessage") as string;
    return {
      title: this.$t("deleteDialogTitle") as string,
      message,
      buttons: [
        {
          title: this.$t("confirmDeleteDialogButton") as string,
          color: "primary",
          onPress: () => this.onDelete(),
        },
        {
          title: this.$t("cancelDeleteDialogButton") as string,
          color: "blue darken-1",
          isText: true,
          onPress: () => this.$emit("change", null),
        },
      ],
    };
  }

  onDelete() {
    this.$emit("delete");
  }
}
