import axios from "@/services/axios";
import Category from "@/interfaces/Category";
import { CatalogLayoutType } from "@/interfaces/ConfigurationsType";
import AbstractApiService from "./AbstractApiService";
import { AxiosResponse } from "axios";

class CategoryService extends AbstractApiService {
  protected action: string = "categories";

  async getCategories(idLayout?: CatalogLayoutType): Promise<Category[]> {
    if (idLayout == null) {
      idLayout = CatalogLayoutType.image;
    }

    const url = this.getUrl(undefined, {
      id_layout: idLayout,
    });
    const response: AxiosResponse<Category[]> = await axios.get(url);
    return response.data;
  }
}

export default new CategoryService();
