import { ActionTree, GetterTree, Module, MutationTree } from "vuex";
import { RootState } from "@/interfaces/Store";
import {
  AppSteppersData,
  ChangeFile,
  FileType,
  FormData,
} from "../../interfaces/AppSteppers";
import { Language, StepDocTranslation } from "@/interfaces/Language";
import HomeSection from "@/interfaces/HomeSection";

import FileUploadService from "@/services/FileUploadService";
import { Color } from "@/interfaces/Color";
import { PaymentModule } from "@/interfaces/Module";

import { SelectItemPack } from "@/interfaces/Select";
import { UploadedImage } from "@/interfaces/Upload";
import { CatalogLayoutType } from "@/interfaces/ConfigurationsType";

export interface AppSteppersState {
  data: AppSteppersData;
  validStepMap: { [key: number]: boolean };
  selectItems: SelectItemPack;
}

const state: AppSteppersState = {
  validStepMap: {},
  selectItems: {
    bannerTypeItems: [],
    bannerSizeItems: [],
    layoutTypeItems: [],
    orderTypeItems: [],
    homeSectionTypeItems: [],
    catalogLayoutTypeItems: [],
    navigationTypeItems: [],
    productLayoutTypeItems: [],
    appFontTypeItems: [],
    productsShapeTypeItems: [],
    scheduledNotificationItems: [],
    webhookNotificationItems: [],
    customerGroups: [],
    carouselTypeItems: [],
    cmsPageTypes: [],
  },
  data: {
    isLogged: false,
    isSubscribed: false,
    componentDocs: null,
    qrCode: null,
    formLanguageIsoCode: "it",
    languages: [],
    appStatus: "online",
    form: {
      name: "",
      icon: null,
      splash: null,
      mainColor: "",
      textMainColor: "",
      secondaryColor: "",
      textSecondaryColor: "",
      isBlackText: true,
      navigationType: "tab",
      catalogLayoutType: 0,
      productsLayoutType: "two",
      appFont: null,
      productsShape: "SQUARE",
      showStockQt: false,
      showAvailable: true,
      showLogo: false,
      showPaypal: true,
      showSocialLogin: true,
      facebookEnabled: true,
      googleEnabled: true,
      ageEnabled: true,
      firebaseConfiguration: {
        active: false,
        name: null,
        value: null,
      },
      minAge: 0,
      logo: null,
      homeSections: [],
    },
  }
};

const mutations: MutationTree<AppSteppersState> = {
  setLanguages(state: AppSteppersState, languages: Language[]) {
    state.data = { ...state.data, languages: languages };
  },
  setStepValid(
    state: AppSteppersState,
    { step, valid }: { step: number; valid: boolean }
  ) {
    state.validStepMap = { ...state.validStepMap, [step]: valid };
  },
  setDefaultLanguage() {
    return;
  },
  setAppStatus(state: AppSteppersState, appStatus: "online" | "offline") {
    state.data = { ...state.data, appStatus };
  },
  setForm(state: AppSteppersState, form: FormData) {
    state.data = { ...state.data, form: form };
  },
  setStepperData(state: AppSteppersState, setup: AppSteppersData) {
    state.data = { ...state.data, ...setup };
  },
  setFormIcons(
    state: AppSteppersState,
    payload: { icon: UploadedImage | null; type: FileType }
  ) {
    state.data.form = { ...state.data.form, [payload.type]: payload.icon };
  },
  setFormLanguage(state: AppSteppersState, language: string) {
    state.data.formLanguageIsoCode = language;
  },
  setNavigationType(state: AppSteppersState, type: "tab" | "sidebar") {
    state.data.form.navigationType = type;
  },
  setCatalogLayoutType(state: AppSteppersState, type: CatalogLayoutType) {
    state.data.form.catalogLayoutType = type;
  },
  setProductLayoutType(state: AppSteppersState, type: "one" | "two") {
    state.data.form.productsLayoutType = type;
  },
  updateShowLogo(state: AppSteppersState, val: boolean) {
    state.data.form = { ...state.data.form, showLogo: val };
  },
  setColor(state: AppSteppersState, color: Color) {
    state.data.form[color.name] = color.value;
  },
  setHomeSectionActive(state: AppSteppersState, { type, id }) {
    state.data.form.homeSections = state.data.form.homeSections.map((s) =>
      s.id === id ? { ...s, active: type === "enable" } : s
    );
  },
  setHomeSectionOrder(state: AppSteppersState, { id, order }) {
    let scaleOrder: number = order;
    state.data.form.homeSections = state.data.form.homeSections.map((s) => {
      if (s.id === id) {
        return {
          ...s,
          order: order,
        };
      }

      if (s.order >= order && s.id !== id) {
        scaleOrder++;
        return {
          ...s,
          order: scaleOrder,
        };
      }

      return s;
    });
  },
  setHomeSections(state: AppSteppersState, homeSections: HomeSection[]) {
    state.data.form = { ...state.data.form, homeSections };
  },
  addHomeSection(state: AppSteppersState, newSection: HomeSection) {
    newSection.order = state.data.form.homeSections.length + 1;
    state.data.form.homeSections = [
      ...state.data.form.homeSections,
      newSection,
    ];
  },
  alignHomeSection(state: AppSteppersState, section: HomeSection) {
    state.data.form.homeSections = state.data.form.homeSections.map((s) => {
      return s.id === section.id ? section : s;
    });
  },
  deleteHomeSection(state: AppSteppersState, section: HomeSection) {
    const indexToBeDeleted = state.data.form.homeSections
      .map((s) => s.id)
      .indexOf(section.id);
    state.data.form.homeSections.splice(indexToBeDeleted, 1);
  },
  setBlackText(state: AppSteppersState, value: boolean) {
    state.data.form = { ...state.data.form, isBlackText: value };
  },
  setShowStockQt(state: AppSteppersState, value: boolean) {
    state.data.form = { ...state.data.form, showStockQt: value };
  },
  setShowAvailable(state: AppSteppersState, value: boolean) {
    state.data.form = { ...state.data.form, showAvailable: value };
  },
  triggerAgeEnabled(state: AppSteppersState, val: boolean) {
    state.data.form = { ...state.data.form, ageEnabled: val };

    if (!val) {
      // Reset field
      state.data.form = { ...state.data.form, minAge: null };
    }
  },
  setFirebaseJson(state: AppSteppersState, json: string | null) {
    if (json) {
      state.data.form.firebaseConfiguration.value = json;
    }
  },
  setMinAge(state: AppSteppersState, minAge: number) {
    state.data.form = { ...state.data.form, minAge };
  },
  setFacebookLogin(state: AppSteppersState, val: boolean) {
    state.data.form = { ...state.data.form, facebookEnabled: val };
  },
  setGoogleLogin(state: AppSteppersState, val: boolean) {
    state.data.form = { ...state.data.form, googleEnabled: val };
  },
  setShowPaypal(state: AppSteppersState, val: boolean) {
    state.data.form = { ...state.data.form, showPaypal: val };
  },
  setPaypalAccessToken(state: AppSteppersState, { isSandbox, accessToken }) {
    const objectKey: "accessTokenLive" | "accessTokenSandbox" = isSandbox
      ? "accessTokenSandbox"
      : "accessTokenLive";
    state.data.form = { ...state.data.form, [objectKey]: accessToken };
  },
  setQrCode(state: AppSteppersState, val: string | null) {
    state.data = { ...state.data, qrCode: val };
  },
  setStepDocs(state: AppSteppersState, val: StepDocTranslation | null) {
    state.data = { ...state.data, componentDocs: val };
  },
  setLogged(state: AppSteppersState, val: boolean) {
    state.data = { ...state.data, isLogged: val };
  },
};

const getters: GetterTree<AppSteppersState, RootState> = {
  isLogged(state: AppSteppersState): boolean {
    return state.data.isLogged;
  },
  isSubscribed(state: AppSteppersState): boolean {
    return state.data.isSubscribed;
  },
  sections(state: AppSteppersState): HomeSection[] {
    return state.data.form.homeSections;
  },
  isSideNav(state: AppSteppersState): boolean {
    return state.data.form.navigationType === "sidebar";
  },
};

const actions: ActionTree<AppSteppersState, RootState> = {
  async uploadAppIcon({ commit }, file: ChangeFile) {
    // TODO call api
    if (file.file) {
      const fileImage = await FileUploadService.uploadFile(file);
      commit("setFormIcons", {
        type: file.type,
        file: fileImage,
      });
    } else {
      commit("setFormIcons", {
        type: file.type,
        file: null,
      });
    }
  },
};

const module: Module<AppSteppersState, RootState> = {
  state,
  mutations,
  getters,
  actions,
  namespaced: true,
};

export default module;
