
import { Component } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import { namespace, Getter } from "vuex-class";
import { ConfiguratorState, FormData } from "@/interfaces/ConfigurationsType";
import { UploadedImage } from "@/interfaces/Upload";
import SimulatorManager from "@/mixins/SimulatorManager";
import HomeSectionsTester from "@/mixins/HomeSectionsTester";
import HomeSection, { BannerSizes } from "@/interfaces/HomeSection";
import ManufacturesrList from "@/components/AppSimulator/UI/ManufactersList.vue";
import ProductsList from "@/components/AppSimulator/UI/ProductsList.vue";
import Banner from "@/components/AppSimulator/UI/Banner.vue";
import CarouselList from "@/components/AppSimulator/UI/CarouselList.vue";
import CategoriesList from "@/components/AppSimulator/UI/CategoriesList.vue";

const configuration = namespace("configuration");

@Component({
  components: {
    ManufacturesrList,
    ProductsList,
    Banner,
    CarouselList,
    CategoriesList,
  },
})
export default class HomePage extends mixins(
  SimulatorManager,
  HomeSectionsTester
) {
  @Getter appDarkMode!: boolean;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;

  mounted() {
    this.$emit("initComponent");
  }

  getColumns(section: HomeSection) {
    const col12 = { flex: "0 0 100%", "max-width": "100%" };
    const col6 = { flex: "0 0 50%", "max-width": "50%" };

    if (this.isBanner(section)) {
      return !section.bannerSize ||
        section.bannerSize === BannerSizes.SQUARE ||
        section.bannerSize === BannerSizes.RECTANGLE_HORIZONTAL ||
        section.bannerSize === BannerSizes.RECTANGLE_VERTICAL
        ? col12
        : col6;
    }

    return col12;
  }

  get logoSrc() {
    const logo: UploadedImage | null = this.appDarkMode
      ? this.form.darkLogo
      : this.form.logo;
    if (logo) {
      return logo.url;
    }

    return null;
  }
}
