
import { Component, Vue } from "vue-property-decorator";
import { Navigation } from "@/interfaces/Store";
import { namespace, Getter } from "vuex-class";
import { SelectItem } from "@/interfaces/Select";
import { Data } from "@/store/modules/data";

const data = namespace("data");
const simulator = namespace("simulator");

@Component
export default class GroupsFilter extends Vue {
  @Getter currentNavigation!: Navigation;
  @data.State((state: Data) => state.selectItems.customerGroups)
  customerGroups!: SelectItem[];
  @simulator.Mutation("setGroupToShow") setGroupToShow!: (
    groupToShow: any
  ) => void;

  groupToShow = 0;
}
