import HomeSection, {
  ActivationType,
  BannerSizes,
  BannerTypes,
  HomeSectionState,
  HomeSectionType,
  LayoutType,
} from "@/interfaces/HomeSection";
import { Component, Vue } from "vue-property-decorator";
import { Language, TranslatedContent } from "@/interfaces/Language";
import { namespace } from "vuex-class";
import moment from "moment";
import { SelectItem } from "@/interfaces/Select";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";

const configuration = namespace("configuration");
const sections = namespace("sections");
@Component
export default class HomeSectionsDisplayer extends Vue {
  @configuration.Getter("defaultLanguage") defaultLanguage!: string;
  @configuration.State((state: ConfiguratorState) => state.languages)
  languages!: Language[];
  @sections.State((state: HomeSectionState) => state.defaultOrderType)
  defaultOrderType!: SelectItem | null;

  getTranslatedTitle(title: TranslatedContent | null): string {
    if (title != null && title[this.defaultLanguage] != null) {
      return title[this.defaultLanguage];
    }

    return "";
  }

  getSectionTranslatedTitle(section: HomeSection): string {
    if (section.title != null && section.title[this.defaultLanguage] != null) {
      return section.title[this.defaultLanguage];
    }

    return "";
  }

  previewImage(item: HomeSection) {
    if (
      item.image != null &&
      Object.keys(item.image).length > 0 &&
      item.type !== HomeSectionType.CAROUSEL
    ) {
      if (item.hasMultiLangImage === true) {
        return item.image[this.defaultLanguage]?.url;
      }
      return item.image["original"]?.url;
    } else if (item.type === HomeSectionType.CAROUSEL) {
      if (item.banners.length > 0 && item.banners[0].image) {
        if (item.banners[0].hasMultiLangImage === true) {
          return item.banners[0].image[this.defaultLanguage]?.url;
        }
        return item.banners[0].image["original"]?.url;
      }
    }

    return "";
  }

  getFormattedDate(date: string): string {
    return (
      moment(date).format("DD/MM/YYYY ") + "<br>" + moment(date).format("HH:mm")
    );
  }

  get defaultSection(): HomeSection {
    let title: TranslatedContent = {};
    for (const lang of this.languages) {
      title = {
        ...title,
        [lang.value]: "",
      };
    }

    return {
      type: HomeSectionType.BANNER,
      carouselType: null,
      bannerType: BannerTypes.BANNER_NO_LINK,
      bannerSize: BannerSizes.RECTANGLE_HORIZONTAL,
      activeTo: null,
      activeFrom: null,
      active: false,
      order: 0,
      webPath: null,
      hasMultiLangImage: false,
      layout: LayoutType.VERTICAL,
      image: {
        original: null,
      },
      activationType: ActivationType.MANUALLY,
      orderType: this.defaultOrderType ? this.defaultOrderType.value : null,
      title,
      hideTitleInHome: false,
      showToAllGroups: true,
      groupsToShow: [],
      banners: [],
      categories: [],
    };
  }
}
