import HomeSection from "@/interfaces/HomeSection";
import { MutationTree, GetterTree, Module, ActionTree } from "vuex";
import { RootState } from "@/interfaces/Store";
import AppDataService from "@/services/AppDataService";
import { PaymentModule } from "@/interfaces/Module";
import { SelectItem, SelectItemPack } from "@/interfaces/Select";
import { AppScheduledNotification } from "@/interfaces/AppNotification";

export interface Data {
  paymentModules: PaymentModule[];
  selectItems: SelectItemPack;
}

const state: Data = {
  paymentModules: [],
  selectItems: {
    bannerTypeItems: [],
    bannerSizeItems: [],
    layoutTypeItems: [],
    orderTypeItems: [],
    homeSectionTypeItems: [],
    catalogLayoutTypeItems: [],
    navigationTypeItems: [],
    productLayoutTypeItems: [],
    appFontTypeItems: [],
    productsShapeTypeItems: [],
    scheduledNotificationItems: [],
    webhookNotificationItems: [],
    customerGroups: [],
    carouselTypeItems: [],
    cmsPageTypes: [],
  },
};

const mutations: MutationTree<Data> = {
  setPaymentModules(state: Data, paymentModules: PaymentModule[]) {
    state.paymentModules = [...paymentModules];
  },
  setSelectItems(state: Data, selectItems: SelectItemPack) {
    state.selectItems = { ...selectItems };
  },
};

const actions: ActionTree<Data, RootState> = {
  async getPaymentModules({ commit }) {
    const paymentModules = await AppDataService.getPaymentModules();
    commit("setPaymentModules", paymentModules);
  },
  async getSelectItems({ commit }) {
    const items: SelectItemPack = await AppDataService.getSelectItems();
    commit("setSelectItems", items);
    commit("sections/setDefaultOrderType", items.orderTypeItems[0], {
      root: true,
    });
  },
  async updatePaymentModuleInApp({ commit }, item: PaymentModule) {
    const modules = await AppDataService.enableOrDisablePaymentModule(item);
    commit('setPaymentModules', modules);
  }
};

const getters: GetterTree<Data, RootState> = {
  getSectionTypeStringFor(state: Data) {
    return (e: HomeSection) => {
      const typeItem = state.selectItems.homeSectionTypeItems.find(
        (selectItem: SelectItem) => selectItem.value === e.type
      );
      const bannerTypeItem = state.selectItems.bannerTypeItems.find(
        (selectItem: SelectItem) => selectItem.value === e.bannerType
      );
      return [typeItem, bannerTypeItem]
        .filter((e) => !!e)
        .map((e) => e!.text!)
        .join(": ");
    };
  },
  getNotificationTypeString(state: Data) {
    return (e: AppScheduledNotification) => {
      const item = state.selectItems.scheduledNotificationItems.find(
        (s) => s.value === e.notificationType
      );
      return item?.text;
    };
  },
};

const module: Module<Data, RootState> = {
  state,
  mutations,
  getters,
  actions,
  namespaced: true,
};
export default module;
