import { Component, Vue, Ref, Provide } from "vue-property-decorator";
import { ValidationObserver, extend } from "vee-validate";
import { regex } from "vee-validate/dist/rules";

extend("regex", {
  ...regex,
  message: "Whitespace not allowed",
});

@Component
export default class ValidableForm extends Vue {
  @Ref("observer") observer!: InstanceType<typeof ValidationObserver>;

  private validable: { [key: number]: () => Promise<boolean> } = {};

  private validableId = 0;

  @Provide("validation")
  pushValidable(res: () => Promise<boolean>): number {
    this.validableId++;
    this.validable[this.validableId] = res;
    return this.validableId;
  }

  @Provide("removeValidation")
  removeValidation(index: number): void {
    delete this.validable[index];
  }

  async validate(): Promise<boolean> {
    const res: Array<boolean> = await Promise.all([
      ...(Object.values(this.validable).map((e) => e()) as Promise<boolean>[]),
      this.observer.validate(),
    ]);

    return res.every((e) => e);
  }

  get getValidableId() {
    return this.validableId;
  }
}
