import { CarouselItem } from "@/interfaces/HomeSection";
import { ActionTree, GetterTree, MutationTree } from "vuex";
import HomeSection, {
  ActivationType,
  BannerTypes,
  HomeBulkActions,
  HomeSectionState,
  HomeSectionType,
  LayoutType,
} from "@/interfaces/HomeSection";
import SectionService from "@/services/SectionService";
import { SelectItem } from "@/interfaces/Select";
import { SearchableEntity } from "@/interfaces/SearchableEntity";
import { BulkActionHas } from "@/interfaces/BulkAction";
import { Count } from "@/interfaces/Api";

const state: HomeSectionState = {
  total: {
    count: 0,
  },
  sections: [],
  showEditor: false,
  editingSection: {},
  selected: [],
  defaultOrderType: null,
};

const mutations: MutationTree<HomeSectionState> = {
  setSections(state: HomeSectionState, sections: HomeSection[]) {
    state.sections = [...sections];
  },
  openEditor(
    state: HomeSectionState,
    { editingSection }: { editingSection?: HomeSection }
  ) {
    // if (!editingSection?.showToAllGroups) {
    //   editingSection!.showToAllGroups = true;
    // }

    state.editingSection =
      editingSection != null
        ? { ...editingSection }
        : {
            type: HomeSectionType.BANNER,
            carouselType: null,
            bannerType: BannerTypes.BANNER_NO_LINK,
            active: false,
            hasMultiLangImage: false,
            layout: LayoutType.VERTICAL,
            image: {
              original: null,
            },
            activationType: ActivationType.MANUALLY,
            orderType: state.defaultOrderType
              ? state.defaultOrderType.value
              : null,
            title: {},
            hideTitleInHome: false,
            showToAllGroups: true,
            groupsToShow: [],
            banners: [],
            categories: [],
          };
  },
  closeEditor(state: HomeSectionState) {
    state.showEditor = false;
    state.editingSection = {};
  },
  updateHomeSectionField(
    state: HomeSectionState,
    fields: Partial<HomeSection>
  ) {
    state.editingSection = {
      ...state.editingSection,
      ...fields,
    };
  },
  updateBannerItem(
    state: HomeSectionState,
    { index, fields }: { index: number; fields: Partial<CarouselItem> }
  ) {
    if (state.editingSection.banners) {
      const cloned = [...state.editingSection.banners];
      cloned[index] = {
        ...cloned[index],
        ...fields,
      };
      state.editingSection.banners = cloned;
    }
  },
  updateCategoryItems(state: HomeSectionState, items: SearchableEntity[]) {
    if (state.editingSection.categories) {
      state.editingSection.categories = items;
    }
  },
  pushSection(state: HomeSectionState, section: HomeSection) {
    const foundIndex = state.sections.findIndex(
      (e: HomeSection) => e.id == section.id
    );
    if (foundIndex > -1) {
      state.sections[foundIndex] = section;
    } else {
      state.sections = [...state.sections, section];
    }

    state.sections = [...state.sections].sort(
      (a: HomeSection, b: HomeSection) => {
        return a.order - b.order;
      }
    );
  },
  setSelected(
    state: HomeSectionState,
    { section, selected }: { section: HomeSection; selected?: boolean }
  ) {
    const newSelected =
      selected === undefined
        ? !state.selected.includes(section.id!)
        : selected;

    if (newSelected) {
      if (!state.selected.includes(section.id!)) {
        state.selected = [...state.selected, section.id!];
      }
    } else {
      state.selected = state.selected.filter((e: number) => e !== section.id);
    }
  },
  setAllSelected(state: HomeSectionState, selected: boolean) {
    if (selected) {
      state.selected = state.sections.map((e: HomeSection) => e.id!);
    } else {
      state.selected = [];
    }
  },
  resetSelected(state: HomeSectionState) {
    state.selected = [];
  },
  removeSection(state: HomeSectionState, section: HomeSection) {
    state.sections = state.sections.filter(
      (e: HomeSection) => e.id !== section.id
    );
    state.selected = state.selected.filter((e: number) => e !== section.id);
  },
  setDefaultOrderType(state: HomeSectionState, defaultOrder: SelectItem) {
    state.defaultOrderType = defaultOrder;
  },
  setCount(state: HomeSectionState, total: Count) {
    state.total = total;
  },
};

const actions: ActionTree<HomeSectionState, any> = {
  async count({ commit }) {
    const total = await SectionService.count();
    commit("pagination/setCount", total, {
      root: true,
    });
  },
  async find({ commit, rootState }) {
    const sections = await SectionService.find(rootState.pagination.filter);
    commit("setSections", sections);
  },
  async init({ commit }) {
    const sections: HomeSection[] = await SectionService.all();
    commit("setSections", sections);
  },
  async saveSection({ dispatch, commit, state }) {
    const section = await SectionService.saveSection(
      state.editingSection as HomeSection
    );
    commit("simulator/pushSection", section, {
      root: true,
    });
    dispatch("count");
  },
  async deleteSection({ dispatch, commit }, section: HomeSection) {
    await SectionService.deleteSection(section.id!);
    commit("removeSection", section);
    commit("simulator/removeSection", section, {
      root: true,
    });
    dispatch("count");
  },
  async changeActiveBulk({ dispatch, commit, state }, active: boolean) {
    if (state.selected.length == 0) {
      return;
    }

    const action = active
      ? HomeBulkActions.activate
      : HomeBulkActions.deactivate;

    const sections: HomeSection[] = await SectionService.bulkSectionAction(
      action,
      state.selected
    );
    commit("simulator/setSections", sections, {
      root: true,
    });
    commit("resetSelected");
    dispatch("count");
  },
  async deleteBulk({ dispatch, commit, state }) {
    if (state.selected.length == 0) {
      return;
    }

    const sections: HomeSection[] = await SectionService.bulkSectionAction(
      HomeBulkActions.remove,
      state.selected
    );
    commit("simulator/setSections", sections, {
      root: true,
    });
    commit("resetSelected");
    dispatch("count");
  },
  async orderSections({ dispatch, commit }, idsOrder: number[]) {
    if (idsOrder.length == 0) {
      return;
    }

    const sections: HomeSection[] = await SectionService.bulkSectionAction(
      HomeBulkActions.order,
      idsOrder
    );
    commit("simulator/setSections", sections, {
      root: true,
    });
    dispatch("count");
  },
};

const getters: GetterTree<HomeSectionState, any> = {
  elegibleHomeSectionTypes(
    state: HomeSectionState,
    getters: any,
    rootState: any
  ) {
    // return rootState.data.selectItems.homeSectionTypeItems.reduce(
    //   (acc: SelectItem[], elem: SelectItem) => {
    //     if (getters.isSectionTypeEligible(elem.value)) {
    //       acc.push(elem);
    //     }
    //     return acc;
    //   },
    //   []
    // );
    return rootState.data.selectItems.homeSectionTypeItems
  },
  isSectionTypeEligible(state: HomeSectionState) {
    const everEligible = [HomeSectionType.BANNER, HomeSectionType.CAROUSEL];

    return (type: HomeSectionType) => {
      if (everEligible.includes(type)) {
        return true;
      }

      if (state.editingSection.type && type === state.editingSection.type) {
        return true;
      }

      if (!state.sections.find((e: HomeSection) => e.type == type)) {
        return true;
      }

      return false;
    };
  },
  currentSections(state: HomeSectionState) {
    return state.sections;
  },
  isSectionSelected(state: HomeSectionState) {
    return (section: HomeSection) => state.selected.includes(section.id!);
  },
  hasSectionsSelected(state: HomeSectionState) {
    return state.selected.length > 0;
  },
  areAllSelected(state: HomeSectionState) {
    return state.selected.length >= state.sections.length;
  },
  selectedSections(state: HomeSectionState) {
    return state.sections.filter((e: HomeSection) =>
      state.selected.includes(e.id!)
    );
  },
  has(state: HomeSectionState): BulkActionHas {
    const selectedSections = state.sections.filter((e: HomeSection) =>
      state.selected.includes(e.id!)
    );
    return {
      enable: !selectedSections.some((e: HomeSection) => !e.active),
      disable: !selectedSections.some((e: HomeSection) => e.active),
      select: !(state.selected.length > 0),
    };
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
  namespaced: true,
};
