import { namespace } from 'vuex-class';
import { Component } from 'vue-property-decorator';
import { SimulatorState } from '@/interfaces/Simulator';
import HomeSection from '@/interfaces/HomeSection';
import { mixins } from 'vue-class-component';
import ShopifyManager from './ShopifyManager';

const simulator = namespace("simulator");
@Component
export default class SimulatorManager extends mixins(ShopifyManager) {
    @simulator.State((state: SimulatorState) => state.sections) sections!: HomeSection[];
    @simulator.Getter('activeSections') activeSections!: HomeSection[];
}
