
import { Component, Vue, Ref } from "vue-property-decorator";
import { namespace, State, Action } from "vuex-class";
import { AppSteppersState } from "@/store/modules/stepper";
import { mixins } from "vue-class-component";
import SettingsStepSwitcher from "@/mixins/SettingsStepSwitcher";
import { AppState } from "@/interfaces/Store";

import { StepDocTranslation } from "@/interfaces/Language";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";
import StatusMessage from "@/components/AppSettings/UI/StatusMessage.vue";
import PrestashopBillingManager from "@/mixins/PrestashopBillingManager";
import ShopifyManager from "@/mixins/ShopifyManager";

const appSteppers = namespace("appSteppers");
const configuration = namespace("configuration");
@Component({
  components: {
    StatusMessage,
  },
})
export default class AppSteppers extends mixins(
  ShopifyManager,
  SettingsStepSwitcher,
  PrestashopBillingManager
) {
  @configuration.State((state: ConfiguratorState) => state.currentStep)
  currentStep!: number;
  @configuration.Getter("hasQr") hasQr!: boolean;
  @configuration.Getter("qrUrl") qrUrl!: string;
  @configuration.Action("getAppQrCode") getAppQrCode!: () => void;
  @configuration.State((state: ConfiguratorState) => state.allowedStep)
  allowedStep!: number;
  @configuration.State((state: ConfiguratorState) => state.canSend)
  canSend!: boolean;
  @configuration.Mutation setCurrentStep!: (payload: number) => void;
  @configuration.Action("saveStep") saveStep!: (
    updateRequired: boolean
  ) => Promise<void>;
  @configuration.Action("requireApp") requireApp!: () => Promise<{ requestAppUrl?: string, appPlansUrl?: string }>;

  @appSteppers.State((state: AppSteppersState) => state.data.componentDocs)
  componentDocs!: StepDocTranslation | null;
  @State((state: AppState) => state.error) error!: string | undefined;
  @Ref("stepperSteps") stepsElems!: (Vue & {
    validate: (value?: any) => Promise<boolean>;
  })[];
  stepping = false;

  requestAppUrl: string | null = null;
  showQrCodeModal = false;
  loadingQr = false;

  prevStep() {
    if (this.currentStep > 1) {
      this.setCurrentStep(this.currentStep - 1);
    }
  }

  async next() {
    const valid = await this.validateStep();
    if (valid) {
      this.stepping = true;
      try {
        await this.saveStep(false);
        if (this.currentStep < this.stepperSteps.length) {
          this.setCurrentStep(this.currentStep + 1);
        } else if (this.currentStep === this.stepperSteps.length) {
          // Open modal with QRCode
          await this.getTestAppQrCode();
        }
      } finally {
        this.stepping = false;
      }
    }
  }

  async sendRequestApp() {
    // Call send app
    const sendAppResponse = await this.requireApp();
    if (!this.isShopify) {
      if (sendAppResponse.requestAppUrl && !this.isPrestashopBilling) {
        // Not trigger open customer panel button
        this.requestAppUrl = sendAppResponse.requestAppUrl;
      }
    } else if (this.isShopify && sendAppResponse.appPlansUrl) {
      window.location.replace(sendAppResponse.appPlansUrl);
    }
  }

  async getTestAppQrCode() {
    if (!this.hasQr) {
      return;
    }
    await this.getAppQrCode();
    this.showQrCodeModal = true;
  }

  toggleQrCodeModal() {
    this.showQrCodeModal = !this.showQrCodeModal;
  }

  stepChanged(event: number) {
    this.setCurrentStep(event);
  }

  get nextLabel() {
    return this.currentStep !== this.stepperSteps.length
      ? "stepperButtons.nextButton"
      : "stepperButtons.tryIt";
  }

  get isNextLoading() {
    return this.stepping || this.loadingQr;
  }

  get shopDescription() {
    const shopDescription = this.$store.state.shortDescription;
    const isMultistore = this.$store.state.isMultistore;
    return isMultistore === "true" ? shopDescription : null;
  }

  async validateStep(): Promise<boolean> {
    if (this.currentStep <= this.stepperSteps.length) {
      const current = this.stepsElems[this.currentStep - 1];
      let valid = true;
      valid = await current.validate();
      if (!valid) {
        this.scrollToError();
      }
      return valid;
    } else {
      return false;
    }
  }

  scrollToError() {
    // const element = document.getElementById("app-data");
    const error = this.$el.querySelector(
      ".warning--text:first-of-type, div.error--text:first-of-type"
    ) as any;
    if (error) {
      error!.scrollIntoView({ behavior: "smooth", block: "center" });
    }
  }

  get testAppButton(): string {
    return this.$t("stepperButtons.testAppButton") as string;
  }
}
