
import { Component } from "vue-property-decorator";
import { namespace } from "vuex-class";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";
import {
  AppNotificationHas,
  AppNotificationState,
  AppScheduledNotification,
} from "@/interfaces/AppNotification";
import { mixins } from "vue-class-component";
import HomeSectionsDisplayer from "@/mixins/HomeSectionsDisplayer";
import NotificationModal from "@/components/AppSettings/UI/NotificationModal.vue";
import WebhookNotificationModal from "@/components/AppSettings/UI/WebhookNotificationModal.vue";
import DialogModal from "@/components/AppSettings/UI/DialogModal.vue";
import BulkActionButton from "@/components/AppSettings/UI/BulkActionButton.vue";
import DeleteDialogModal from "@/components/AppSettings/UI/DeleteDialogModal.vue";
import PaginationController from "@/components/AppSettings/UI/PaginationController.vue";
import { Filter } from "@/interfaces/Api";

const notifications = namespace("notifications");
const data = namespace("data");
@Component({
  components: {
    CardMaterial,
    NotificationModal,
    DialogModal,
    WebhookNotificationModal,
    BulkActionButton,
    DeleteDialogModal,
    PaginationController,
  },
})
export default class NotificationTab extends mixins(HomeSectionsDisplayer) {
  @data.Getter("getNotificationTypeString") getNotificationTypeString!: (
    e: AppScheduledNotification
  ) => string;

  @notifications.Action("count") count!: () => Promise<void>;
  @notifications.Action("find") find!: (filter: Filter) => Promise<void>;
  @notifications.Getter("has") has!: AppNotificationHas;
  @notifications.Getter("allSelected") allSelected!: boolean;
  @notifications.Mutation("setAllSelected") setAllSelected!: (
    reset?: boolean
  ) => void;
  @notifications.Mutation("setEditing") setEditing!: (
    editing?: Partial<AppScheduledNotification>
  ) => void;
  @notifications.State((state: AppNotificationState) => state.notifications)
  notifications!: AppScheduledNotification[];
  @notifications.State((state: AppNotificationState) => state.selected)
  selected!: AppScheduledNotification[];
  @notifications.Action("delete") delete!: (id: number) => Promise<void>;
  @notifications.Mutation("addOrRemoveSelection")
  addOrRemoveSelection!: (config: {
    item: AppScheduledNotification;
    selected?: boolean;
  }) => void;
  @notifications.Action("execBulkAction") execBulkAction!: (
    type: string
  ) => Promise<void>;

  showModal = false;
  showWebhookModal = false;
  itemToDelete: AppScheduledNotification | null = null;
  closeOnClick = true;

  async mounted() {
    await this.count();
  }

  async deleteAppNotification() {
    if (this.itemToDelete == null || this.itemToDelete.id == null) {
      return;
    }
    await this.delete(this.itemToDelete.id);
    this.itemToDelete = null;
  }

  toggleWebhookModal() {
    this.showWebhookModal = !this.showWebhookModal;
  }

  openModal(editing?: Partial<AppScheduledNotification>) {
    this.setEditing(editing);
    this.showModal = true;
  }

  closeModal() {
    this.showModal = false;
  }

  openDeleteModal(item: AppScheduledNotification) {
    this.itemToDelete = item;
  }

  isItemSelected(item: AppScheduledNotification) {
    const index = this.selected.findIndex((s) => s.id === item.id);
    return index > -1;
  }

  get tableCols(): string[] {
    return [
      "id",
      "sentDate",
      "type",
      "title",
      "active",
      "sent",
      "updateAction",
    ];
  }

  mobileGridItemStyle(item: AppScheduledNotification): any {
    return this.isItemSelected(item) ? "#eeeeee" : undefined;
  }
}
