
import { Component } from "vue-property-decorator";
import { Mutation, namespace, Getter } from "vuex-class";
import { Navigation } from "@/interfaces/Store";
import HomeSectionsComponent from "@/mixins/HomeSectionComponent";
import { mixins } from "vue-class-component";
import { dragscroll } from "vue-dragscroll";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";
const configuration = namespace("configuration");
const simulator = namespace("simulator");

@Component({
  directives: {
    dragscroll,
  },
})
export default class ManufacturersList extends mixins(HomeSectionsComponent) {
  @Mutation setNavigation!: (payload: Navigation) => void;
  @simulator.Mutation setManufacturerId!: (payload: any) => void;
  @Getter appDarkMode!: boolean;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;

  dragged = false;

  get manufacturers() {
    return this.item && this.item.items ? this.item.items : [];
  }

  async goTomManufacturerPage(manufacturer) {
    if (this.dragged) return null;
    this.setManufacturerId(manufacturer.id);
    this.setNavigation({
      name: "list",
      listType: "manufacturers",
      context: { name: manufacturer.name } as any,
    });
  }
}
