import { Count, Filter } from "@/interfaces/Api";
import { CmsPage } from "@/interfaces/CmsPage";
import axios from "@/services/axios";
import { AxiosResponse } from "axios";
import AbstractApiService from "./AbstractApiService";

class CmsPageService extends AbstractApiService {
  protected action: string = "cms";

  async count(): Promise<Count> {
    const url = this.getCountUrl();
    const response: AxiosResponse<Count> = await axios.get(url);
    return response.data;
  }

  async find(filter: Filter): Promise<CmsPage> {
    const url = this.filterUrl(filter);
    const response: AxiosResponse<CmsPage> = await axios.get(url);
    return response.data;
  }

  async addPage(idCms: number, simpleCms: boolean): Promise<CmsPage> {
    const url = this.getUrl();
    const response: AxiosResponse<CmsPage> = await axios.post(url, {
      id_cms: idCms,
      simple_cms: simpleCms
    });
    return response.data;
  }

  async delete(cmsPage: CmsPage): Promise<boolean> {
    const url = this.getUrl(cmsPage.id);
    const response: AxiosResponse<boolean> = await axios.delete(url);
    return response.data;
  }
}

export default new CmsPageService();
