
import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import { CustomerState } from "@/store/modules/customer";

const customer = namespace("customer");

@Component({})
export default class StatusMessage extends Vue {
  @customer.State((e: CustomerState) => e)
  status!: CustomerState | null;

  get priorityMessage() {
    return this.status ? this.status.priorityMessage ?? null : null;
  }
}
