import { ImageTranslatedContent } from "@/interfaces/Language";
import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";

const configuration = namespace("configuration");
@Component
export default class ImageDisplayer extends Vue {
  @configuration.Getter("defaultLanguage") defaultLanguage!: string;

  previewImage(
    image: ImageTranslatedContent | any,
    hasMultiLangImage: boolean = false
  ) {
    if (hasMultiLangImage === true) {
      return image[this.defaultLanguage]?.url;
    }
    return image["original"] ? image["original"].url : image.url ?? image;
  }
}
