
import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import {
  AppNotificationState,
  WebhookNotificationType,
} from "@/interfaces/AppNotification";
import { Data } from "@/store/modules/data";
import { SelectItem } from "@/interfaces/Select";

const data = namespace("data");
const notifications = namespace("notifications");
@Component
export default class WebhookNotificationModal extends Vue {
  @data.State((state: Data) => state.selectItems.webhookNotificationItems)
  webhookNotificationItems!: SelectItem[];

  @notifications.Action("findEnabledWebhooks")
  findEnabledWebhooks!: () => Promise<void>;
  @notifications.Action("saveEnabledWebhooks")
  saveEnabledWebhooks!: () => Promise<void>;
  @notifications.State(
    (state: AppNotificationState) => state.webhookNotifications
  )
  webhookNotifications!: WebhookNotificationType[];
  @notifications.Mutation("setWebhookNotifications") setWebhookNotifications!: (
    webhook: WebhookNotificationType[]
  ) => void;

  loading = false;
  isMounted = false;

  async mounted() {
    await this.findEnabledWebhooks();
    this.isMounted = true;
  }

  async onSave() {
    this.loading = true;
    try {
      await this.saveEnabledWebhooks();
      this.loading = false;
    } catch (e) {
      this.loading = false;
    }
  }

  onClose() {
    this.$emit("close");
  }
}
