
import { Component, Prop } from "vue-property-decorator";
import AppStatus from "@/components/AppSettings/UI/AppStatus.vue";
import DialogAppStatus from "@/components/AppSettings/UI/DialogAppStatus.vue";
import DialogAppDocumentation from "@/components/AppSettings/UI/DialogAppDocumentation.vue";
import { mixins } from "vue-class-component";
import ShopifyManager from "@/mixins/ShopifyManager";

@Component({
  components: {
    DialogAppDocumentation,
    AppStatus,
    DialogAppStatus,
  },
})
export default class HeaderActions extends mixins(ShopifyManager) {
  @Prop() stepperMode!: boolean;
  clearingCache = false;
  refreshStatus = false;
  showAppPreview = true;
  showStatusDialog = false;

  showDocsDialog = false;

  hideButtons = true;
  hideMenu = true;

  triggerAppStatus() {
    this.refreshStatus = !this.refreshStatus;
  }

  async clearCache() {
    console.log("clean");
    this.clearingCache = true;
    try {
      await this.$cache.clearCache();
    } finally {
      this.clearingCache = false;
    }
  }
  toggleAppPreview() {
    this.showAppPreview = !this.showAppPreview;
    this.$emit("clearCount", this.showAppPreview);
  }

  toggleHeaderMenu() {
    this.hideMenu = !this.hideMenu;
  }

  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  }
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  }
  handleResize() {
    this.hideButtons = window.innerWidth < 1440;
    if (!this.hideButtons) {
      this.hideMenu = true;
    }
  }
}
