
import { Component } from "vue-property-decorator";
import { namespace } from "vuex-class";
import MultilangField from "@/components/AppSettings/UI/MultilangField.vue";
import {
  AppNotificationState,
  AppScheduledNotification,
} from "@/interfaces/AppNotification";
import DateTimePicker from "@/components/AppSettings/UI/DateTimePicker.vue";
import { mixins } from "vue-class-component";
import ValidableForm from "@/mixins/ValidableForm";
import AppNotificationManager from "@/mixins/AppNotificationManager";
import { Data } from "@/store/modules/data";
import { SelectItem } from "@/interfaces/Select";
import EntitySearcher from "@/components/AppSettings/UI/EntitySearcher.vue";

const notifications = namespace("notifications");
const data = namespace("data");
@Component({
  components: {
    MultilangField,
    DateTimePicker,
    EntitySearcher,
  },
})
export default class NotificationModal extends mixins(
  ValidableForm,
  AppNotificationManager
) {
  @data.State((state: Data) => state.selectItems.customerGroups)
  customerGroups!: SelectItem[];
  @data.State((state: Data) => state.selectItems.scheduledNotificationItems)
  notificationTypes!: SelectItem[];
  @notifications.State(
    (state: AppNotificationState) => state.editingNotification
  )
  editingNotification!: AppScheduledNotification;
  @notifications.Mutation("updateEditingForm") updateEditingForm!: (
    e: Partial<AppScheduledNotification>
  ) => void;
  @notifications.Action("save") save!: (
    payload: AppScheduledNotification
  ) => Promise<void>;

  loading = false;

  async onSave() {
    try {
      this.loading = true;
      const valid = await this.validate();
      if (valid) {
        const sentDate = typeof this.editingNotification.sentDate === 'string' ? new Date(this.editingNotification.sentDate).getTime() : this.editingNotification.sentDate;
        await this.save({
          ...this.editingNotification,
          sentDate
        });
        this.onClose();
      }
      this.loading = false;
    } catch (err) {
      this.loading = false;
    }
  }

  onClose() {
    this.$emit("close");
  }

  get title() {
    return this.editingNotification.id
      ? this.$t("dialogSection.editScheduledNotification")
      : this.$t("dialogSection.newScheduledNotification");
  }
}
