
import { Component, Vue } from "vue-property-decorator";
import Button from "@/components/AppSettings/UI/Button.vue";
import { Mutation, namespace } from "vuex-class";
import { ConfiguratorState, FormData } from "@/interfaces/ConfigurationsType";
import { Navigation } from "@/interfaces/Store";

const configuration = namespace("configuration");
const MOCK_ICON = "https://via.placeholder.com/200x100";
@Component({
  components: {
    Button,
  },
})
export default class IconPage extends Vue {
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @Mutation setNavigation!: (payload: Navigation) => void;
  get iconStyle() {
    const image = this.form.icon ? this.form.icon.url : MOCK_ICON;
    return {
      backgroundImage: "url(" + image + ")",
    };
  }

  getAppName(): string {
    const lenghtLimit = this.form.name.indexOf(" ") >= 0 ? 12 : 10;

    if (this.form.name.length > lenghtLimit) {
      return this.form.name.substr(0, lenghtLimit - 1) + "...";
    }
    return this.form.name;
  }
}
