import AbstractApiService from "@/services/AbstractApiService";
import axios from "@/services/axios";
import Axios, { AxiosResponse, CancelTokenSource } from "axios";
import { PaymentModule } from "@/interfaces/Module";
import Manufacturer from "@/interfaces/Manufacturer";
import Product from "@/interfaces/Product";
import Category from "@/interfaces/Category";
import {
  SelectItem,
  SelectItemPack,
  SelectItemsTypes,
} from "@/interfaces/Select";
import { SearchableEntity } from "@/interfaces/SearchableEntity";
import store from "@/store";
import {CmsPageState} from "@/interfaces/Simulator";

export interface CacheClearInterface {
  clearCache(): Promise<boolean>;
}

class AppDataService implements CacheClearInterface {
  private cache: { [key: string]: any[] } = {};
  private tokens: { [key: string]: CancelTokenSource } = {};

  async getPaymentModules(): Promise<PaymentModule[]> {
    if (!this.cache["modules"]) {
      const url = AbstractApiService.getUrlForAction("modules");
      const response: AxiosResponse<PaymentModule[]> = await axios.get(url);
      this.cache["modules"] = response.data;
    }
    return this.cache["modules"];
  }

  async getProducts(): Promise<Product[]> {
    if (!this.cache["products"]) {
      try {
        const url = AbstractApiService.getFrontendUrl("products", { home: 1 });
        const response: AxiosResponse<{
          facets: any;
          sort_orders: any;
          count: number;
          result: Product[];
        }> = await axios.get(url);
        this.cache["products"] = response.data.result;
      } catch (error) {
        this.cache["products"] = [];
      }
    }
    return this.cache["products"];
  }

  async getProductsFromWebPath(page, webpath): Promise<any> {
    if (!webpath) return [];

    if (!store.getters['isShopify']) {
      const url = AbstractApiService.getFrontendUrl(webpath, { page });
      const response: AxiosResponse<{
        facets: any;
        sort_orders: any;
        count: number;
        result: Product[];
      }> = await axios.get(url);

      return response.data;
    }

    return [];
  }

  async getAllProducts(page: number, id_category: number): Promise<any> {
    const url = AbstractApiService.getFrontendUrl("products", {
      id_category,
      page,
    });
    const response: AxiosResponse<{
      facets: any;
      sort_orders: any;
      count: number;
      result: Product[];
    }> = await axios.get(url);
    return response.data;
  }

  async getSingleProduct(id: number): Promise<Product> {
    const url = AbstractApiService.getFrontendUrl("products", { id });
    const response: any = await axios.get(url);
    return response.data;
  }

  async getCMSPage(id: number): Promise<CmsPageState> {
    const url = AbstractApiService.getUrlForAction("cms", id);
    const response: any = await axios.get(url);
    return response.data;
  }

  async getManufacturers(page): Promise<any> {
    if (!this.isShopify()) {
      const url = AbstractApiService.getFrontendUrl("manufacturers", { page });
      const response: AxiosResponse<{
        count: number;
        result: Manufacturer[];
      }> = await axios.get(url);
      return response.data;
    }
    return {};
  }

  async getProductsFromManufacturer(
    page: number,
    manufacturer_id: any
  ): Promise<any> {
    if (!this.isShopify()) {
      const url = AbstractApiService.getFrontendUrl("manufacturer_products", {
        manufacturer_id,
        page,
      });
      const response: AxiosResponse<{
        count: number;
        result: Product[];
      }> = await axios.get(url);
      return response.data;
    }
    return [];
  }

  async getCategories(): Promise<Category[]> {
    if (!this.cache["categories"]) {
      const url = AbstractApiService.getFrontendUrl("categories");
      const response: AxiosResponse<Category[]> = await axios.get(url);
      this.cache["categories"] = response.data;
    }

    return this.cache["categories"];
  }

  async getCategoryProduct(id_category: number, page = 0): Promise<any> {
    if (!this.isShopify()) {
      const url = AbstractApiService.getFrontendUrl("categories", {
        id_category,
        page: page++,
      });
      const response: AxiosResponse<Product[]> = await axios.get(url);
      return response.data;
    }
    return [];
  }

  async getSelectItems(): Promise<SelectItemPack> {
    const url = AbstractApiService.getUrlForAction("typologies");
    const response: AxiosResponse<{
      [key: string]: SelectItem[];
    }> = await axios.get(url);
    const vals = response.data;
    return {
      [SelectItemsTypes.homeSectionTypeItems]: vals.section_types,
      [SelectItemsTypes.bannerTypeItems]: vals.banner_types,
      [SelectItemsTypes.bannerSizeItems]: vals.banner_sizes,
      [SelectItemsTypes.layoutTypeItems]: vals.section_products_layouts,
      [SelectItemsTypes.orderTypeItems]: vals.section_products_sorting,
      [SelectItemsTypes.catalogLayoutTypeItems]: vals.catalog_layouts,
      [SelectItemsTypes.navigationTypeItems]: vals.app_navigation_types,
      [SelectItemsTypes.productLayoutTypeItems]: vals.product_row_layout,
      [SelectItemsTypes.appFontTypeItems]: vals.app_font_types,
      [SelectItemsTypes.productsShapeTypeItems]: vals.products_shape_types,
      [SelectItemsTypes.scheduledNotificationItems]:
        vals.scheduled_notification_types,
      [SelectItemsTypes.webhookNotificationItems]:
        vals.webhook_notification_types,
      [SelectItemsTypes.customerGroups]: vals.customer_groups,
      [SelectItemsTypes.carouselTypeItems]: vals.carousel_types,
      [SelectItemsTypes.cmsPageTypes]: vals.cms_page_types,
    };
  }

  async searchItems(
    itemType: string,
    searchTerm: string
  ): Promise<SearchableEntity[]> {
    if (this.tokens[itemType]) {
      this.tokens[itemType].cancel();
    }
    this.tokens[itemType] = Axios.CancelToken.source();
    const url = AbstractApiService.getUrlForAction("autocomplete", 0, {
      filter: itemType,
      q: searchTerm,
    });
    const response: AxiosResponse<SearchableEntity[]> = await axios.get(url, {
      cancelToken: this.tokens[itemType].token,
    });
    delete this.tokens[itemType];
    return response.data;
  }

  async clearCache(): Promise<boolean> {
    const url = AbstractApiService.getFrontendUrl('remove/cache');
    const response: AxiosResponse<{ success: boolean }> = await axios.post(
        url,
        {},
        { type: 'cache' },
    );
    return response.data.success;
  }

  async enableOrDisablePaymentModule(paymentModule: PaymentModule): Promise<PaymentModule[]> {
    const url = AbstractApiService.getUrlForAction("modules", paymentModule.id_payment_module);
    try {
      const { data } = await axios.post(url, { id: paymentModule.id_payment_module });
      return data;
    } catch (e) {
      return [];
    }
  }

  isShopify(): boolean {
    return store.getters['isShopify'];
  }
}

export default new AppDataService();
