import AbstractApiService from "./AbstractApiService";
import axios from "@/services/axios";
import { WebhookNotificationType } from "@/interfaces/AppNotification";
import { AxiosResponse } from "axios";

class WebhookNotificationService extends AbstractApiService {
  protected action = "webhook_notifications";

  async find(): Promise<WebhookNotificationType[]> {
    const url = this.getUrl();
    const response: AxiosResponse<WebhookNotificationType[]> = await axios.get(
      url
    );
    return response.data;
  }

  async save(
    webhooks: WebhookNotificationType[]
  ): Promise<WebhookNotificationType[]> {
    const url = this.getUrl();
    const saved: AxiosResponse<WebhookNotificationType[]> = await axios.post(
      url,
      { webhooks }
    );
    return saved.data;
  }
}

export default new WebhookNotificationService();
