
import { Component, Vue, Watch } from "vue-property-decorator";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";
import BulkActionButton from "@/components/AppSettings/UI/BulkActionButton.vue";
import {Mutation, namespace} from "vuex-class";
import { CmsState, CmsPage } from "@/interfaces/CmsPage";
import CmsPageModal from "@/components/AppSettings/UI/CmsPageModal.vue";
import DeleteDialogModal from "@/components/AppSettings/UI/DeleteDialogModal.vue";
import { ToastTypes } from "@/interfaces/Toast";
import PaginationController from "@/components/AppSettings/UI/PaginationController.vue";
import { Filter } from "@/interfaces/Api";
import {Navigation} from "@/interfaces/Store";

const cms = namespace("cms");
const simulator = namespace("simulator");

@Component({
  components: {
    CardMaterial,
    BulkActionButton,
    CmsPageModal,
    DeleteDialogModal,
    PaginationController,
  },
})
export default class CmsPageTab extends Vue {
  @cms.Action("count") count!: () => Promise<void>;
  @cms.Action("getCmsPages") getCmsPages!: (filter: Filter) => Promise<void>;
  @cms.Action("deleteCmsPage") deleteCmsPage!: (
    cmsPage: CmsPage
  ) => Promise<boolean>;
  @cms.Getter("allSelected") allSelected!: boolean;
  @cms.Mutation("setAllSelected") setAllSelected!: (reset?: boolean) => void;
  @cms.State((state: CmsState) => state.cmsPages) pages!: CmsPage[];
  @cms.State((state: CmsState) => state.selected) selected!: CmsPage[];
  @cms.Mutation("addOrRemoveSelection") addOrRemoveSelection!: (config: {
    item: CmsPage;
    selected?: boolean;
  }) => void;

  @cms.Action("addCmsPage") addCmsPage!: (cmsPage: {idCms: number; simpleCms: boolean}) => Promise<void>;
  @Mutation setNavigation!: (payload: Navigation) => void;

  @simulator.Action("setCMSPage") setCMSPage!: (idCms: number) => Promise<void>

  showModal = false;
  pageToDelete: CmsPage | null = null;

  async mounted() {
    await this.count();

    if (this.pages.length > 0) {
      const cmsPage = this.pages[0];
      await this.setCMSPage(cmsPage.id_cms);
      this.setNavigation({name: "cms", context: cmsPage});
    }
  }

  @Watch('pages')
  async listenPages(newPages: CmsPage[], old: CmsPage[]) {
    if (newPages.length > 0 && old.length == 0) {
      const cmsPage = newPages[0];
      await this.changePage(cmsPage);
    }
  }

  async changePage(cmsPage: CmsPage) {
    await this.setCMSPage(cmsPage.id_cms);
    this.setNavigation({name: "cms", context: cmsPage});
  }

  openModal() {
    this.showModal = true;
  }

  async deletePages(){
      for (const page of this.selected) {
         await this.deleteCmsPage(page);
      }
      this.setAllSelected(true);
  }

  async deletePage(page: CmsPage) {
      try {
        await this.deleteCmsPage(page);
      } catch (err) {
        this.$store.commit("toasts/pushToast", {
          type: ToastTypes.error,
          message: "notifications.error",
        });
      }

  }

  get tableCols() {
    return ["id", "title", "simpleCmsAction", "deleteAction"];
  }

  itemTitle(item: CmsPage): string {
    return item.title + " - " + item.description;
  }

  isItemSelected(item: CmsPage) {
    return this.selected.map((p) => p.id).includes(item.id);
  }

  mobileGridItemStyle(item: CmsPage): any {
    return this.isItemSelected(item) ? "#eeeeee" : undefined;
  }

  icon(isTrue: boolean) {
    return isTrue ? "checkbox-marked-circle" : "close-circle";
  }

  buttonColor(isTrue: boolean) {
    return isTrue ? 'error' : 'green';
  }

  async toggleSimpleCmsPage(cmsPage: CmsPage) {
    await this.addCmsPage({idCms: cmsPage.id_cms, simpleCms: !cmsPage.simple_cms});
    this.changePage(cmsPage);
  }
}
