import { MutationPayload, Store } from "vuex";

const stepPages: { [key: number]: string } = {
  1: "icon",
  2: "home",
  3: "home",
  4: "home",
  5: "home",
  6: "home",
  7: "home",
  8: "home",
  9: 'home'
};

const VuexAppSync = (store: Store<any>) => {
  store.subscribe((mutation: MutationPayload) => {
    const isShopify = store.getters['isShopify'];
    switch (mutation.type) {
      case "configuration/init":
        if (mutation.payload.isStepper) {
          navigateToStepPage(mutation.payload.currentStep as number, isShopify);
        }
        break;
      case "configuration/setCurrentStep":
        navigateToStepPage(mutation.payload as number, isShopify);
        break;
      case "setCurrentTab":
        navigateToStepPage(1 + +mutation.payload, isShopify);
        break;
    }
  });

  function navigateToStepPage(step: number, isShopify: boolean) {
    const pageName = (step === 4 && !isShopify ? "payment_modules" : stepPages[step]);
    store.commit("setNavigation", { name: pageName });
  }
};

export default VuexAppSync;
