import { TranslatedContent } from "./Language";
import { SearchableEntity } from "./SearchableEntity";

export enum WebhookNotificationType {
  AppUninstalled = "APP_UNINSTALLED",
  AppSubscriptionUpdate = "APP_SUBSCRIPTIONS_UPDATE",
  OrdersCancelled = "ORDERS_CANCELLED",
  OrdersCreate = "ORDERS_CREATE",
  OrdersDelete = "ORDERS_DELETE",
  OrdersEdited = "ORDERS_EDITED",
  OrdersFulfilled = "ORDERS_FULFILLED",
  OrdersPaid = "ORDERS_PAID",
  OrdersUpdated = "ORDERS_UPDATED",
  ProductPublished = "PRODUCT_PUBLICATIONS_CREATE",
}

export enum ScheduleNotificationType {
  Generic = "GENERIC",
  ProductDetail = "PRODUCT_DETAIL",
  Collection = "COLLECTION",
  BestSeller = "BEST_SELLER",
  NewProduct = "NEW_PRODUCT",
  Catalog = "CATEGORY_BRAND",
  SpecialOffers = "SPECIAL_OFFERS",
  Cms = "CMS",
}

export interface AppScheduledNotification {
  id?: number;
  sentDate: string | number;
  title: TranslatedContent;
  notificationType: ScheduleNotificationType;
  product?: SearchableEntity;
  collection?: SearchableEntity;
  category?: SearchableEntity;
  manufacturer?: SearchableEntity;
  cms?: SearchableEntity;
  active: boolean;
  sent?: boolean;
  group?: number;
}

export interface AppNotificationState {
  notifications: AppScheduledNotification[];
  webhookNotifications: WebhookNotificationType[];
  selected: AppScheduledNotification[];
  editingNotification: Partial<AppScheduledNotification>;
}

export type AppNotificationHas = {
  select: boolean;
  disable: boolean;
  enable: boolean;
};

export enum AppNotificationBulkActions {
  Enable = "enable",
  Disable = "disable",
  Delete = "delete",
}
