
import { mixins } from "vue-class-component";
import ConfigurationStep from "@/mixins/ConfigurationStep";
import { Component, Watch } from "vue-property-decorator";
import { namespace, Getter } from "vuex-class";
import ImageUploader from "@/components/AppSettings/UI/ImageUploader.vue";
import DialogModal from "@/components/AppSettings/UI/DialogModal.vue";
import HomeSection from "@/interfaces/HomeSection";
import { DialogModalConfig } from "@/interfaces/DialogModal";
import VueDraggable from "vuedraggable";
import { HomeSectionState } from "@/interfaces/HomeSection";
import HomeSectionsDisplayer from "@/mixins/HomeSectionsDisplayer";
import HomeSectionModal from "@/components/AppSettings/UI/HomeSectionModal.vue";
import ValidableForm from "@/mixins/ValidableForm";
import moment from "moment";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";
import BulkActionButton from "@/components/AppSettings/UI/BulkActionButton.vue";
import { BulkActions } from "@/interfaces/BulkAction";
import PaginationController from "@/components/AppSettings/UI/PaginationController.vue";
import { Count, Filter } from "@/interfaces/Api";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";
import { SelectItem } from "@/interfaces/Select";
import { Data } from "@/store/modules/data";

const sections = namespace("sections");
const data = namespace("data");
const configuration = namespace("configuration");
@Component({
  components: {
    ImageUploader,
    HomeSectionModal,
    DialogModal,
    VueDraggable,
    CardMaterial,
    BulkActionButton,
    PaginationController,
  },
})
export default class HomeTab extends mixins(
  ConfigurationStep,
  HomeSectionsDisplayer,
  ValidableForm
) {
  @sections.Action("count") count!: () => Promise<void>;
  @sections.Action("find") find!: (filter: Filter) => Promise<void>;
  @sections.State((state: HomeSectionState) => state.total) total!: Count;
  @sections.State((e: HomeSectionState) => e.sections)
  sections!: HomeSection[];
  @sections.Mutation("openEditor") openEditor!: (payload: {
    editingSection?: HomeSection;
  }) => void;
  @sections.Mutation("setSelected") setSelected!: ({
    section,
    selected,
  }: {
    section: HomeSection;
    selected?: boolean;
  }) => void;
  @sections.Mutation("setAllSelected") setAllSelected!: (
    selected: boolean
  ) => void;
  @sections.Action("deleteBulk") deleteBulk!: () => Promise<void>;
  @sections.Action("changeActiveBulk") changeActiveBulk!: (
    active: boolean
  ) => Promise<void>;
  @sections.Action("orderSections") orderSections!: (
    idsOrder: number[]
  ) => Promise<void>;
  @sections.Action("deleteSection") deleteHomeSection!: (selectedItem: HomeSection) => Promise<void>;
  @sections.Getter("isSectionSelected") isSectionSelected!: (
    section: HomeSection
  ) => boolean;
  @sections.Getter("areAllSelected") areAllSelected!: boolean;
  @sections.Getter("hasSectionsSelected") hasSectionsSelected!: boolean;
  @sections.Getter("selectedSections") selectedSections!: HomeSection[];
  @data.Getter("getSectionTypeStringFor") getSectionTypeStringFor!: (
    e: HomeSection
  ) => string;
  @configuration.Mutation setAllowedStep!: (payload: number) => void;
  @configuration.State((e: ConfiguratorState) => e.currentStep)
  currentStep!: number;
  @configuration.State((e: ConfiguratorState) => e.allowedStep)
  allowedStep!: number;
  @data.State((state: Data) => state.selectItems.customerGroups)
  customerGroups!: SelectItem[];

  @Getter("formLanguageIsocode") formLanguageIsocode!: string;

  showDeleteSectionDialog = false;

  selected: HomeSection[] = [];
  selectedItem: HomeSection | null = null;
  shake = false;
  timeout: number | null = null;
  resorted: HomeSection[] = [];
  closeOnClick = true;
  modalOpened = false;
  private validationIndex: number | null = null;

  closeModal() {
    this.modalOpened = false;
  }

  openModal(object?: any) {
    if (object == null) {
      this.openEditor({
        editingSection: this.defaultSection,
      });
    } else {
      this.openEditor(object);
    }
    this.modalOpened = !this.modalOpened;
  }

  @Watch("sortedSections")
  onPropertyChanged() {
    this.checkSortedSelections();
  }

  async mounted() {
    await this.count();
    this.checkSortedSelections();
  }

  checkSortedSelections() {
    this.validationIndex = this.pushValidable(this.checkHomes);
    if (
      this.sections.length === 0 &&
      this.allowedStep < 4 &&
      this.currentStep < 4
    ) {
      this.setAllowedStep(3);
    }
  }

  destroyed() {
    if (this.validationIndex) {
      this.removeValidation(this.validationIndex);
    }
  }

  showDeleteDialog(item: HomeSection) {
    this.selectedItem = item;
    this.toggleDeleteDialog();
  }

  private async checkHomes(): Promise<boolean> {
    const valid = this.sections.length > 0;

    if (!valid) {
      this.shake = true;
      if (this.timeout !== null) {
        clearTimeout(this.timeout as number);
      }

      this.timeout = setTimeout(() => {
        this.shake = false;
      }, 1500);
    }

    return valid;
  }

  getGroups(item: HomeSection) {
    return this.customerGroups
      .filter((d: SelectItem) => item.groupsToShow?.includes(d.value))
      .map((elem: SelectItem) => {
        return elem.text;
      })
      .join(", ");
  }

  get deleteDialogConfig(): DialogModalConfig {
    let message = this.$t("deleteDialogMessage") as string;
    if (this.selectedItem && this.selectedItem.title) {
      message = message.replace(
        /<sectionName>/,
        this.selectedItem.title[this.formLanguageIsocode] as string
      );
    }

    const retval = {
      title: this.$t("deleteDialogTitle") as string,
      message,
      buttons: [
        {
          title: this.$t("confirmDeleteDialogButton") as string,
          color: "primary",
          onPress: () => this.deleteSection(),
        },
        {
          title: this.$t("cancelDeleteDialogButton") as string,
          color: "blue darken-1",
          isText: true,
          onPress: () => this.toggleDeleteDialog(),
        },
      ],
    };

    return retval;
  }

  async deleteSection() {
    if (!this.selectedItem) {
      this.toggleDeleteDialog();
      return;
    }
    await this.deleteHomeSection(this.selectedItem);
    this.toggleDeleteDialog();
    this.selected = this.selected.filter((s) => s.id !== this.selectedItem!.id);
    this.selectedItem = null;
  }

  toggleDeleteDialog() {
    this.showDeleteSectionDialog = !this.showDeleteSectionDialog;
  }

  mobileGridItemStyle(item: HomeSection) {
    return this.isSectionSelected(item) ? "#eeeeee" : undefined;
  }

  getActivePeriod(item: HomeSection): string {
    if (!item.activeFrom && !item.activeTo) {
      return this.$t("homesection.activation.manual") as string;
    }

    const parts: any = [];

    if (item.activeFrom) {
      const d = moment(item.activeFrom, "YYYY-MM-DD HH:mm:ss").format(
        "DD/MM/YYYY HH:mm"
      );
      parts.push(this.$t("homesection.activation.from", { date: d }));
    }

    if (item.activeTo) {
      const c = moment(item.activeTo, "YYYY-MM-DD HH:mm:ss").format(
        "DD/MM/YYYY HH:mm"
      );
      parts.push(this.$t("homesection.activation.to", { date: c }));
    }

    const t = parts.join(" ");
    return t.charAt(0).toUpperCase() + t.slice(1).toLowerCase();
  }

  get sortedSections(): HomeSection[] {
    return this.resorted.length > 0 ? this.resorted : this.sections;
  }

  set sortedSections(sorted: HomeSection[]) {
    this.resorted = [...sorted.filter((e) => !!e)];
    const sortedId = sorted.map((e) => e.id!);
    this.orderSections(sortedId).finally(() => (this.resorted = []));
  }

  get mobileTableCols() {
    return this.tableCols.filter(
      (e: string) => false == ["selected", "id"].includes(e)
    );
  }

  async execBulkAction(action: BulkActions) {
    switch (action) {
      case BulkActions.Enable:
        await this.changeActiveBulk(true);
        break;
      case BulkActions.Disable:
        await this.changeActiveBulk(false);
        break;
      case BulkActions.Delete:
        await this.deleteBulk();
        break;
    }
  }
}
