import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import HomeSection from "@/interfaces/HomeSection";

const sections = namespace("sections");

@Component
export default class HomeSectionsAware extends Vue {
  @sections.Getter("currentSections") sections!: HomeSection[];

  get activeSections(): HomeSection[] {
    return this.sections.filter((e) => e.active);
  }
}
