
import { Component, Prop, Vue } from "vue-property-decorator";
import { GroupTextItem } from "@/interfaces/GroupText";

@Component({})
export default class GroupText extends Vue {
  @Prop({ default: [] }) items!: GroupTextItem[];
  @Prop({ default: "right" }) alignment!: string;
  itemType(item: GroupTextItem) {
    return item.type ? item.type : "text";
  }
}
