import { MutationTree, ActionTree, GetterTree, Module } from "vuex";
import { Toast, ToastTypes } from "@/interfaces/Toast";

export type ToastsState = {
  toasts: Toast[];
};

const state: ToastsState = {
  toasts: [],
};

const mutations: MutationTree<ToastsState> = {
  pushToast(
    state: ToastsState,
    { message, type }: { message: string; type: ToastTypes }
  ) {
    const id: number = Date.now() * 1000 + Math.round(Math.random() * 1000);
    state.toasts = [...state.toasts, { id, message, type }];
  },
  deleteToast(state: ToastsState, toast: Toast) {
    state.toasts = state.toasts.filter((e: Toast) => e.id !== toast.id);
  },
};

const actions: ActionTree<ToastsState, any> = {};
const getters: GetterTree<ToastsState, any> = {};

const module: Module<ToastsState, any> = {
  state,
  mutations,
  actions,
  getters,
  namespaced: true,
};

export default module;
