
import { Component, Prop } from "vue-property-decorator";
import { Getter, Mutation, namespace } from "vuex-class";
import { Language, TranslatedContent } from "@/interfaces/Language";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";
import { mixins } from "vue-class-component";
import ValidableField from "@/mixins/ValidableField";

const configuration = namespace("configuration");

@Component
export default class MultilangField extends mixins(ValidableField) {
  @Getter("formLanguageIsocode") formLanguageIsocode!: string;
  @Mutation("setLanguageIsocode") setLanguageIsocode!: (
    isoCode: string
  ) => void;

  @configuration.State((e: ConfiguratorState) => e.languages)
  languages!: Language[];

  @Prop() declare value: TranslatedContent;
  @Prop() type!: string;
  @Prop() label!: string;
  @Prop() message!: string;
  @Prop({ type: Boolean, default: false }) required!: boolean;
  @Prop({ type: Number, default: 0 }) minLength!: number;
  @Prop({ type: Number, default: 0 }) maxLength!: number;
  @Prop() hint!: string;
  @Prop() placeholder!: string;

  get val(): string {
    if (this.value && this.value[this.formLanguageIsocode]) {
      return this.value[this.formLanguageIsocode];
    }

    return "";
  }

  set val(value: string) {
    let emitting: TranslatedContent = this.value == null ? {} : this.value;
    emitting = { ...emitting, [this.formLanguageIsocode]: value };
    this.$emit("input", emitting);
    this.validate(emitting);
  }

  get rules(): string {
    let rules: string[] = [];
    if (this.required) {
      rules = [
        ...rules,
        "langRequired:" + this.languages.map((e: Language) => e.value).join(),
      ];
    }

    if (this.minLength > 0 || this.maxLength > 0) {
      rules = [
        ...rules,
        `multilangLen:${Math.max(0, this.minLength)},${Math.max(
          0,
          this.maxLength
        )}`,
      ];
    }
    return rules.join("|");
  }

  get component() {
    switch (this.type) {
      case "textarea":
        return "v-textarea";
      default:
        return "v-text-field";
    }
  }

  get counter(): boolean | number {
    return this.maxLength > 0 ? this.maxLength : false;
  }
}
