import { AppStatus } from "@/interfaces/App";
import { AppSubscription, SubscriptionStatus } from "@/interfaces/AppSettings";
import {
  ConfiguratorState,
  RequireUpdateField,
} from "@/interfaces/ConfigurationsType";
import { CustomerState } from "@/store/modules/customer";
import { Component, Watch } from "vue-property-decorator";
import { namespace } from "vuex-class";
import moment from "moment";
import { FormData } from "@/interfaces/ConfigurationsType";
import { mixins } from "vue-class-component";
import ShopifyManager from "./ShopifyManager";

const customer = namespace("customer");
const configuration = namespace("configuration");

@Component
export default class AppStatusManager extends mixins(ShopifyManager) {
  @customer.Action("checkStatus") checkStatus!: () => Promise<void>;
  @customer.State((state: CustomerState) => state.haveSubscription) haveSubscription!: boolean;
  @customer.State((state: CustomerState) => state.appStatus) appStatus!: AppStatus | null;
  @customer.State((state: CustomerState) => state.status) status!: SubscriptionStatus | null;
  @customer.State((state: CustomerState) => state.subscription) subscription!: AppSubscription;
  @customer.State((state: CustomerState) => state.supportEndDate) supportEndDate!: string;
  @customer.State((state: CustomerState) => state.appStoreUrl) appStoreUrl!: string | null;
  @customer.State((state: CustomerState) => state.appDetailUrl) appDetailUrl!: string | null;
  @customer.State((state: CustomerState) => state.googlePlayStoreUrl) googlePlayStoreUrl!: string | null;
  @customer.State((state: CustomerState) => state.fromAddons) fromAddons!: boolean;
  @customer.State((state: CustomerState) => state.cancelledAt) cancelledAt!: string | null;
  @configuration.State((state: ConfiguratorState) => state.requireUpdateField) updateField!: RequireUpdateField | null;
  @configuration.State((state: ConfiguratorState) => state.form) form!: FormData;
  @configuration.State((state: ConfiguratorState) => state.updateRequired) updateRequired!: boolean;

  // Object of FormData values
  // For only require update fields
  snapshotRequireUpdateField: any;
  needUpdate = false;
  saveSent = false;
  loading = false;

  created() {
    this.popSnapshotFields();
  }

  get canSendUpdate(): boolean {
    return (
      this.appStatus != null &&
      [AppStatus.Completed, AppStatus.Pending].includes(this.appStatus) &&
      this.haveSubscription &&
      this.needUpdate
    );
  }

  @Watch("form")
  onFormChanged(newV: FormData) {
    if (!this.saveSent) {
      // Check if field is changed
      // And update needUpdate to true
      for (const field of Object.keys(this.snapshotRequireUpdateField)) {
        if (
          this.snapshotRequireUpdateField[field] !==
          newV[field as keyof FormData]
        ) {
          this.needUpdate = true;
          return;
        }
      }
    } else {
      this.needUpdate = false;
      this.popSnapshotFields();
    }
  }

  popSnapshotFields() {
    this.snapshotRequireUpdateField = {};
    this.needUpdate = false;
    if (this.updateField) {
      for (const field of Object.keys(this.updateField)) {
        if (this.updateField[field as keyof RequireUpdateField] === true) {
          this.snapshotRequireUpdateField = {
            ...this.snapshotRequireUpdateField,
            [field]: this.form[field as keyof FormData],
          };
        }
      }
    }

    this.saveSent = false;
  }

  async loadStatus() {
    try {
      this.loading = true;
      await this.checkStatus();
    } finally {
      this.loading = false;
    }
  }

  get statusLabel() {
    return this.status
      ? `subscriptionStatus.${this.status}`
      : `subscriptionStatus.unknown`;
  }

  get statusColor(): string {
    switch (this.status) {
      case SubscriptionStatus.ACTIVE:
        return "success";
      case SubscriptionStatus.TRIAL:
        return "amber";
      case SubscriptionStatus.PAST:
        return "deep-orange";
      case SubscriptionStatus.DELETED:
        return "red";
      case SubscriptionStatus.PAUSED:
        return "cyan";
      case SubscriptionStatus.REFUND:
        return "tial";
      case SubscriptionStatus.CANCELLED:
        return 'red';
    }
    return "grey";
  }

  get appStatusLabel() {
    return this.appStatus
      ? `appStatus.${this.appStatus.toLowerCase()}`
      : `appStatus.unknown`;
  }

  get appStatusColor(): string {
    switch (this.appStatus) {
      case AppStatus.Completed:
        return "success";
      case AppStatus.Pending:
        return "deep-orange";
      case AppStatus.Request:
        return "cyan";
      case AppStatus.RequestUpdate:
        return "deep-purple";
      case AppStatus.Waiting:
        return "amber";
      default:
        return "grey";
    }
  }

  get formattedSupportEndDate() {
    return this.supportEndDate
      ? moment(this.supportEndDate).format("YYYY-MM-DD")
      : "/";
  }
}
