
import { Component, Ref } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import SettingsStepSwitcher from "@/mixins/SettingsStepSwitcher";
import { State, Mutation, namespace, Action } from "vuex-class";
import { AppState } from "@/interfaces/Store";
import { AppSteppersState } from "@/store/modules/stepper";
import { StepDocTranslation } from "@/interfaces/Language";
import ValidableForm from "@/mixins/ValidableForm";
import AppStatusManager from "@/mixins/AppStatusManager";
import { ToastTypes } from "@/interfaces/Toast";
import Message from "@/components/AppSettings/UI/Message.vue";
import StatusMessage from "@/components/AppSettings/UI/StatusMessage.vue";
import { CustomerState } from "@/store/modules/customer";
import { SubscriptionModule } from "@/interfaces/Module";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";

import { SubscriptionStatus } from "@/interfaces/AppSettings";
import PrestashopBilling from "@/components/AppSettings/UI/PrestashopBilling.vue";
import ShopifyManager from '@/mixins/ShopifyManager';

const appSteppers = namespace("appSteppers");
const configuration = namespace("configuration");
const customer = namespace("customer");

@Component({
  components: {
    Message,
    StatusMessage,
    CardMaterial,
    PrestashopBilling,
  },
})
export default class AppSettings extends mixins(
  SettingsStepSwitcher,
  AppStatusManager,
  ShopifyManager
) {
  @State((state: AppState) => state.currentTab) currentTab!: number;
  @Action("setCurrentTab") setCurrentTab!: (tabIndex: number) => void;
  @Mutation setStepperMode!: (val: boolean) => void;
  @appSteppers.Mutation setCurrentStep!: (step: number) => void;
  @appSteppers.Mutation setLogged!: (isLogged: boolean) => void;
  @configuration.Action("sendAppRequestEdit")
  sendAppRequestEdit!: () => Promise<{ requestAppUrl?: string | null }>;
  @appSteppers.Getter isLogged!: boolean;
  @appSteppers.Getter isSubscribed!: boolean;
  @appSteppers.State((state: AppSteppersState) => state.data.componentDocs)
  componentDocs!: StepDocTranslation | null;
  @Ref("tabs") tabs!: ValidableForm[];
  @customer.State((e: CustomerState) => e)
  customerStatus!: CustomerState | null;
  showNotLoggedDialog = false;

  validMaps: boolean[] = [];

  showDocsDialog = false;
  drawer = false;

  get enablePsBilling() {
    return (
      !this.isShopify &&
      (process.env.VUE_APP_MOCK_PS_BILL == '1' ||
          window.psBillingContext != null)
    );
  }

  get panel() {
    const status = this.customerStatus?.status !== SubscriptionStatus.ACTIVE;

    return {
      expand: status ? 0 : null,
      showIcon: status ? false : true,
    };
  }

  updateCurrentTab(tabIndex: number) {
    if (this.drawer === true) {
      this.drawer = false;
    }

    this.setCurrentTab(tabIndex);
  }

  get currentStepDocsTitle(): string | null {
    return this.componentDocs ? this.componentDocs.title : null;
  }

  get currentStepDocsMessage(): string | null {
    return this.componentDocs ? this.componentDocs.message : null;
  }

  get mainActionButtonText(): string {
    return "";
  }

  get modulesLenght() {
    return this.customerStatus
      ? this.customerStatus.modules
        ? this.customerStatus.modules.filter(
            (d: SubscriptionModule) => d.needUpdate && !d.needInstall
          ).length
        : 0
      : 0;
  }

  onCloseNotLogged() {
    this.showNotLoggedDialog = false;
  }

  isStepValid(index: number): boolean {
    if (this.appSteps[index].skipValidation) {
      return true;
    }

    /*
	          se l'index supera la lunghezza, molto probabilmente non  ancora stata
	          triggerata la validazione, quindi lo considero valido
	        */
    if (index >= this.validMaps.length) {
      return true;
    }

    return !!this.validMaps[index];
  }

  async save(): Promise<boolean> {
    this.validMaps = await Promise.all(
      this.tabs.map((e: ValidableForm) => e && e.validate && e.validate())
    );

    if (this.validMaps.some((e: boolean) => e === false)) {
      this.$store.commit("toasts/pushToast", {
        type: ToastTypes.error,
        message: "notifications.tabs.validationError",
      });
      this.scrollToError()
      return false;
    } else {
      this.saveSent = true;
      this.$store.dispatch(
        "configuration/saveConfiguration",
        this.canSendUpdate
      );
      return true;
    }
  }

    scrollToError() {
    const error = this.$el.querySelector(
      ".warning--text:first-of-type, div.error--text:first-of-type"
    ) as any;
    if (error) {
      error!.scrollIntoView({ behavior: "smooth", block: "center" });
    }
  }

  async sendUpdate(): Promise<void> {
    const isSaved = await this.save();
    if (isSaved) {
      await this.sendAppRequestEdit();
    }
  }
}
