import { Component } from "vue-property-decorator";
import { Step } from "@/interfaces/AppSteppers";

import StoreTab from "@/components/AppSettings/Tabs/StoreTab.vue";
import ColorsTab from "@/components/AppSettings/Tabs/ColorsTab.vue";
import LayoutTab from "@/components/AppSettings/Tabs/LayoutTab.vue";
import HomeTab from "@/components/AppSettings/Tabs/HomeTab.vue";
import SettingTab from "@/components/AppSettings/Tabs/SettingTab.vue";
import ModuleTab from "@/components/AppSettings/Tabs/ModulesTab.vue";
import NotificationTab from "@/components/AppSettings/Tabs/NotificationTab.vue";
import CmsPageTab from "@/components/AppSettings/Tabs/CmsPageTab.vue";

import Message from "@/components/AppSettings/UI/Message.vue";
import { mixins } from "vue-class-component";
import ShopifyManager from "./ShopifyManager";

@Component({
  components: {
    StoreTab,
    ColorsTab,
    LayoutTab,
    HomeTab,
    SettingTab,
    ModuleTab,
    NotificationTab,
    CmsPageTab,
    Message
  },
})
export default class SettingsStepSwitcher extends mixins(ShopifyManager) {
  stepperSteps: Step[] = [
    {
      name: "steps.colors",
      component: "ColorsTab",
    },
    {
      name: "steps.layout",
      component: "LayoutTab",
    },
    {
      name: "steps.home",
      component: "HomeTab",
    },
    {
      name: "steps.settings",
      component: "SettingTab",
    },
    {
      name: "steps.store",
      component: "StoreTab",
    },
  ];

  steps: Step[] = [
    {
      name: "steps.colors",
      component: "ColorsTab",
    },
    {
      name: "steps.layout",
      component: "LayoutTab",
    },
    {
      name: "steps.home",
      component: "HomeTab",
    },
    {
      name: "steps.settings",
      component: "SettingTab",
    },
    {
      name: "steps.store",
      component: "StoreTab",
    },
    {
      name: "steps.notifications",
      component: "NotificationTab",
      skipSave: true,
      skipValidation: true,
    },
    {
      name: "steps.cms",
      component: "CmsPageTab",
      skipSave: true,
      skipValidation: true,
      hideShopify: true,
    },
    {
      name: "steps.modules",
      component: "ModuleTab",
      skipSave: true,
      skipValidation: true,
      hideShopify: true,
    }
  ];

  get appSteps(): Step[] {
    if (this.isShopify) {
      return this.steps.filter((s) => s.hideShopify !== true);
    }
    return this.steps;
  }
}
