import {SubscriptionModule} from "@/interfaces/Module";
import ConfigurationService from "@/services/ConfigurationService";
import {ActionContext, ActionTree, GetterTree, Module, MutationTree,} from "vuex";
import {AppSubscription, SubscriptionStatus,} from "@/interfaces/AppSettings";
import {AppStatus} from "@/interfaces/App";

export interface CustomerState {
  appStatus: AppStatus | null;
  status: SubscriptionStatus | null;
  modules: SubscriptionModule[];
  haveSubscription: boolean;
  supportEndDate?: string | null;
  priorityMessage?: string | null;
  requestAppUrl?: string | null;
  subscription: AppSubscription | null;
  googlePlayStoreUrl: string | null;
  appStoreUrl: string | null;
  appDetailUrl: string | null;
  fromAddons: boolean;
  cancelledAt: string | null;
}

const state: CustomerState = {
  appStatus: null,
  status: null,
  modules: [],
  haveSubscription: false,
  supportEndDate: null,
  priorityMessage: null,
  requestAppUrl: null,
  subscription: null,
  googlePlayStoreUrl: null,
  appStoreUrl: null,
  appDetailUrl: null,
  fromAddons: false,
  cancelledAt: null
};

const mutations: MutationTree<CustomerState> = {
  setAppStatus(state: CustomerState, appStatus: AppStatus) {
    state.appStatus = appStatus;
  },
  setStatus(state: CustomerState, status: SubscriptionStatus) {
    state.status = status;
  },
  setSubscriptionModules(
    state: CustomerState,
    subscriptionModules: SubscriptionModule[]
  ) {
    state.modules = subscriptionModules;
  },
  setHaveSubscription(state: CustomerState, haveSubscription: boolean) {
    state.haveSubscription = haveSubscription;
  },
  setSupportEndMessage(state: CustomerState, priorityMessage: string) {
    Object.assign(state, {
      priorityMessage: priorityMessage,
    });
  },
  setRequestAppUrl(state: CustomerState, requestAppUrl: boolean) {
    Object.assign(state, {
      requestAppUrl: requestAppUrl,
    });
  },

  setSupportEndDate(state: CustomerState, supportEndDate: string) {
    state.supportEndDate = supportEndDate;
  },
  setSubscription(state: CustomerState, subscription: AppSubscription | null) {
    state.subscription = subscription;
  },
  setStoreUrl(state: CustomerState, { appStoreUrl, googlePlayStoreUrl }) {
    state.appStoreUrl = appStoreUrl;
    state.googlePlayStoreUrl = googlePlayStoreUrl;
  },
  setDetailUrl(state: CustomerState, appDetailUrl: string) {
    state.appDetailUrl = appDetailUrl;
  },
  setFromAddons(state: CustomerState, fromAddons: boolean) {
    state.fromAddons = fromAddons;
  },
  setCancelledAt(state: CustomerState, cancelledAt: string | null) {
    state.cancelledAt = cancelledAt;
  }
};

const actions: ActionTree<CustomerState, any> = {

  updateStatus(context: ActionContext<CustomerState, any>, status: SubscriptionStatus){
    context.commit("setHaveSubscription", status == SubscriptionStatus.ACTIVE);
    context.commit("setStatus", status);
  },

  async checkStatus(context: ActionContext<CustomerState, any>) {
    try {
      const response = await ConfigurationService.checkStatus();
      context.commit("setAppStatus", response.data.appStatus);
      context.commit("setStatus", response.data.status);
      context.commit("setSubscription", response.data.subscription);
      context.commit("setSubscriptionModules", response.data.modules);
      context.commit("setHaveSubscription", response.data.haveSubscription);
      context.commit("setSupportEndMessage", response.data.priorityMessage);
      context.commit("setRequestAppUrl", response.data.requestAppUrl);
      context.commit("setSupportEndDate", response.data.supportEndDate);
      context.commit("setStoreUrl", {
        appStoreUrl: response.data.appStoreUrl,
        googlePlayStoreUrl: response.data.googlePlayStoreUrl,
      });
      context.commit("setDetailUrl", response.data.appDetailUrl);
      context.commit('setFromAddons', response.data.isPrestashopAddonsApp);
      context.commit('setCancelledAt', response.data.cancelledAt);
    } catch (err) {
      await context.dispatch(
         "configuration/init",
         {
           'isStepper':false
         },
         {
           root: true,
         }
       );
    }
  },
};

const getters: GetterTree<CustomerState, any> = {};

const customer: Module<CustomerState, any> = {
  state,
  mutations,
  actions,
  getters,
  namespaced: true,
};

export default customer;
