import { namespace } from 'vuex-class';
import { CarouselItem } from "./../interfaces/HomeSection";
import { Component } from "vue-property-decorator";
import HomeSection, {
  BannerTypes,
  HomeSectionType,
} from "@/interfaces/HomeSection";
import { mixins } from "vue-class-component";
import ShopifyManager from "./ShopifyManager";
import { Data } from '@/store/modules/data';
import { SelectItem } from '@/interfaces/Select';
import HomeSectionExclusion from './HomeSectionExclusion';

const data = namespace('data');
@Component
export default class HomeSectionsTester extends mixins(HomeSectionExclusion, ShopifyManager) {
  @data.State((e: Data) => e.selectItems.orderTypeItems) orderTypeItems!: SelectItem[];

  isManufacturers(section: HomeSection): boolean {
    return section.type == HomeSectionType.MANUFACTURERS;
  }

  isProducts(section: HomeSection): boolean {
    return section.webPath != null ||
    (
      this.isShopify &&
      section.items != null &&
      section.items.length > 0 &&
      !this.isBanner(section)
    );
  }

  isBanner(section: HomeSection): boolean {
    return section.type == HomeSectionType.BANNER;
  }

  isCarousel(section: HomeSection): boolean {
    return section.type == HomeSectionType.CAROUSEL;
  }
  isCategories(section: HomeSection): boolean {
    return section.type == HomeSectionType.LVL_ONE_CATEGORIES;
  }

  isDetailBanner(section: HomeSection): boolean {
    return (
      this.isBanner(section) &&
      section.bannerType == BannerTypes.BANNER_PRODUCT_DETAIL
    );
  }

  isCategoryManufacturerBanner(section: HomeSection): boolean {
    return (
      this.isBanner(section) &&
      section.bannerType == BannerTypes.BANNER_CATEGORY_BRAND
    );
  }

  isCmsBanner(section: HomeSection): boolean {
    return (
      this.isBanner(section) && section.bannerType == BannerTypes.BANNER_CMS
    );
  }

  isCollection(section: HomeSection): boolean {
    return section.type === HomeSectionType.COLLECTION;
  }

  isBestSeller(section: HomeSection): boolean {
    return section.type === HomeSectionType.BEST_SELLER;
  }

  isBannerCollection(section: HomeSection): boolean {
    return (
      this.isBanner(section) &&
      section.bannerType === BannerTypes.BANNER_COLLECTION
    );
  }

  isBannerBestSeller(section: HomeSection): boolean {
    return (
      this.isBanner(section) &&
      section.bannerType === BannerTypes.BANNER_BEST_SELLER
    );
  }

  isCarouselDetail(banner: CarouselItem) {
    return banner.bannerType === BannerTypes.BANNER_PRODUCT_DETAIL;
  }

  isCarouselCategoryManufacturerBanner(banner: CarouselItem): boolean {
    return banner.bannerType == BannerTypes.BANNER_CATEGORY_BRAND;
  }

  isCarouselCmsBanner(banner: CarouselItem): boolean {
    return banner.bannerType == BannerTypes.BANNER_CMS;
  }

  isCarouselCollection(banner: CarouselItem) {
    return banner.bannerType === BannerTypes.BANNER_COLLECTION;
  }

  isCategoriesType(section: HomeSection) {
    return section.type === HomeSectionType.CATEGORIES;
  }

  isCategoryProductsType(section: HomeSection) {
    return section.type === HomeSectionType.CATEGORY_PRODUCTS;
  }

  isShopifyCollectionList(section: HomeSection) {
    return this.isShopify && section.type === HomeSectionType.CATEGORIES;
  }

  sectionOrderTypeItems(section: HomeSection): SelectItem[] {
    switch (section.type) {
      case HomeSectionType.NEW_PRODUCTS:
        return this.orderTypeItems.filter(ot => !this.newProductOrderTypes.includes(ot.value));
      default:
        return this.orderTypeItems;
    }
  }
}
