
import { Component, Prop, Vue } from "vue-property-decorator";
import StatusBar from "@/components/AppSimulator/UI/StatusBar.vue";
import NavBar from "@/components/AppSimulator/UI/NavBar.vue";
import { Getter, namespace, State } from "vuex-class";
import { FormData, ConfiguratorState } from "@/interfaces/ConfigurationsType";
import { Navigation, Page, RootState } from "@/interfaces/Store";

const configuration = namespace("configuration");

@Component({
  components: {
    StatusBar,
    NavBar,
  },
})
export default class AppHeader extends Vue {
  @Getter appDarkMode!: boolean;
  @State((state: RootState) => state.currentPage) currentPage!: Page;
  @Getter currentNavigation!: Navigation;
  @Prop({ default: false }) darkMode!: boolean;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  toggleSidebar() {
    this.$emit("sidebar-toggled");
  }

  get headerStyle() {
    if (!this.form.showNavBar && this.currentNavigation.name === "detail") {
      return {
        "background-color": "transparent !important",
        border: "none",
      };
    } else {
      return {
        "background-color": this.darkMode ? "#222" : this.form.mainColor,
      };
    }
  }

  getAppName(): string {
    const lenghtLimit = this.form.name.indexOf(" ") >= 0 ? 12 : 10;

    if (this.form.name.length > lenghtLimit) {
      return this.form.name.substr(0, lenghtLimit - 1) + "...";
    }
    return this.form.name;
  }
}
