import { Component, Prop } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import HomeSectionsDisplayer from "@/mixins/HomeSectionsDisplayer";

import HomeSection, { LayoutType } from "@/interfaces/HomeSection";

@Component
export default class HomeSectionsComponent extends mixins(
  HomeSectionsDisplayer
) {
  @Prop({ type: Object, default: () => ({}) }) item!: HomeSection;
  get title(): string | undefined {
    if (!this.item.title || this.item.hideTitleInHome) {
      return;
    }
    return this.getSectionTranslatedTitle(this.item);
  }

  get layout(): string {
    return this.getLayout(this.item.layout!);
  }

  getLayout(layout: LayoutType) {
    return layout == LayoutType.VERTICAL ? "vertical" : "horizontal";
  }

  get backgroundImage() {
    return this.previewImage(this.item);
  }
}
