import {
  AppNotificationBulkActions,
  AppNotificationHas,
  AppNotificationState,
  AppScheduledNotification,
  ScheduleNotificationType,
  WebhookNotificationType,
} from "@/interfaces/AppNotification";
import { ActionTree, GetterTree, Module, MutationTree } from "vuex";
import NotificationService from "@/services/NotificationService";
import WebhookNotificationService from "@/services/WebhookNotificationService";

const state: AppNotificationState = {
  notifications: [],
  webhookNotifications: [],
  editingNotification: {},
  selected: [],
};

const mutations: MutationTree<AppNotificationState> = {
  setAllSelected(state: AppNotificationState) {
    const reset = state.selected.length > 0;
    if (reset) {
      state.selected = [];
    } else {
      state.selected = state.notifications;
    }
  },
  addOrRemoveSelection(
    state: AppNotificationState,
    { item, selected }: { item: AppScheduledNotification; selected?: boolean }
  ) {
    const newSelected =
      selected === undefined
        ? false == state.selected.map((s) => s.id).includes(item.id!)
        : selected;

    if (newSelected) {
      if (false == state.selected.map((s) => s.id).includes(item.id!)) {
        state.selected = [...state.selected, item];
      }
    } else {
      state.selected = state.selected.filter((e) => e.id !== item.id);
    }
  },
  resetSelected(state: AppNotificationState) {
    state.selected = [];
  },
  setNotifications(
    state: AppNotificationState,
    notifications: AppScheduledNotification[]
  ) {
    state.notifications = notifications;
  },
  pushOrUpdateNotification(
    state: AppNotificationState,
    notification: AppScheduledNotification
  ) {
    const index = state.notifications.findIndex(
      (n) => n.id === notification.id
    );
    if (index > -1) {
      state.notifications[index] = notification;
    } else {
      state.notifications.push(notification);
    }
  },
  removeNotification(state: AppNotificationState, id: number) {
    state.notifications = state.notifications.filter(
      (e: AppScheduledNotification) => e.id !== id
    );
    state.selected = state.selected.filter(
      (e: AppScheduledNotification) => e.id !== id
    );
  },
  setEditing(state: AppNotificationState, edited?: AppScheduledNotification) {
    state.editingNotification =
      edited && edited.id
        ? { ...edited }
        : {
            sentDate: new Date().toISOString(),
            title: {},
            notificationType: ScheduleNotificationType.Generic,
            active: false,
            group: 0,
          };
  },
  updateEditingForm(
    state: AppNotificationState,
    fields: Partial<AppScheduledNotification>
  ) {
    state.editingNotification = {
      ...state.editingNotification,
      ...fields,
    };
  },
  setWebhookNotifications(
    state: AppNotificationState,
    webhooks: WebhookNotificationType[]
  ) {
    state.webhookNotifications = webhooks;
  },
};

const getters: GetterTree<AppNotificationState, any> = {
  has(state: AppNotificationState): AppNotificationHas {
    return {
      select: false === state.selected.length > 0,
      disable: false === state.selected.some((e) => e.active),
      enable: false === state.selected.some((e) => !e.active),
    };
  },
  allSelected(state: AppNotificationState): boolean {
    return state.selected.length === state.notifications.length;
  },
};

const actions: ActionTree<AppNotificationState, any> = {
  async count({ commit }) {
    const total = await NotificationService.count();
    commit("pagination/setCount", total, { root: true });
  },
  async find({ commit, rootState }) {
    const notifications: AppScheduledNotification[] =
      await NotificationService.find(rootState.pagination.filter);
    commit("setNotifications", notifications);
  },
  async save({ dispatch, commit }, payload: AppScheduledNotification) {
    const notification: AppScheduledNotification =
      await NotificationService.save(payload);
    commit('pushOrUpdateNotification', notification);
    dispatch("count");
  },
  async delete({ dispatch, commit }, id: number) {
    await NotificationService.delete(id);
    commit("removeNotification", id);
    dispatch("count");
  },
  async execBulkAction(
    { state, dispatch, commit },
    type: AppNotificationBulkActions
  ) {
    if (state.selected.length === 0) {
      return;
    }

    const notifications: AppScheduledNotification[] =
      await NotificationService.bulkAction(
        type,
        state.selected.map((s) => s.id) as number[]
      );
    commit("setNotifications", notifications);
    commit("resetSelected");
    dispatch("count");
  },
  async findEnabledWebhooks({ commit }) {
    const enabledWebhooks: WebhookNotificationType[] =
      await WebhookNotificationService.find();
    commit("setWebhookNotifications", enabledWebhooks);
  },
  async saveEnabledWebhooks({ state, commit }) {
    const webhooks = await WebhookNotificationService.save(
      state.webhookNotifications
    );
    commit("setWebhookNotifications", webhooks);
  },
};

const module: Module<AppNotificationState, any> = {
  state,
  mutations,
  getters,
  actions,
  namespaced: true,
};

export default module;
