
import { Component } from "vue-property-decorator";
import DisplayPages from "@/interfaces/DisplayPages";
import NavigationManager from "@/mixins/NavigationManager";
import { mixins } from "vue-class-component";

// utilizzo una piccola factory per templatizzare i selettori da utilizzare
function selectorGenerator(
  label: string,
  page: string,
  displayPage?: keyof DisplayPages
) {
  return { label, page, displayPage: displayPage || page };
}

@Component
export default class AppNavigator extends mixins(NavigationManager) {
  manageNavigation = (selector) => {
    this.setNavigation({ name: selector.page });
    this.$emit("sidebar-toggled", false);
  };

  selectors = [
    selectorGenerator("Icon", "icon"),
    selectorGenerator("Home", "home"),
    selectorGenerator("Catalog", "catalog"),
    selectorGenerator("List", "list"),
    selectorGenerator("Detail", "detail"),
  ];
}
