
import { Component, Prop, Vue } from "vue-property-decorator";
import { Toast, ToastTypes } from "@/interfaces/Toast";

@Component
export default class ToastNotification extends Vue {
  @Prop() toast!: Toast;
  show = true;

  get message(): string {
    let message = "";
    // check if translation exists since an error can occur before translations load
    if (this.$te(this.toast.message)) {
      message = this.$t(this.toast.message) as string;
    } else {
      switch (this.toast.type) {
        case ToastTypes.success:
          message = "Operation successfully completed";
          break;
        case ToastTypes.error:
          message = "An error occured";
          break;
      }
    }

    return message;
  }

  get color(): string {
    return this.toast.type == ToastTypes.success ? "#4CAF50" : "#fe5151";
  }

  get icon(): string {
    return this.toast.type == ToastTypes.error
      ? "mdi-alert-circle"
      : "mdi-check-circle";
  }

  removeToast() {
    this.show = false;
    this.$emit("closed", this.toast);
  }
}
