import { Language, StepDocTranslation } from "./Language";
import HomeSection from "./HomeSection";
import { UploadedImage } from "./Upload";
import {
  AppFontType,
  CatalogLayoutType,
} from "@/interfaces/ConfigurationsType";

export enum FileType {
  icon = "icon",
  splash = "splash",
  logo = "logo",
  banner = "banner",
  darkSplash = "darkSplash",
  darkLogo = "darkLogo",
}

export type ChangeFile = {
  type: FileType;
  file: File | null;
};

export type Step = {
  name: string;
  component: string;
  skipSave?: boolean;
  skipValidation?: boolean;
  hideShopify?: boolean;
};

export type FormData = {
  name: string;
  [FileType.icon]: UploadedImage | null;
  [FileType.splash]: UploadedImage | null;
  [FileType.logo]: UploadedImage | null;
  mainColor: string;
  textMainColor: string;
  secondaryColor: string;
  textSecondaryColor: string;
  isBlackText: boolean;
  navigationType: "sidebar" | "tab";
  catalogLayoutType: CatalogLayoutType;
  productsLayoutType: "one" | "two";
  productsShape: "RECTANGLE" | "SQUARE";
  appFont: AppFontType | null;
  showStockQt: boolean;
  showAvailable: boolean;
  showLogo: boolean;
  homeSections: HomeSection[];
  firebaseConfiguration: firebaseConfiguration;
  ageEnabled: boolean;
  minAge: number | null;
  showPaypal: boolean;
  accessTokenSandbox?: string;
  accessTokenLive?: string;
  showSocialLogin: boolean;
  facebookEnabled: boolean;
  googleEnabled: boolean;
};

export interface AppSteppersData {
  formLanguageIsoCode: string;
  languages: Array<Language>;
  appStatus: "online" | "offline";
  form: FormData;
  qrCode: string | null;
  componentDocs: StepDocTranslation | null;
  isLogged: boolean;
  isSubscribed: boolean;
}

export type firebaseConfiguration = {
  name: string | null;
  value: string | null;
  active: boolean;
};
