
import {Component} from "vue-property-decorator";
import {mixins} from "vue-class-component";
import PrestashopBillingManager from "@/mixins/PrestashopBillingManager";
import {namespace} from "vuex-class";
import {CustomerState} from "@/store/modules/customer";
import {SubscriptionStatus} from "@/interfaces/AppSettings";
import {contextMock} from "@/mixins/mock";
import {PrestashopBillingContext} from "@/interfaces/PrestashopBilling";

const customer = namespace("customer");
@Component({})
export default class PrestashopBilling extends mixins(
  PrestashopBillingManager
) {
  @customer.State((e: CustomerState) => e)
  customerStatus!: CustomerState | null;
  @customer.Action("checkStatus") checkStatus!: () => Promise<void>;
  @customer.Action("updateStatus") updateStatus!: (status: SubscriptionStatus) => void;



  get panel() {
    const status = this.customerStatus?.status !== SubscriptionStatus.ACTIVE;

    return {
      expand: 0,
      showIcon: !status,
    };
  }

  get subscriptionStatus(): string{
      let text = this.$t("static.subscriptionStatus") as string;

      if(this.customerStatus?.status != null
          && ![SubscriptionStatus.ACTIVE, SubscriptionStatus.TRIAL].includes(this.customerStatus?.status)) {
          return text.concat(': ', this.$t("static.subscriptionRequired") as string);
      }

      return text;
  }

  psBillingContext: PrestashopBillingContext | null = null;
  psModalType: string | null = null;

  mounted() {
    if (window.psBillingContext != null || process.env.VUE_APP_MOCK_PS_BILL) {
      this.psBillingContext =
        window.psBillingContext != null
          ? window.psBillingContext.context
          : { ...(contextMock.context as any) };

      window.psBilling.initialize(
        this.psBillingContext,
        "#ps-billing",
        "#ps-modal",
        (type, data) => {
          switch (type) {
              case window.psBilling.EVENT_HOOK_TYPE.SUBSCRIPTION_CREATED:
              case window.psBilling.EVENT_HOOK_TYPE.SUBSCRIPTION_UPDATED:
              case window.psBilling.EVENT_HOOK_TYPE.SUBSCRIPTION_REACTIVATED:
                  this.updateStatus(SubscriptionStatus.ACTIVE);
                  break;
              case window.psBilling.EVENT_HOOK_TYPE.SUBSCRIPTION_CANCELLED:
                  this.updateStatus(SubscriptionStatus.CANCELLED);
                  break;
          }
        }
      );
    }
  }
}
