
import { Component, Prop, Watch } from "vue-property-decorator";
import { namespace } from "vuex-class";
import AppStatusManager from "@/mixins/AppStatusManager";

import GroupText from "@/components/AppSettings/UI/GroupText.vue";
import { GroupTextItem } from "@/interfaces/GroupText";
import { mixins } from "vue-class-component";

const customer = namespace("customer");

@Component({
  components: {
    GroupText,
  },
})
export default class DialogAppStatus extends mixins(AppStatusManager) {
  @Prop({ default: false }) show!: boolean;
  @customer.Action("checkStatus") declare checkStatus: () => Promise<void>;

  onClickOutside() {
    return this.$emit("click:outside");
  }

  @Watch("show")
  onChangeShow(newValue: boolean) {
    if (newValue) {
      this.loadStatus();
    }
  }

  get items(): GroupTextItem[] {
    const items: GroupTextItem[] = [
      {
        text: this.$t("static.appStatus"),
        value: this.$t(this.appStatusLabel) as string,
        type: "chip",
        chipColor: this.appStatusColor,
      },
      {
        text: this.$t("static.subscriptionStatus"),
        value: this.$t(this.statusLabel) as string,
        type: "chip",
        chipColor: this.statusColor,
      },
      {
        text: this.$t("static.subscriptionGroup"),
        value: this.subscription ? this.subscription.groupName : "/",
      },
      {
        text: this.$t("static.billingType.title"),
        value: this.subscription
          ? (this.$t(
              "static.billingType." + this.subscription.billing_type
            ) as string)
          : "/",
      },
      {
        text: this.$t("static.trailDays"),
        value: this.subscription ? String(this.subscription.trail_days) : "/",
      },
      {
        text: this.$t("static.supportEndDate"),
        value: this.formattedSupportEndDate,
      },
      {
        text: this.$t('static.cancelledAt'),
        value: this.cancelledAt || ''
      },
      {
        text: this.$t("static.iosStoreUrl"),
        value: this.appStoreUrl
          ? (this.$t("static.open") as string)
          : (this.$t("static.notAvailableYet") as string),
        link: this.appStoreUrl || undefined,
        type: this.appStoreUrl ? "link" : "text",
        blank: this.appStoreUrl != null,
      },
      {
        text: this.$t("static.googlePlayStoreUrl"),
        value: this.googlePlayStoreUrl
          ? (this.$t("static.open") as string)
          : (this.$t("static.notAvailableYet") as string),
        link: this.googlePlayStoreUrl || undefined,
        type: this.googlePlayStoreUrl ? "link" : "text",
        blank: this.googlePlayStoreUrl != null,
      },
    ];

    if(this.appDetailUrl && this.appDetailUrl.length > 0) {
        items.push({
            text: this.$t("static.appDetail"),
            value: this.$t("static.open") as string,
            link: this.appDetailUrl,
            type: "link",
            blank: true,
        });
    }

    return items;
  }
}
