
import { Component, Watch } from "vue-property-decorator";
import { namespace, Getter } from "vuex-class";
import HomeSection, {
  BannerSizes,
  BannerTypes,
  HomeSectionState,
  HomeSectionType,
  CarouselType,
  CarouselItem,
} from "@/interfaces/HomeSection";
import DateTimePicker from "@/components/AppSettings/UI/DateTimePicker.vue";
import { SelectItem } from "@/interfaces/Select";
import { Data } from "@/store/modules/data";
import { mixins } from "vue-class-component";
import HomeSectionsTester from "@/mixins/HomeSectionsTester";
import MultilangField from "@/components/AppSettings/UI/MultilangField.vue";
import ValidableForm from "@/mixins/ValidableForm";
import ImageUploader from "@/components/AppSettings/UI/ImageUploader.vue";
import moment from "moment";
import EntitySearcher from "@/components/AppSettings/UI/EntitySearcher.vue";
import AddField from "@/components/AppSettings/UI/AddField.vue";
import MultipleEntitySearcher from "@/components/AppSettings/UI/MultipleEntitySearcher.vue";
import ImageUploaderMultiLang from "@/components/AppSettings/UI/ImageUploaderMultiLang.vue";
import { SearchableEntity } from "@/interfaces/SearchableEntity";

const appSteppers = namespace("appSteppers");
const data = namespace("data");
const sections = namespace("sections");
const configuration = namespace("configuration");
@Component({
  components: {
    DateTimePicker,
    MultilangField,
    ImageUploader,
    EntitySearcher,
    MultipleEntitySearcher,
    AddField,
    ImageUploaderMultiLang,
  },
})
export default class HomeSectionModal extends mixins(
  HomeSectionsTester,
  ValidableForm
) {
  @configuration.Getter("defaultLanguage") defaultLanguage!: string;
  @Getter("formLanguageIsocode") formLanguageIsocode!: string;
  @data.State((e: Data) => e.selectItems.homeSectionTypeItems)
  homeSectionTypeItems!: SelectItem[];
  @data.State((state: Data) => state.selectItems.carouselTypeItems)
  carouselItems!: SelectItem[];
  @data.State((e: Data) => e.selectItems.bannerTypeItems)
  bannerTypeItems!: SelectItem[];
  @data.State((e: Data) => e.selectItems.bannerSizeItems)
  bannerSizeItems!: SelectItem[];
  // @data.State((e: Data) => e.selectItems.orderTypeItems)
  // orderTypeItems!: SelectItem[];
  @data.State((e: Data) => e.selectItems.layoutTypeItems)
  layoutTypeItems!: SelectItem[];
  @data.State((state: Data) =>
    state.selectItems.customerGroups.filter((d: SelectItem) => d.value !== 0)
  )
  customerGroups!: SelectItem[];
  @sections.Getter("elegibleHomeSectionTypes")
  elegibleHomeSectionTypes!: SelectItem[];
  @sections.State((e: HomeSectionState) => e.editingSection)
  editingSection!: HomeSection;
  @sections.Mutation("updateHomeSectionField") updateHomeSectionField!: (
    e: Partial<HomeSection>
  ) => void;
  @sections.Mutation("closeEditor") closeEditor!: () => void;
  @sections.Action("saveSection") saveSection!: () => void;
  @sections.Mutation("updateBannerItem") updateBannerItem!: (body: {
    index: number;
    fields: Partial<CarouselItem>;
  }) => void;
  @sections.Mutation("updateCategoryItems") updateCategoryItems!: (
    item: SearchableEntity
  ) => void;

  loading = false;
  carouselItemTemplate: CarouselItem = {
    image: {
      original: null,
    },
    bannerType: BannerTypes.BANNER_NO_LINK,
    product: null,
    category: null,
    manufacturer: null,
    cms: null,
    hasMultiLangImage: false,
    title: {},
  };

  get labelForBannerImage() {
    let descriptionDetail = "";

    if (!this.editingSection.bannerSize) {
      this.editingSection.bannerSize = BannerSizes.RECTANGLE_HORIZONTAL;
    }

    switch (this.editingSection.bannerSize) {
      case BannerSizes.RECTANGLE_HORIZONTAL:
      case BannerSizes.RECTANGLE_HORIZONTAL_HALF: {
        descriptionDetail = " (2000 x 1000)";
        break;
      }
      case BannerSizes.RECTANGLE_VERTICAL:
      case BannerSizes.RECTANGLE_VERTICAL_HALF: {
        descriptionDetail = " (2000 x 2700)";
        break;
      }
      case BannerSizes.SQUARE:
      case BannerSizes.SQUARE_HALF: {
        descriptionDetail = " (2000 x 2000)";
        break;
      }
    }

    return (
      this.$t("dialogSection.bannerFields.image.label") + descriptionDetail
    );
  }

  get carouselTypeSelected() {
    for (let index = 0; index < this.carouselItems.length; index++) {
      if (
        this.editingSection.carouselType === this.carouselItems[index].value
      ) {
        return index;
      }
    }

    return 0;
  }

  get bannerSizesSelected() {
    for (let index = 0; index < this.bannerSizeItems.length; index++) {
      if (
        this.editingSection.bannerSize ===
        (this.bannerSizeItems[index].value as BannerSizes)
      ) {
        return index;
      }
    }

    return 0;
  }

  updateCarouselType($event: number) {
    if ($event != null && this.carouselItems[$event] != null) {
      this.updateHomeSectionField({
        carouselType: this.carouselItems[$event].value as CarouselType,
      });
    }
  }

  // Do that for clean data
  updateHomeSectionType($event: HomeSectionType) {
    let carouselType: CarouselType | null = null;
    if (
      $event === HomeSectionType.CAROUSEL &&
      this.editingSection.carouselType == null
    ) {
      carouselType = CarouselType.CAROUSEL_DEFAULT;
    }

    this.updateHomeSectionField({ type: $event, carouselType });
  }

  updateBannerSize($event: number) {
    if (this.bannerSizeItems[$event] != null) {
      this.updateHomeSectionField({
        bannerSize: this.bannerSizeItems[$event].value as BannerSizes,
      });
    }
  }

  carouselTypeImg(type: string) {
    switch (type as CarouselType) {
      case CarouselType.CAROUSEL_DEFAULT:
        return require("@/assets/images/layouts/layout_carousel_default.png");
      case CarouselType.CAROUSEL_FULL:
        return require("@/assets/images/layouts/layout_carousel_full.png");
      case CarouselType.CAROUSEL_MARGIN:
        return require("@/assets/images/layouts/layout_carousel_margin.png");
    }
  }

  bannerSizesTypeImg(index: number) {
    let img = undefined;

    switch (index) {
      case 0: {
        img = require("@/assets/images/layouts/layout_banner_hor.png");
        break;
      }
      case 1: {
        img = require("@/assets/images/layouts/layout_banner_hor_50.png");
        break;
      }
      case 2: {
        img = require("@/assets/images/layouts/layout_banner_vert.png");
        break;
      }
      case 3: {
        img = require("@/assets/images/layouts/layout_banner_vert_50.png");
        break;
      }
      case 4: {
        img = require("@/assets/images/layouts/layout_banner_square.png");
        break;
      }
      case 5: {
        img = require("@/assets/images/layouts/layout_banner_square_50.png");
        break;
      }
    }

    return img;
  }

  async onSave() {
    try {
      this.loading = true;
      const valid = await this.validate();
      if (valid) {
        await this.saveSection();
        this.loading = false;
        this.onClose();
      } else {
        this.scrollToError();
      }
      this.loading = false;
    } catch (err) {
      this.loading = false;
    }
  }

  scrollToError() {
    const form = this.$refs.form as any;
    const el = form.$el.querySelector(".error--text:first-of-type");
    el.scrollIntoView({ behavior: "smooth", block: "center" });
  }

  onClose() {
    this.$emit("closeModalEvent");
    this.removeValidation(this.getValidableId);
  }

  resetDate(type: string) {
    if (type === "from") {
      this.editingSection.activeFrom = null;
      this.editingSection.activeTo = null;
    } else {
      this.editingSection.activeTo = null;
    }
  }

  get active() {
    if (this.editingSection.activeFrom && this.editingSection.activeTo) {
      const active = moment().isBetween(
        this.editingSection.activeFrom,
        this.editingSection.activeTo
      )
        ? true
        : false;
      this.updateHomeSectionField({ active: active });
      return active;
    } else if (this.editingSection.activeFrom) {
      const active = moment(this.editingSection.activeFrom).isSameOrBefore(
        moment()
      );
      this.updateHomeSectionField({ active: active });
      return active;
    } else {
      return this.editingSection.active;
    }
  }

  updateShowToAllGroups(event: any) {
    this.updateHomeSectionField(event);
    if (this.editingSection.showToAllGroups) {
      this.updateHomeSectionField({ groupsToShow: null });
    }
  }

  get sectionPreviewImage(): string | undefined {
    if (
      !this.editingSection.hasMultiLangImage &&
      this.editingSection.image != null &&
      this.editingSection.image["original"] != null
    ) {
      return this.editingSection.image["original"].url;
    }

    return this.editingSection.image[this.defaultLanguage]?.url;
  }

  get showProductsSettings() {
    return (
      this.editingSection.type === HomeSectionType.SHOWCASE ||
      this.editingSection.type === HomeSectionType.SPECIAL_OFFERS ||
      this.editingSection.type === HomeSectionType.NEW_PRODUCTS ||
      this.editingSection.type === HomeSectionType.COLLECTION ||
      this.editingSection.type === HomeSectionType.CATEGORY_PRODUCTS ||
      this.editingSection.type === HomeSectionType.BEST_SELLER
    );
  }

  get showAdditionalFields() {
    if (
      this.editingSection.bannerType === undefined ||
      this.editingSection.bannerType === null
    ) {
      return false;
    }
    const layoutable = [
      BannerTypes.BANNER_NO_LINK,
      BannerTypes.BANNER_SPECIAL_OFFERS,
      BannerTypes.BANNER_NEW_PRODUCTS,
      BannerTypes.BANNER_SHOWCASE,
    ];
    return !(this.editingSection.bannerType in layoutable);
  }

  showBannerAdditionalFields() {
    return false;
  }

  get title() {
    return this.editingSection.id
      ? this.$t("dialogSection.editSectionTitle")
      : this.$t("dialogSection.newSectionTitle");
  }

  get isActiveFlagDisabled() {
    return this.editingSection.activeTo != null ||
      this.editingSection.activeFrom != null
      ? true
      : false;
  }

  get minDate() {
    return moment().valueOf();
  }

  get isHideTitleVisible() {
    return this.editingSection.title == null;
  }

  get hideTitle() {
    if (
      this.isCarousel(this.editingSection) ||
      this.isBanner(this.editingSection)
    ) {
      return this.editingSection.hideTitleInHome;
    } else {
      this.updateHomeSectionField({ hideTitleInHome: false });
      return false;
    }
  }

  checkTitle(title) {
    return Array.isArray(title) && title.length === 0 ? {} : title;
  }

  @Watch("isHideTitleVisible")
  changeHideVisible(newV: boolean) {
    if (newV === true) {
      this.updateHomeSectionField({ hideTitleInHome: false });
    }
  }
}
