
import { Component, Prop } from "vue-property-decorator";
import Product from "@/interfaces/Product";
import { Navigation } from "@/interfaces/Store";
import { Mutation, namespace, Getter } from "vuex-class";
import Category from "@/interfaces/Category";
import {
  AppColors,
  ConfiguratorState,
  ProductsLayoutType,
  FormData,
} from "@/interfaces/ConfigurationsType";
import HomeSectionsComponent from "@/mixins/HomeSectionComponent";
import { LayoutType } from "@/interfaces/HomeSection";
import { mixins } from "vue-class-component";
import { dragscroll } from "vue-dragscroll";
import ShopifyManager from "@/mixins/ShopifyManager";

// const data = namespace("data");
const simulator = namespace("simulator");
const configuration = namespace("configuration");

@Component({
  components: {},
  directives: {
    dragscroll,
  },
})
export default class ProductList extends mixins(
  HomeSectionsComponent,
  ShopifyManager
) {
  @Prop() eventType!: string;
  @Prop({ default: LayoutType.VERTICAL }) listDirection!: LayoutType;
  @Prop({ default: false }) wishList!: boolean;
  @Prop() category!: Category;
  @Prop() products!: Product[];
  @Prop({ default: null }) limit!: number;
  @Prop({ default: null }) enableVariant!: number;

  @Getter appDarkMode!: boolean;
  @Getter currentNavigation!: Navigation;
  @Mutation setNavigation!: (payload: Navigation) => void;
  @simulator.Mutation setWebPath!: (webPath: any) => void;
  //   @simulator.Mutation setProductsCatalog!: (productsCatalog: Product[]) => void;
  @configuration.State((state: ConfiguratorState) => state.form.showAvailable)
  showAvailable!: boolean;
  @configuration.State(
    (state: ConfiguratorState) => state.form[AppColors.secondaryColor]
  )
  secondaryColor!: string;
  @configuration.State(
    (state: ConfiguratorState) => state.form.productsLayoutType
  )
  productsLayoutType!: ProductsLayoutType;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @simulator.Action("getSingleProduct") getSingleProduct!: (productId: number) => Promise<Product|null>;

  dragged = false;

  get productItems() {
    const prod = this.item && this.item.items ? this.item.items : this.products;
    return this.limit ? prod.slice(0, this.limit) : prod;
  }

  get navigationPayload(): Navigation {
    const payload: Navigation = {
      name: "list",
      context: { name: this.title } as any,
    };

    return payload;
  }

  async viewAll(name) {
    this.setWebPath(this.item.webPath);
    this.setNavigation({
      name: "list",
      listType: "product",
      context: { name } as any,
    });
  }

  async goToProduct(product) {
    if (!this.dragged && !this.isShopify) {
      this.setNavigation({
        name: "detail",
        listType: "product",
        context: await this.getSingleProduct(product.id),
      });
    } else {
      this.setNavigation({
        name: "detail",
        context: product,
      });
    }
  }

  get layout() {
    return this.listDirection == LayoutType.HORIZONTAL
      ? "horizontal"
      : "vertical";
  }

  get variantClass() {
    return this.listDirection == LayoutType.HORIZONTAL
      ? ""
      : this.productsLayoutType == ProductsLayoutType.one
      ? this.enableVariant
        ? "one"
        : "two"
      : "two";
  }
}
