
import { Component, Prop } from "vue-property-decorator";
import { Mutation, namespace, Getter } from "vuex-class";
import { Navigation } from "@/interfaces/Store";
import HomeSectionsComponent from "@/mixins/HomeSectionComponent";
import { mixins } from "vue-class-component";
import { dragscroll } from "vue-dragscroll";
import { ConfiguratorState, FormData } from "@/interfaces/ConfigurationsType";
import Category from "@/interfaces/Category";

const simulator = namespace("simulator");
const configuration = namespace("configuration");

@Component({
  directives: {
    dragscroll,
  },
})
export default class CategoriesList extends mixins(HomeSectionsComponent) {
  @Mutation setNavigation!: (payload: Navigation) => void;
  @Getter appDarkMode!: boolean;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @Prop() categories!: any;
  @Prop({ default: false }) circleImage!: any;
  @simulator.Mutation setCategoryId!: (category_id: any) => void;

  dragged = false;

  async goToCatalog(category) {
    if (!this.dragged) {
      this.navigateTo("list", category);
    }
  }

  navigateTo(name: string, category: Category) {
    this.setNavigation({
      name: name,
      listType: "catalog",
      context: { name: category.name } as any,
    });
    this.setCategoryId(category.id);
  }

  get allCategories() {
    return this.item && this.item.categories
      ? this.item.categories
      : this.categories;
  }
}
