
import { Component, Vue } from "vue-property-decorator";
import { Getter, Mutation } from "vuex-class";

@Component({})
export default class AppDarkModeButton extends Vue {
  @Getter appDarkMode!: boolean;
  @Mutation switchDarkMode!: () => void;

  get themeIconClass(): string {
    return this.appDarkMode ? "dark-mode" : "light-mode";
  }

  onSwitchDarkMode(isFrom: string) {
    // Not toggle if same state
    if (
      (this.appDarkMode && isFrom !== "dark") ||
      (!this.appDarkMode && isFrom !== "light")
    ) {
      this.switchDarkMode();
    }
  }
}
