
import { Component, Prop } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import ValidableField from "@/mixins/ValidableField";

@Component({})
export default class SimpleFileUploader extends mixins(ValidableField) {
  @Prop({ default: "" }) label!: string;
  @Prop({ default: false, type: Boolean }) required!: boolean;
  @Prop({ type: String }) hint!: string;
  textFieldValue = null;

  get rules() {
    return this.required ? "required" : "";
  }

  operation() {
    const inp = this.$refs.input as any;
    // inp.value = null;
    inp.click();
  }

  onUpload(file: File) {
    this.$emit("onUpload", file);
    const inp = this.$refs.input as any;
    // const txtF = this.$refs.textFieldInput as any;
    this.textFieldValue = inp.value.replace(/C:\\fakepath\\/i, "");
  }
}
