import { BulkActionHas } from "@/interfaces/BulkAction";
import { CmsPage, CmsState } from "@/interfaces/CmsPage";
import CmsPageService from "@/services/CmsPageService";
import { ActionTree, GetterTree, Module, MutationTree } from "vuex";

const state: CmsState = {
  cmsPages: [],
  selected: [],
};

const mutations: MutationTree<CmsState> = {
  setAllSelected(state: CmsState) {
    const reset = state.selected.length > 0;
    if (reset) {
      state.selected = [];
    } else {
      state.selected = state.cmsPages;
    }
  },
  setCmsPages(state: CmsState, cmsPages: CmsPage[]) {
    state.cmsPages = cmsPages;
    state.selected = [];
  },
  addOrRemoveSelection(
    state: CmsState,
    { item, selected }: { item: CmsPage; selected?: boolean }
  ) {
    const newSelected =
      selected === undefined
        ? !state.selected.map((s) => s.id).includes(item.id!)
        : selected;

    if (newSelected) {
      if (!state.selected.map((s) => s.id).includes(item.id!)) {
        state.selected = [...state.selected, item];
      }
    } else {
      state.selected = state.selected.filter((e) => e.id !== item.id);
    }
  },
  removeCmsPage(state: CmsState, cmsPage: CmsPage) {
    state.cmsPages = state.cmsPages.filter((p) => p.id !== cmsPage.id);
  },
};

const getters: GetterTree<CmsState, any> = {
  has(state: CmsState): BulkActionHas {
    return {
      select: !(state.selected.length > 0),
      disable: true,
      enable: true,
    };
  },
  allSelected(state: CmsState): boolean {
    return state.selected.length === state.cmsPages.length;
  },
};

const actions: ActionTree<CmsState, any> = {
  async count({ commit }) {
    const total = await CmsPageService.count();
    commit("pagination/setCount", total, {
      root: true,
    });

    if(total.count == 0){
        commit("setCmsPages", []);
    }
  },
  async getCmsPages({ rootState, commit }) {
    const cmsPages = await CmsPageService.find(rootState.pagination.filter);
    commit("setCmsPages", cmsPages);
  },
  async addCmsPage({ dispatch }, cmsPage: {idCms: number; simpleCms: boolean}) {
    await CmsPageService.addPage(cmsPage.idCms, cmsPage.simpleCms);



    // Force typologies refresh for CMS
    dispatch(
      "data/getSelectItems",
      {},
      {
        root: true,
      }
    );
    dispatch("count");
  },
  async deleteCmsPage({ dispatch }, cmsPage: CmsPage) {
    const isDeleted = await CmsPageService.delete(cmsPage);
    if (isDeleted) {
      dispatch(
        "data/getSelectItems",
        {},
        {
          root: true,
        }
      );
      dispatch("count");
    }
    return isDeleted;
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
  namespaced: true,
} as Module<CmsState, any>;
