
import { Component, Prop, Vue } from "vue-property-decorator";
import { Getter, Mutation, namespace } from "vuex-class";
import { FormData, ConfiguratorState } from "@/interfaces/ConfigurationsType";
import { Navigation } from "@/interfaces/Store";
import Product from "@/interfaces/Product";
import Category from "@/interfaces/Category";
import Manufacturer from "@/interfaces/Manufacturer";
import {CmsPage} from "@/interfaces/CmsPage";

const configuration = namespace("configuration");

@Component({
  components: {},
})
export default class NavBar extends Vue {
  @Getter appDarkMode!: boolean;
  @configuration.Getter isSideNav!: boolean;
  @Getter currentNavigation!: Navigation;
  @Prop() title!: string;
  @Mutation popNavigation!: () => void;

  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;

  toggleSidebar() {
    this.$emit("sidebar-toggled");
  }

  getAppName(): string {
    const lenghtLimit = this.form.name.indexOf(" ") >= 0 ? 12 : 10;
    if (this.form.name.length > lenghtLimit) {
      return this.form.name.substr(0, lenghtLimit - 1) + "...";
    }
    return this.form.name;
  }

  get actualTitle() {
    switch (this.currentNavigation.name) {
      case "home":
        if (!this.form.showLogo) {
          return this.getAppName();
        } else {
          return "Showcase";
        }
      case "list":
        return (this.currentNavigation.context as Product)?.name ?? "";
      case "category":
        return (this.currentNavigation.context as Category)?.name ?? "";
      case "detail":
        return "";
      case "catalog":
        return "";
      case "cart":
        return this.$t('cart.cartLabel');
      case "catalog_manufacturers":
        return (this.currentNavigation.context as Manufacturer)?.name ?? "";
      case "cms":
        return (this.currentNavigation.context as CmsPage)?.title ?? "";
        case "payment_modules":
        return this.$t('cardTitles.payment')
      default:
        return "";
    }
  }

  get showSearchbar() {
    switch (this.currentNavigation.name) {
      case "catalog":
        return true;
      default:
        return false;
    }
  }

  get backArrow() {
    switch (this.currentNavigation.name) {
      case "detail":
      case "list":
      case "catalog_manufacturers":
      case "payment_modules":
        return true;
      default:
        return false;
    }
  }

  get cartIcon() {
    switch (this.currentNavigation.name) {
      case "detail":
        return true;
      default:
        return false;
    }
  }

  get navbarStyle() {
    return {
      color: this.appDarkMode ? "white" : this.form.textMainColor,
    };
  }

  toggleSidenav() {
    this.$emit("sidebar-toggled");
  }
}
