import axios, { AxiosInstance, InternalAxiosRequestConfig } from "axios";
import store from "@/store";
import { ToastTypes } from "@/interfaces/Toast";
import { getSessionToken } from '@shopify/app-bridge-utils';

declare module "axios" {
  export interface AxiosRequestConfig {
    type?: string;
  }
}

declare module "axios" {
  export interface AxiosRequestConfig {
    noToast?: boolean;
    isRetry?: boolean;
  }
}

const instance: AxiosInstance = axios.create();

if (window.shopifyAppBridge != null) {
  const createApp = window.shopifyAppBridge.AppBridge.default;
  const shopifyApp = createApp({
    apiKey: process.env.VUE_APP_SHOPIFY_API_KEY,
    host: window.shopifyAppBridge.params.get('host'),
    shopOrigin: window.shopifyAppBridge.params.get('host'),
    forceRedirect: true
  });

  instance.interceptors.request.use(
    (config: InternalAxiosRequestConfig<any>) => {
      if (shopifyApp != null) {
        return getSessionToken(shopifyApp) // requires a Shopify App Bridge instance
          .then((token) => {
            // Append your request headers with an authenticated token
            config.headers['Authorization'] = `Bearer ${token}`;
            return config;
          })
          .catch((error) => {
            return Promise.reject(error);
          });
      } else {
        return config;
      }
    }
  );
}

instance.interceptors.response.use(
  (response) => {
    if (!response.config.noToast) {
      switch (response.config.method) {
        case "post":
        case "put":
        case "patch":
          let data: any = {};
          try {
            data = JSON.parse(response.config.data);
          } catch (e) {
            data = {};
          }

          let message = "notifications.confirm.save";
          if (response.config.type) {
            switch (response.config.type) {
              case "file":
                message = "notifications.confirm.upload";
                break;
              case "cache":
                message = "notifications.confirm.cacheClear";
                break;
            }
          } else if (data.bulkAction && data.bulkAction == "delete") {
            message = "notifications.confirm.deleteBulk";
          }

          store.commit("toasts/pushToast", {
            type: ToastTypes.success,
            message,
          });
          break;
        case "delete":
          store.commit("toasts/pushToast", {
            type: ToastTypes.success,
            message: "notifications.confirm.delete",
          });
          break;
      }
    }
    return response;
  },
  (error) => {
    if (
      error.response.status !== undefined &&
      error.response.status == 412 &&
      !error.config.isRetry
    ) {
      error.config.isRetry = true;
      return new Promise((resolve) => {
        resolve(axios(error.config));
      });
    }

    if (!error.config.noToast) {
      if (
        error.response.status !== undefined &&
        ![422, 404].includes(error.response.status)
      ) {
        store.commit("toasts/pushToast", {
          type: ToastTypes.error,
          message: "notifications.error",
        });
      }
    }

    return Promise.reject(error);
  }
);

export default instance;
