import { Count, Filter, PaginationState } from "@/interfaces/Api";
import { GetterTree, Module, MutationTree } from "vuex";

const state: PaginationState = {
  total: {
    count: 0,
  },
  filter: {
    limit: 10,
    offset: 0,
  },
  selectedPage: 1,
};

const mutations: MutationTree<PaginationState> = {
  reset(state: PaginationState) {
    state.total = {
      count: 0,
    };
    state.filter = {
      limit: 10,
      offset: 0,
    };
    state.selectedPage = 1;
  },
  setCount(state: PaginationState, total: Count) {
    state.total = total;
  },
  setFilters(state: PaginationState, filter: Filter) {
    state.filter = filter;
  },
  setLimit(state: PaginationState, limit: number) {
    state.filter = {
      offset: (state.selectedPage - 1) * limit,
      limit,
    };
  },
  setSelectedPage(state: PaginationState, page: number) {
    state.selectedPage = page;
    state.filter = {
      ...state.filter,
      offset: (page - 1) * state.filter.limit,
    };
  },
};

const getters: GetterTree<PaginationState, any> = {};

export default {
  state,
  mutations,
  getters,
  namespaced: true,
} as Module<PaginationState, any>;
