
import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import ToastNotification from "@/components/AppSettings/UI/ToastNotification.vue";
import { ToastsState } from "@/store/modules/toasts";
import { Toast } from "@/interfaces/Toast";

const toasts = namespace("toasts");

@Component({
  components: {
    ToastNotification,
  },
})
export default class Notifications extends Vue {
  // Notification are mostly triggered directly from axions interceptor
  @toasts.State((state: ToastsState) => state.toasts) toasts!: Toast[];
  @toasts.Mutation deleteToast!: (toast: Toast) => void;
}
