import { Component, Prop } from "vue-property-decorator";
import { namespace, Mutation, Getter } from "vuex-class";
import {
  ConfiguratorState,
  FormData,
  SocialLoginData,
} from "@/interfaces/ConfigurationsType";
import { Navigation } from "@/interfaces/Store";
import { mixins } from "vue-class-component";
import ShopifyManager from "./ShopifyManager";

const configuration = namespace("configuration");
@Component
export default class ConfigurationStep extends mixins(ShopifyManager) {
  @Prop({ type: Number }) stepNum!: number;
  @Prop({ type: Boolean, default: false }) asStep!: boolean;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @configuration.State((state: ConfiguratorState) => state.socialLogin)
  socialLogin!: SocialLoginData;
  @configuration.Mutation("updateFormData") updateFormData!: (
    e: Partial<FormData>
  ) => void;
  @Mutation setNavigation!: (payload: Navigation) => void;
  @Getter("currentNavigation") currentNavigation!: Navigation;


  toExampleProduct() {
    if (this.currentNavigation.name != "product") {
      this.setNavigation({
        name: "detail",
        selectedIcon: "home",
      });
    }
  }

  toCatalogue() {
    if (this.currentNavigation.name != "catalog") {
      this.setNavigation({
        name: "catalog",
        selectedIcon: "explore",
      });
    }
  }

  get tableCols(): string[] {
    let cols: string[] = [
      "id",
      "type",
      "image",
      "title",
      "active",
      "groups",
      "updateAction",
    ];

    if (this.isShopify) {
      cols = cols.filter(c => !this.exceptions.tableCols.includes(c));
    }

    return cols;
  }
}
