
import { Component, Prop } from "vue-property-decorator";

import GroupText from "@/components/AppSettings/UI/GroupText.vue";
import { GroupTextItem } from "@/interfaces/GroupText";
import { mixins } from "vue-class-component";
import ShopifyManager from "@/mixins/ShopifyManager";

@Component({
  components: {
    GroupText,
  },
})
export default class DialogAppStatus extends mixins(ShopifyManager) {
  @Prop({ default: false }) show!: boolean;
  onClickOutside() {
    return this.$emit("click:outside");
  }

  get items(): GroupTextItem[] {
    const moduleDocumentation = this.isShopify ? 'static.shopifyModuleDocUrl' : 'static.prestashopModuleDocUrl';
    const moduleDocUrl = this.$tc(moduleDocumentation);
    const youtubeUrl = this.$tc('static.youtubeUrl');
    const appStoreMerchantUrl = this.$tc('static.appStoreMerchantUrl');
    const googlePlayMerchantUrl = this.$tc('static.googlePlayMerchantUrl');

    return [
      {
        text: this.$t("static.moduleDoc"),
        value: (this.$t("static.open") as string),
        link: moduleDocUrl,
        type: "link",
        blank: moduleDocUrl != null,
      },
      {
        text: this.$t("static.youtube"),
        value: (this.$t("static.open") as string),
        link: youtubeUrl,
        type: "link",
        blank: youtubeUrl != null,
      },
      {
        text: this.$t("static.googlePlayMerchant"),
        value: (this.$t("static.open") as string),
        link: googlePlayMerchantUrl,
        type: "link",
        blank: googlePlayMerchantUrl != null,
      },
      {
        text: this.$t("static.appStoreMerchant"),
        value: (this.$t("static.open") as string),
        link: appStoreMerchantUrl,
        type: "link",
        blank: appStoreMerchantUrl != null,
      }
    ];
  }
}
