
import { Component, Prop } from "vue-property-decorator";
import { dragscroll } from "vue-dragscroll";
import Product from "@/interfaces/Product";
import { CarouselType } from "@/interfaces/HomeSection";
import { mixins } from "vue-class-component";
import ImageDisplayer from "@/mixins/ImageDisplayer";

@Component({
  components: {},
  directives: {
    dragscroll,
  },
})
export default class Carousel extends mixins(ImageDisplayer) {
  @Prop() products!: Product[];
  @Prop({ default: null }) carouselType!: string;
  showDots = false;
  dotActive = 0;
  timeout;

  onScroll() {
    if (this.timeout) {
      clearTimeout(this.timeout);
    }

    const width = document.getElementById("slide-" + 0)?.offsetWidth;
    const scroll = document.getElementById("carousel-scrollable")!.scrollLeft;

    this.dotActive = Math.round(scroll / width!);

    this.timeout = setTimeout(() => {
      this.goToImage(this.dotActive, "smooth");
    }, 200);
  }

  get active() {
    return this.dotActive;
  }

  goToImage = (index, mode?) => {
    const slide = document.getElementById("slide-" + index);
    slide!.scrollIntoView({
      behavior: mode ?? "smooth",
      block: "center",
      inline: "center",
    });
  };

  get getCarouselClass() {
    let carouselClass = "";
    switch (this.carouselType) {
      case CarouselType.CAROUSEL_DEFAULT:
        this.showDots = false;

        carouselClass = "default";
        break;
      case CarouselType.CAROUSEL_FULL:
        this.showDots = true;

        carouselClass = "full";
        break;
      case CarouselType.CAROUSEL_MARGIN:
        this.showDots = true;

        carouselClass = "margin";
        break;
      default:
        this.showDots = true;
        carouselClass = "noList";
        break;
    }
    return carouselClass;
  }
}
