
import { Component, Vue, Prop, ModelSync } from "vue-property-decorator";

@Component
export default class AddField extends Vue {
  @ModelSync("items", "change", { type: Array }) itemsValue!: any[];
  @Prop({ default: null }) template!: any;
  @Prop({ default: null }) max!: number | null;

  onAdd() {
    if (this.max == null || this.max >= this.itemsValue.length) {
      this.itemsValue.push(JSON.parse(JSON.stringify(this.template)));
    }
  }

  onRemove(index: number) {
    this.itemsValue.splice(index, 1);
  }
}
