
import { mixins } from "vue-class-component";
import ConfigurationStep from "@/mixins/ConfigurationStep";
import { Component } from "vue-property-decorator";
import { namespace } from "vuex-class";

import { SelectItem, SelectProductsShapeItem } from "@/interfaces/Select";
import Message from "@/components/AppSettings/UI/Message.vue";
import { Data } from "@/store/modules/data";
import ValidableForm from "@/mixins/ValidableForm";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";
import { CatalogLayoutType } from "@/interfaces/ConfigurationsType";

const data = namespace("data");
@Component({
  components: {
    Message,
    CardMaterial,
  },
})
export default class LayoutTab extends mixins(
  ConfigurationStep,
  ValidableForm
) {
  @data.State((e: Data) => e.selectItems.navigationTypeItems)
  navigationTypeItems!: SelectItem[];
  @data.State((e: Data) => e.selectItems.productsShapeTypeItems)
  productsShapeTypeItems!: SelectProductsShapeItem[];
  @data.State((e: Data) => e.selectItems.catalogLayoutTypeItems)
  catalogLayoutTypeItems!: SelectItem[];
  @data.State((e: Data) => e.selectItems.productLayoutTypeItems)
  productLayoutTypeItems!: SelectItem[];
  @data.State((e: Data) => e.selectItems.appFontTypeItems)
  appFontTypeItems!: SelectItem[];

  get appFontData() {
    return !this.form.appFont ? this.appFontTypeItems[0] : this.form.appFont;
  }

  get productsShapeSelected() {
    for (let index = 0; index < this.productsShapeTypeItems.length; index++) {
      if (this.form.productsShape == this.productsShapeTypeItems[index].value) {
        return index;
      }
    }

    return 0;
  }

  async updateCatalogLayoutType(v: CatalogLayoutType) {
    this.updateFormData({ catalogLayoutType: v });
    await this.$store.dispatch("simulator/getCategories", v);
  }

  navigationTypeImg(index: number) {
    let img = undefined;

    switch (index) {
      case 0: {
        img = require("@/assets/images/layouts/layout_tab_bar.png");
        break;
      }
      case 1: {
        img = require("@/assets/images/layouts/layout_side_menu.png");
        break;
      }
    }

    return img;
  }

  productsShapeTypeImg(index: number) {
    let img = undefined;

    switch (index) {
      case 1: {
        img = require("@/assets/images/layouts/layout_rectangular_pic.png");
        break;
      }
      case 0: {
        img = require("@/assets/images/layouts/layout_square_pic.png");
        break;
      }
    }

    return img;
  }

  productsTypeImg(index: number) {
    let img = undefined;

    switch (index) {
      case 1: {
        img = require("@/assets/images/layouts/layout_one_product.png");
        break;
      }
      case 0: {
        img = require("@/assets/images/layouts/layout_two_product.png");
        break;
      }
    }

    return img;
  }

  catalogTypeImg(index: number) {
    let img = undefined;

    switch (index) {
      case 0: {
        img = require("@/assets/images/layouts/layout_category_image.png");
        break;
      }
      case 1: {
        img = require("@/assets/images/layouts/layout_category_list.png");
        break;
      }
    }

    return img;
  }
}
