
import { ConfiguratorState, FormData } from "@/interfaces/ConfigurationsType";
import NavigationManager from "@/mixins/NavigationManager";
import WishlistAware from "@/mixins/WishlistAware";
import { mixins } from "vue-class-component";
import { Component, Prop } from "vue-property-decorator";
import { namespace } from "vuex-class";

const configuration = namespace("configuration");

@Component({})
export default class Slider extends mixins(NavigationManager, WishlistAware) {
  @Prop({ default: false }) darkMode!: boolean;
  @Prop() active!: boolean;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @Prop({ default: false }) showSlider!: boolean;

  activeStyleVar = {};

  items = [
    {
      title: "App Name",
      links: [
        {
          name: "home",
          title: "Home",
          icon: "mdi-store-outline",
          clickable: true,
          visible: true,
        },
        {
          name: "catalog",
          title: "Catalog",
          icon: "mdi-magnify",
          clickable: true,
          visible: true,
        },
        {
          name: "cart",
          title: "Cart",
          icon: "mdi-cart-outline",
          clickable: true,
          visible: true,
        },
        {
          name: "whishlist",
          title: "Whishlist",
          icon: "mdi-heart-outline",
          visible: true,
        },
      ],
    },
    {
      title: "Account",
      links: [
        {
          title: "Account",
          icon: "mdi-account-outline",
          visible: true,
        },
      ],
    },
    {
      title: "Impostazioni",
      links: [
        {
          title: "Leave a Review",
          icon: "mdi-star-outline",
          visible: true,
        },
        {
          title: "Share",
          icon: "mdi-share-variant",
          visible: true,
        },
        {
          title: "Credits",
          icon: "mdi-information-outline",
          visible: true,
        },
      ],
    },
  ];

  get sliderManagement() {
    return this.showSlider;
  }

  get activeStyle() {
    return this.activeStyleVar;
    //   return {
    //         'background-color': this.darkMode ? 'black' : 'background-color: rgb(240, 240, 240);' ,
    //         color: this.darkMode ? this.form.darkTextColorOnSecondary : this.form.textSecondaryColor
    //     }
  }

  set activeStyle(button) {
    this.activeStyleVar = {
      "background-color": this.darkMode
        ? "black"
        : "background-color: rgb(240, 240, 240);",
      color: this.darkMode
        ? this.form.darkTextColorOnSecondary
        : this.form.textSecondaryColor,
    };
  }

  navigate(button) {
    if (!button.name) return;
    this.clickOutside();
    this.setNavigation({ name: button.name });
  }

  clickOutside() {
    this.$emit("sidebar-toggled");
  }
}
