
import { Component, Vue } from "vue-property-decorator";
import { FormData, ConfiguratorState } from "@/interfaces/ConfigurationsType";
import Button from "@/components/AppSettings/UI/Button.vue";
import { namespace, Getter } from "vuex-class";
import Product from "../../../interfaces/Product";
import { mixins } from "vue-class-component";
import { Navigation } from "@/interfaces/Store";
import { SimulatorState } from "@/interfaces/Simulator";
import WishlistAware from "@/mixins/WishlistAware";
import ImageDisplayer from "@/mixins/ImageDisplayer";
import CartPageBar from "../UI/CartPageBar.vue";

const configuration = namespace("configuration");
const simulator = namespace("simulator");
@Component({
  components: {
    Button,
    CartPageBar,
  },
})
export default class CartPage extends mixins(WishlistAware, ImageDisplayer) {
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @simulator.State((state: SimulatorState) => state.products)
  allProducts!: Product[];
  @Getter appDarkMode!: boolean;
  selectedItems: Product | null = null;

  mounted() {
    this.$emit("initComponent");
  }

  get activeProduct(): any | null {
    if (this.selectedItems) return this.selectedItems;

    const defaultProduct = this.allProducts;
    const nav: Navigation = this.$store.getters["currentNavigation"];

    this.selectedItems = nav.context as Product;
    return defaultProduct[0];
  }

  get defaultPrice(): any | null {
    return this.activeProduct ? this.activeProduct.discounted_price : "";
  }
}
