
import { Component, Prop, Watch } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import ValidableField from "@/mixins/ValidableField";

@Component({})
export default class ColorPicker extends mixins(ValidableField) {
  @Prop() declare value: string;
  @Prop() name!: string;
  @Prop() translation!: string;
  @Prop() rules!: string;
  @Prop({ type: Boolean, default: false }) blackWhite!: boolean;
  @Prop() hint!: string;
  @Prop({ default: false }) disabled!: boolean;

  writing = false;
  pickerColor = "#FFFFFF";

  get color(): string {
    return this.value;
  }

  set color(value: string) {
    this.setColor(value);
  }

  get pColor(): string {
    return this.pickerColor;
  }

  set pColor(value: string) {
    this.setColor(value);
  }

  blurring() {
    this.writing = false;
    this.pickerColor = this.value;
  }

  check(event: KeyboardEvent) {
    if ([8, 37, 38, 29, 40, 67, 86].includes(event.which)) {
      return;
    }
    const reg = /[a-fA-F0-9#]/gm;
    if (reg.exec(event.key) == null || this.color.length >= 7) {
      event.preventDefault();
    }
  }

  setColor(value: string) {
    if (
      value &&
      this.value &&
      value.toLowerCase() == this.value.toLowerCase()
    ) {
      return;
    }
    const start = value.indexOf("#") + 1;
    const val = "#" + value.substring(start, start + 6);
    this.$emit("updateColor", {
      name: this.name,
      value: val,
    });
  }

  @Watch("value", { immediate: true })
  updatePickerColor(val: string) {
    if (this.writing == false) {
      this.pickerColor = val;
    }
  }

  // mounted() {
  // 	this.setDefaultColor();
  // }
  // setDefaultColor() {
  // 	this.$emit('updateColor', {
  // 		name: this.name,
  // 		value: this.value,
  // 	});
  // }

  get inputClass(): string {
    const hex = this.color.replace(/#/g, "");
    const hexArr: any = [];

    for (let i = 0; i < 3; i++) {
      const start = i * 2;
      const end = start + 2;
      const h: any = hex.substring(start, end).padEnd(2, "0");
      hexArr.push(parseInt(h, 16));
    }

    const brightness = Math.round(
      (hexArr[0] * 299 + hexArr[1] * 587 + hexArr[2] * 114) / 1000
    );
    return brightness > 125 ? "input-text-black" : "input-text-white";
  }

  get swatches(): string[][] | undefined {
    if (this.blackWhite) {
      return [["#ffffff"], ["#000000"]];
    } else {
      return undefined;
    }
  }
}
