
import { Component, Prop, Watch } from "vue-property-decorator";
import { namespace } from "vuex-class";
import { CustomerState } from "@/store/modules/customer";
import { SubscriptionStatus } from "@/interfaces/AppSettings";

import AppStatusManager from "@/mixins/AppStatusManager";
import { mixins } from "vue-class-component";

const customer = namespace("customer");

@Component
export default class AppStatus extends mixins(AppStatusManager) {
  @customer.State((e: CustomerState) => e.status)
  declare status: SubscriptionStatus | null;
  @customer.State((e: CustomerState) => e)
  customerState!: CustomerState | null;

  @Prop()
  clickAppStatus!: boolean;

  @Watch("clickAppStatus")
  async onChangeClicked() {
    await this.loadStatus();
    if (this.customerState?.requestAppUrl) {
      window.open(this.customerState.requestAppUrl, "_blank");
    }
  }
}
