
import { Component, Prop, Ref } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import ValidableField from "@/mixins/ValidableField";
import { UploadedImage } from "@/interfaces/Upload";
import { ChangeFile, FileType } from "@/interfaces/AppSteppers";

@Component
export default class ImageUploader extends mixins(ValidableField) {
  @Prop({ default: null }) declare value: UploadedImage | null;
  @Prop() imageType!: FileType;
  @Ref("picker") picker!: HTMLInputElement;
  @Prop({ default: false, type: Boolean }) originalSize!: boolean;
  @Prop({ default: null }) label!: string | null;
  @Prop({ default: false, type: Boolean }) required!: boolean;
  @Prop({ default: "" }) aspectRatio!: number | string;
  @Prop({ type: Number }) maxWidth?: number;
  @Prop({ type: Number }) minWidth?: number;
  @Prop({ type: Number }) maxHeight?: number;
  @Prop({ type: Number }) minHeight?: number;
  @Prop({ type: Boolean, default: false }) disabled!: boolean;
  @Prop({ type: Boolean, default: true }) deletable!: boolean;
  @Prop({ type: Array, default: () => [] }) messages!: string[];
  @Prop({ type: Array, default: () => [] }) errorsMessages!: string[];
  @Prop() hint!: string;
  @Prop() backgroundSize!: string;

  defaultImage = require("@/assets/images/placeholder.png");
  transparentBg = require("@/assets/images/transparent_background.png");
  imagePreview: string | null = null;

  progress = 0;

  get containerStyle() {
    return {
      backgroundImage: "url(" + this.transparentBg + ")",
    };
  }

  get previewStyle() {
    const style = {
      backgroundImage: "url(" + this.imagePath + ")",
    };

    if (this.backgroundSize && this.imagePath !== this.defaultImage) {
      Object.assign(style, { "background-size": this.backgroundSize });
    }

    return style;
  }

  get showDelete() {
    return this.hasImage && this.deletable;
  }

  openPicker() {
    this.picker.click();
  }

  updatePreview(event: any) {
    const input = event.target;
    if (input.files && input.files[0]) {
      const reader = new FileReader();
      reader.onload = async () => {
        this.imagePreview = reader.result as string;
        this.progress = 30;
        try {
          const r: ChangeFile = {
            type: this.imageType,
            file: input.files[0],
          };

          const uploaded: UploadedImage = await this.$uploader.uploadFile(r);
          this.$emit("input", uploaded);
          this.validate(uploaded);
        } catch (e) {
          this.imagePreview = null;
        }
        this.progress = 0;
        this.imagePreview = null;
        input.value = "";
      };
      reader.readAsDataURL(input.files[0]);
    }
  }

  deleteImage() {
    this.$emit("input", null);
  }

  get imagePath() {
    return this.imagePreview || this.value?.url || this.defaultImage;
  }

  get hasImage() {
    return !!this.currentImage;
  }

  get currentImage() {
    return this.imagePreview || this.value?.url;
  }

  get showDiv() {
    return !this.currentImage || !this.originalSize;
  }

  get rules(): string {
    return this.required ? "required|imageRequired" : "";
  }
}
