
import { Component } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import HomeSectionsComponent from "@/mixins/HomeSectionComponent";
import { namespace, Getter } from "vuex-class";
import { ConfiguratorState, FormData } from "@/interfaces/ConfigurationsType";
import { BannerSizes } from "@/interfaces/HomeSection";

const configuration = namespace("configuration");

@Component
export default class Banner extends mixins(HomeSectionsComponent) {
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @Getter appDarkMode!: boolean;

  get image() {
    return this.item.image?.url;
  }

  get style(): Record<string, string> {
    let aspectRatio: string;

    switch (this.item.bannerSize) {
      case BannerSizes.RECTANGLE_VERTICAL_HALF: {
        aspectRatio = "0.7";
        break;
      }
      case BannerSizes.RECTANGLE_VERTICAL: {
        aspectRatio = "0.7";
        break;
      }
      case BannerSizes.RECTANGLE_HORIZONTAL_HALF: {
        aspectRatio = "2";
        break;
      }
      case BannerSizes.SQUARE_HALF: {
        aspectRatio = "1";
        break;
      }
      case BannerSizes.RECTANGLE_HORIZONTAL: {
        aspectRatio = "2";
        break;
      }
      default: {
        aspectRatio = "1";
        break;
      }
    }

    return {
      "aspect-ratio": aspectRatio,
      "background-image": 'url("' + this.backgroundImage + '")',
      "background-repeat": "no-repeat",
      "background-position": "center",
      "background-size": "cover",
      "border-radius": this.form.roundedImages ? "8px" : "",
      "margin": "2px"
    };
  }
}
