
import { Component, Prop } from "vue-property-decorator";
import { Language, TranslatedContent } from "@/interfaces/Language";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";
import { namespace, Getter, Mutation } from "vuex-class";
import ImageUploader from "@/components/AppSettings/UI/ImageUploader.vue";
import { mixins } from "vue-class-component";
import ValidableField from "@/mixins/ValidableField";

const configuration = namespace("configuration");
@Component({
  components: {
    ImageUploader,
  },
})
export default class ImageUploaderMultiLang extends mixins(ValidableField) {
  @Getter("formLanguageIsocode") formLanguageIsocode!: string;
  @Mutation("setLanguageIsocode") setLanguageIsocode!: (
    isoCode: string
  ) => void;
  @Prop({ default: null }) label!: string | null;
  @Prop({ default: null }) hint!: string | null;
  // @Prop({ default: false }) hideRow!: boolean;
  @Prop({ type: Boolean, default: false }) required!: boolean;
  @Prop() declare value: TranslatedContent;
  @configuration.State((e: ConfiguratorState) => e.languages)
  languages!: Language[];

  get currentImageVal() {
    if (this.value != null && this.value[this.formLanguageIsocode]) {
      return this.value[this.formLanguageIsocode];
    }

    return null;
  }

  get rules(): string {
    let rules: string[] = [];
    if (this.required) {
      rules = [
        ...rules,
        "imageLangRequired:" +
          this.languages.map((e: Language) => e.value).join(),
      ];
    }

    return rules.join("|");
  }

  onUpdateVal(val) {
    let res: TranslatedContent = this.value || {};
    res = { ...res, [this.formLanguageIsocode]: val };
    this.$emit("input", res);
    this.validate(res);
  }
}
