
import {Component, Vue} from "vue-property-decorator";
import Button from "@/components/AppSettings/UI/Button.vue";
import { Getter, namespace } from "vuex-class";
import {CmsPageState, SimulatorState} from "@/interfaces/Simulator";

const simulator = namespace("simulator");
@Component({
  components: {
    Button,
  },
})
export default class CMSPage extends Vue {
  @Getter appDarkMode!: boolean;
  @simulator.State((state: SimulatorState) => state.cmsPage) cmsPage!: CmsPageState;
}
