import AbstractApiService from "@/services/AbstractApiService";
import axios from "@/services/axios";
import { AxiosResponse } from "axios";

export type LabelTranslations = { [key: string]: string };

class TranslationsService extends AbstractApiService {
  protected action = "translations";
  async getAll(): Promise<LabelTranslations> {
    const response: AxiosResponse<LabelTranslations> = await axios.get(
      this.getUrl()
    );
    return response.data;
  }
}

export default new TranslationsService();
