
import { Component, Vue, Prop } from "vue-property-decorator";
import {
  BulkActionType,
  BulkActionHas,
  BulkActions,
  BulkActionVisible,
} from "@/interfaces/BulkAction";

@Component
export default class BulkActionButton extends Vue {
  @Prop({ default: BulkActionType.Notification }) type!: BulkActionType;
  @Prop({ default: true }) closeOnClick!: boolean;

  execBulkAction(action: string) {
    this.$emit("click", action as BulkActions);
  }

  get has(): BulkActionHas {
    let out: BulkActionHas = {
      enable: false,
      disable: false,
      select: false,
    };
    switch (this.type) {
      case BulkActionType.Cms:
        out = this.$store.getters["cms/has"];
        break;
      case BulkActionType.Notification:
        out = this.$store.getters["notifications/has"];
        break;
      case BulkActionType.Home:
        out = this.$store.getters["sections/has"];
        break;
    }

    return out;
  }

  get visible(): BulkActionVisible {
    switch (this.type) {
      case BulkActionType.Cms:
        return {
          select: true,
          enable: false,
          disable: false,
        };
      default:
        return {
          select: true,
          enable: true,
          disable: true,
        };
    }
  }
}
