import AbstractApiService from "@/services/AbstractApiService";
import axios from "@/services/axios";
import { AxiosResponse } from "axios";

import {
  ConfiguratorState,
  SaveConfiguration,
} from "@/interfaces/ConfigurationsType";
import { CheckStatusResponse } from "@/interfaces/AppSettings";
import { SubscriptionModuleVersion } from "@/interfaces/Module";

class ConfigurationService extends AbstractApiService {
  protected action = "configuration";
  protected checkStatusPromise?: Promise<
    AxiosResponse<CheckStatusResponse>
  > | null;

  async init(stepNum: number | null): Promise<ConfiguratorState> {
    const url = stepNum ? this.getUrl(stepNum) : this.getUrl();
    const response: AxiosResponse<ConfiguratorState> = await axios.get(url);
    return response.data;
  }

  async saveStep(
    index: number | undefined,
    data: SaveConfiguration
  ): Promise<ConfiguratorState> {
    const url = this.getUrl(index);
    const response: AxiosResponse<ConfiguratorState> = await axios.post(
      url,
      data
    );
    return response.data;
  }

  async save(data: SaveConfiguration): Promise<ConfiguratorState> {
    const url = this.getUrl();
    const response: AxiosResponse<ConfiguratorState> = await axios.post(
      url,
      data
    );
    return response.data;
  }

  async requireApp(): Promise<{ requestAppUrl?: string }> {
    const url = AbstractApiService.getUrlForAction("send_app");
    const response: AxiosResponse<{
      requestAppUrl?: string;
    }> = await axios.post(url, {});
    return response.data;
  }

  async sendRequestEdit(): Promise<{ requestAppUrl?: string | null }> {
    const url = AbstractApiService.getUrlForAction("send_request_edit");
    const response: AxiosResponse<{
      requestAppUrl?: string | null;
    }> = await axios.post(url, {});
    return response.data;
  }

  async checkStatus(): Promise<AxiosResponse<CheckStatusResponse>> {
    const url = AbstractApiService.getUrlForAction("check_status");
    if (!this.checkStatusPromise) {
      this.checkStatusPromise = axios.get(url);
    }
    try {
      return await this.checkStatusPromise;
    } finally {
      this.checkStatusPromise = null;
    }
  }

  async getModuleVersions(
    moduleName: string
  ): Promise<SubscriptionModuleVersion[]> {
    const url = AbstractApiService.getUrlForAction(
      "module_changelog",
      undefined,
      {
        module_name: moduleName,
      }
    );
    const response = await axios.get(url);
    return response.data;
  }

  async getQrCode(): Promise<{ url: string }> {
    const url = AbstractApiService.getUrlForAction("qrcode_app");
    const response = await axios.get(url);
    return response.data;
  }
}

export default new ConfigurationService();
