
import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import { Data } from "@/store/modules/data";
import { SelectItem } from "@/interfaces/Select";

const cms = namespace("cms");
const data = namespace("data");
@Component({})
export default class CmsPageModal extends Vue {
  @cms.Action("addCmsPage") addCmsPage!: (cmsPage: {idCms: number; simpleCms: boolean}) => Promise<void>;
  @data.State((state: Data) => state.selectItems.cmsPageTypes)
  cmsPageTypes!: SelectItem[];

  loading = false;
  selectedIdCms: number | null = null;
  selectedSimpleCms: boolean = false;

  onClose() {
    this.$emit("close");
  }

  onUpdate($event: number) {
    this.selectedIdCms = $event;
  }

  async onSave() {
    this.loading = true;
    if (this.selectedIdCms != null) {
      try {
        await this.addCmsPage({idCms: this.selectedIdCms, simpleCms: this.selectedSimpleCms});
        this.loading = false;
        this.selectedIdCms = null;
        this.onClose();
      } catch (e) {
        this.loading = false;
      }
    }
  }

  icon(isTrue: boolean) {
    return isTrue ? "checkbox-marked-circle" : "close-circle";
  }

  buttonColor(isTrue: boolean) {
    return isTrue ? 'error' : 'green';
  }

  async toggleSimpleCmsPage() {
    this.selectedSimpleCms = !this.selectedSimpleCms;
  }
}
