import { AppStatus } from "./App";
import { CatalogLayoutType } from "./ConfigurationsType";
import { NavigationType, ProductLayoutType } from "./HomeSection";
import { SubscriptionModule } from "./Module";

export type Primitive = string | boolean | number | any[];

export type SettingDataType<T extends Primitive> = {
  label: string;
  value: T;
};

export interface AppSettingsData {
  name: SettingDataType<string>;
  icon: SettingDataType<string>;
  splash: SettingDataType<string>;
  mainColor: SettingDataType<string>;
  textMainColor: SettingDataType<string>;
  secondaryColor: SettingDataType<string>;
  textSecondaryColor: SettingDataType<string>;
  isBlackText: SettingDataType<boolean>;
  navigationType: SettingDataType<NavigationType>;
  catalogLayoutType: SettingDataType<CatalogLayoutType>;
  productsLayoutType: SettingDataType<ProductLayoutType>;
  showStockQt: SettingDataType<boolean>;
  showAvailable: SettingDataType<boolean>;
  showLogo: SettingDataType<boolean>;
  logo: SettingDataType<string>;

  sections: SettingDataType<any[]>;

  ageEnabled: SettingDataType<boolean>;
  minAge: SettingDataType<string>;

  showPaypal: SettingDataType<boolean>;
  accessTokenSandbox: SettingDataType<string>;
  accessTokenLive: SettingDataType<string>;

  showSocialLogin: SettingDataType<boolean>;
  facebookEnabled: SettingDataType<boolean>;
  googleEnabled: SettingDataType<boolean>;
}

export type ContainerDataKey = {
  type: "text" | "boolean" | "image" | "color" | "select";
  key: string;
  getterExtractor?: string;
};

export interface AppSettingsContainer {
  containerName: string;
  dataKeys: ContainerDataKey[];
}

export enum SubscriptionStatus {
  ACTIVE = "active",
  TRIAL = "trialing",
  PAST = "past_due",
  PAUSED = "paused",
  REFUND = "refund",
  DELETED = "deleted",
  CANCELLED = 'cancelled'
}

export enum SubscriptionPeriod {
  MONTH = "month",
  YEAR = "year",
}

export type AppSubscription = {
  id: number;
  billing_type: SubscriptionPeriod;
  trail_days: number;
  groupName: string;
};

export type CheckStatusResponse = {
  appStatus: AppStatus;
  status: SubscriptionStatus;
  modules: SubscriptionModule[];
  haveSubscription: boolean;
  supportEndDate?: string;
  priorityMessage?: string;
  requestAppUrl?: string;
  subscription: AppSubscription | null;
  appStoreUrl: string | null;
  googlePlayStoreUrl: string | null;
  appDetailUrl: string;
  isPrestashopAddonsApp: boolean;
  cancelledAt: string | null;
};
