
import { Component, Prop, Watch } from "vue-property-decorator";
import { namespace, State, Getter, Mutation } from "vuex-class";

import AppHeader from "@/components/AppSimulator/UI/AppHeader.vue";
import TabBar from "@/components/AppSimulator/UI/TabBar.vue";
import Slider from "@/components/AppSimulator/UI/Slider.vue";
import AppNavigator from "@/components/AppSimulator/UI/AppNavigator.vue";
import GroupsFilter from "@/components/AppSimulator/UI/GroupsFilter.vue";
import HomePage from "@/components/AppSimulator/pages/HomePage.vue";
import ListPage from "@/components/AppSimulator/pages/ListPage.vue";
import CatalogPage from "@/components/AppSimulator/pages/CatalogPage.vue";
import CMSPage from "@/components/AppSimulator/pages/CMSPage.vue";
import DetailPage from "@/components/AppSimulator/pages/DetailPage.vue";
import CartPage from "@/components/AppSimulator/pages/CartPage.vue";
import IconPage from "@/components/AppSimulator/pages/IconPage.vue";
import PaymentModules from "@/components/AppSimulator/pages/PaymentModules.vue"
import SplashScreenPage from "@/components/AppSimulator/pages/SpashScreenPage.vue";
import CatalogManufacturersPage from "@/components/AppSimulator/pages/CatalogManufacturersPage.vue";
import {
  AppFontType,
  ConfiguratorState,
  FormData,
} from "@/interfaces/ConfigurationsType";
import { SelectItem } from "@/interfaces/Select";
import { Data } from "@/store/modules/data";
import { Navigation, Page, RootState } from "@/interfaces/Store";
import {SimulatorState} from "@/interfaces/Simulator";
import { mixins } from 'vue-class-component';
import SimulatorManager from '@/mixins/SimulatorManager';

const configuration = namespace("configuration");
const data = namespace("data");
const simulator = namespace("simulator");

@Component({
  components: {
    AppHeader,
    TabBar,
    Slider,
    HomePage,
    ListPage,
    CatalogPage,
    DetailPage,
    CartPage,
    CMSPage,
    IconPage,
    SplashScreenPage,
    PaymentModules,
    AppNavigator,
    CatalogManufacturersPage,
    GroupsFilter
  },
})
export default class AppSimulator extends mixins(SimulatorManager) {
  @State((state: RootState) => state.currentPage) currentPage!: Page;
  @Getter appDarkMode!: boolean;
  @Getter currentNavigation!: Navigation;
  @Prop({ default: false }) navigator!: boolean;
  @Mutation setNavigation!: (payload: Navigation) => void;
  @configuration.Getter isSideNav!: boolean;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;

  @simulator.State((state: SimulatorState) => state) simulator!: SimulatorState;
  @simulator.Mutation setPagination!: (payload: any) => void;
  @simulator.Mutation reset!: () => void;
  @simulator.Action getGeneric!: (context?: any) => Promise<void>;

  @data.State((e: Data) => e.selectItems.appFontTypeItems)
  appFontTypeItems!: SelectItem[];
  @configuration.State(
    (state: ConfiguratorState) => state.form.productsBackgroundGrey
  )
  backgroundGrey!: boolean;
  showSlider = false;
  refresh = false;
  context: any;

  toggleSlider(event) {
    this.showSlider = event ?? !this.showSlider;
  }

  detectScroll() {
    this.refresh = false;
    const scrollable = document.getElementById("scrollable-container");
    const topPlusHeight = scrollable!.scrollTop + scrollable!.offsetHeight;
    const scrollHeight = scrollable!.scrollHeight;

    this.refresh = scrollHeight - topPlusHeight < 200 ?? false;
  }

  @Watch("refresh")
  onPropertyChanged() {
    if (this.refresh) {
      setTimeout(() => {
        this.setPagination({ context: this.currentPage.id });
        this.getGeneric();
      }, 500);
    }
  }

  initComponent(event) {
      this.reset();
      this.refresh = false;
      this.setPagination({ context: this.currentPage.id });
      (this.$refs.appContent as Element).scrollTop = 0;
      this.getGeneric(event);
  }

  get noNavBar() {
    return !this.form.showNavBar && this.currentNavigation.name === "detail";
  }

  get previewWrapperStyle(): Record<string, string> {
    if (this.form.appFont !== AppFontType.System) {
      for (let i = 0; i < this.appFontTypeItems.length; i++) {
        if (this.appFontTypeItems[i].value === (this.form.appFont as string)) {
          return { "font-family": this.appFontTypeItems[i].text };
        }
      }
    }

    return {};
  }
}
