
import { Component, Vue } from "vue-property-decorator";
import Button from "@/components/AppSettings/UI/Button.vue";
import { Getter, Mutation, namespace } from "vuex-class";
import { ConfiguratorState, FormData } from "@/interfaces/ConfigurationsType";
import { Navigation } from "@/interfaces/Store";

const configuration = namespace("configuration");
const MOCK_SPLASH = "https://via.placeholder.com/400x800";
@Component({
  components: {
    Button,
  },
})
export default class SplashScreenPage extends Vue {
  @Getter appDarkMode!: boolean;
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @Mutation setNavigation!: (payload: Navigation) => void;
  get splashStyle() {
    if (this.appDarkMode) {
      return this.form.darkSplash ? this.form.darkSplash.url : MOCK_SPLASH;
    } else {
      return this.form.splash ? this.form.splash.url : MOCK_SPLASH;
    }
  }
}
