import Vue from "vue";
import Vuex, { ActionTree, GetterTree, MutationTree } from "vuex";
import data from "@/store/modules/data";
import cms from "@/store/modules/cms";
import appSteppers from "@/store/modules/stepper";
import configuration from "@/store/modules/configuration";
import toasts from "@/store/modules/toasts";
import customer from "@/store/modules/customer";
import sections from "@/store/modules/sections";
import pagination from "@/store/modules/pagination";
import simulator from "@/store/modules/simulator";
import notifications from "@/store/modules/notifications";
import {
  AppState,
  RootState,
  Navigation,
  AppStateShopify,
} from "@/interfaces/Store";
import SectionService from "@/services/SectionService";
import VuexStepper from "@/plugins/vuex-step";
import VuexAppSync from "@/plugins/vuex-app-sync";

Vue.config.devtools = true;
Vue.use(Vuex);

const tabSection: any = {
  1: "store",
  2: "colors",
  3: "layout",
  4: "home",
  5: "setting",
  6: "notifications",
  7: "cms",
  8: "modules",
  9: "payment_modules",
};

const navPages: Array<string> = [
  "icon",
  "splash",
  "home",
  "catalog",
  "catalog_manufacturers",
  "list",
  "search",
  "cart",
  "payment_modules",
];

export const tabsPages: any = {
  "steps.store": "icon",
  "steps.colors": "home",
  "steps.layout": "catalog",
  "steps.home": "home",
  "steps.settings": "home",
  "steps.notifications": "home",
  "steps.cms": "home",
  "steps.modules": "home",
  "steps.payment_modules": "home",
};

const state: AppState = {
  navigation: [{ name: "home" }],
  currentPage: {
    id: "home",
    component: "HomePage",
    title: "Showcase",
    dynamicTitle: true,
  },
  currentTab: 0,
  error: undefined,
  stepperMode: false,
  languageIsoCode: null,
  darkMode: false,
  apiAdminUrl: "",
  apiBaseUrl: "",
  shopify: null,
  isPaypalSandbox: false,
  isMultistore: false,
  stepKey: 1,
  tabKey: -1,
};

const pages: any = {
  splash: {
    id: "splash",
    component: "SplashScreenPage",
    title: "",
  },
  icon: {
    id: "icon",
    component: "IconPage",
    title: "",
  },
  home: {
    id: "home",
    component: "HomePage",
    title: "Showcase",
    dynamicTitle: true,
  },
  catalog: {
    id: "catalog",
    component: "CatalogPage",
    title: "",
    noTitle: true,
  },
  catalog_manufacturers: {
    id: "catalog_manufacturers",
    component: "CatalogManufacturersPage",
    title: "",
    dynamicTitle: true,
  },
  detail: {
    id: "detail",
    component: "DetailPage",
    title: "",
    noTitle: true,
  },
  list: {
    id: "list",
    component: "ListPage",
    title: "",
    dynamicTitle: true,
  },
  cart: {
    id: "cart",
    component: "CartPage",
    title: "Cart",
    noTitle: false,
  },
  cms: {
    id: "cms",
    component: "CMSPage",
    title: "",
    noTitle: true,
  },
  payment_modules: {
    id: "payment_modules",
    component: "PaymentModules",
    title: "Payment Modules",
    noTitle: false,
  },
};

const mutations: MutationTree<AppState> = {
  setShopify(state: AppState, shopify: AppStateShopify | null) {
    state.shopify = shopify;
  },
  setApiAdminUrl(state: AppState, apiAdminUrl: string) {
    state.apiAdminUrl = apiAdminUrl;
  },
  setApiBaseUrl(state: AppState, apiBaseUrl: string) {
    state.apiBaseUrl = apiBaseUrl;
  },
  setPaypalSandbox(state: AppState, paypalSandobox: boolean) {
    state.isPaypalSandbox = paypalSandobox;
  },
  setShopDescription(state: AppState, shopDescription: string) {
    state.shopDescription = shopDescription;
  },
  setIsMultistore(state: AppState, isMultistore: boolean) {
    state.isMultistore = isMultistore;
  },
  setStepKey(state: AppState, stepKey: number) {
    state.stepKey = stepKey;
  },
  setTabKey(state: AppState, tabKey: number) {
    state.tabKey = tabKey;
  },
  setCurrentTab(state: AppState, val: number) {
    state.currentTab = val;
  },
  setStepperMode(state: AppState, val: boolean) {
    state.stepperMode = val;
  },
  setError(state: AppState, error: string | undefined) {
    state.error = error;
  },
  setNavigation(state: AppState, navigation: Navigation) {
    // Prepare selectedIcon
    // Retrieve by previous page
    let prevNavPage = navPages.includes(navigation.name) ? navigation.name : "";
    if (prevNavPage.length === 0) {
      for (let i = state.navigation.length - 1; i >= 0; i--) {
        if (navPages.includes(state.navigation[i].name)) {
          prevNavPage = state.navigation[i].name;
          break;
        }
      }
    }
    navigation.selectedIcon = prevNavPage.length > 0 ? prevNavPage : undefined;

    state.navigation = [...state.navigation, navigation];
    state.currentPage = pages[navigation.name] || null;
  },
  popNavigation(state: AppState) {
    if (state.navigation.length > 1) {
      state.navigation = state.navigation.slice(0, -1);
      state.currentPage =
        pages[state.navigation[state.navigation.length - 1].name] || null;
    }
  },
  setLanguageIsocode(state: AppState, language: string) {
    state.languageIsoCode = language;
  },
  switchDarkMode(state: AppState) {
    state.darkMode = !state.darkMode;
  },
};

const actions: ActionTree<AppState, RootState> = {
  async getSectionDocs({ commit }) {
    const docs = await SectionService.getSectionDocs();
    commit("appSteppers/setStepDocs", docs);
  },
  init({ commit }) {
    commit("setCurrentTab", 0);
  },
  setCurrentTab({ commit }, tab: number) {
    commit("setTabKey", tab);
    commit("setCurrentTab", tab);
  },
};

const getters: GetterTree<AppState, RootState> = {
  appDarkMode(state: AppState): boolean {
    return state.darkMode;
  },
  currentNavigation(state: AppState): Navigation | undefined {
    return [...state.navigation].pop();
  },

  formLanguageIsocode(
    state: AppState,
    getters: any,
    rootState: any,
    rootGetters: any
  ): string {
    return (
      state.languageIsoCode || rootGetters["configuration/defaultLanguage"]
    );
  },
  headerIcon(
    state: AppState,
    getters: GetterTree<AppState, RootState>
  ): string | null {
    const currentNavigation: Navigation = getters["currentNavigation"];
    let retval: any = null;
    if (currentNavigation) {
      // gestisco solo i casi in cui deve effettivamente venire mostrata un'icona
      switch (currentNavigation.name) {
        case "explore":
          retval = "search";
          break;
        case "home":
          retval = "bell";
          break;
        case "cart":
          retval = "";
          break;
        default:
          retval = "arrow-left";
          break;
      }
    }

    return retval;
  },
  isShopify(state: AppState): boolean {
    return state.shopify != null;
  },
};

export default new Vuex.Store({
  plugins: [VuexStepper, VuexAppSync],
  state,
  mutations,
  getters,
  actions,
  modules: {
    data,
    appSteppers,
    configuration,
    sections,
    toasts,
    customer,
    notifications,
    cms,
    pagination,
    simulator,
  },
});
