
import { Component, Prop } from "vue-property-decorator";
import { SearchableEntity } from "@/interfaces/SearchableEntity";
import AppDataService from "@/services/AppDataService";
import ValidableField from "@/mixins/ValidableField";
import { mixins } from "vue-class-component";
const DEBOUNCE_INTERVAL = 750;
@Component
export default class EntitySearcher extends mixins(ValidableField) {
  @Prop() declare value: SearchableEntity | null;
  @Prop() entityType!: string;
  @Prop() label!: string;
  @Prop({ type: Boolean, default: false }) required!: boolean;

  loading = false;
  searchRunned = false;
  searchTerm: string | null = null;
  timeout: any = null;

  items: SearchableEntity[] = [];

  search(term: string | null) {
    if (term == this.searchTerm) {
      return;
    }

    this.searchTerm = term;
    if (this.timeout) {
      clearTimeout(this.timeout as number);
    }

    this.timeout = setTimeout(() => {
      this.runSearch(term);
    }, DEBOUNCE_INTERVAL);
  }

  async runSearch(term: string | null) {
    if (!term) {
      if (this.items.length > 0) {
        this.items = [];
      }

      if (this.searchRunned) {
        this.$emit("input", null);
      }
      return;
    }

    this.loading = true;
    try {
      this.items = await AppDataService.searchItems(this.entityType, term);
      this.searchRunned = true;
    } catch (e) {
      console.log(e);
    }

    this.loading = false;
    clearTimeout(this.timeout);
  }

  get selectableItems() {
    if (false == this.searchRunned && this.value) {
      return [this.value];
    }

    return this.items;
  }

  get term(): string | undefined {
    if (this.searchTerm) {
      return this.searchTerm;
    }

    return this.value?.name;
  }

  get rules(): string {
    return this.required ? "required|relatedRequired" : "";
  }

  emitItem(item: SearchableEntity) {
    this.$emit("input", item);
  }
}
