import { Filter } from "@/interfaces/Api";
import store from '@/store';

export default abstract class AbstractApiService {
  protected abstract action: string;

  static getUrlForAction(
    action: string,
    id?: number,
    qs?: { [key: string]: any }
  ): string {
    const isShopify = store.getters['isShopify'];
    const url = new URL(location.href);
    const token = new URLSearchParams(String(url)).get("token") ?? null;

    const c = `index.php`;
    let q: { [key: string]: any } = {
      controller: "AdminMrShopApiSettings",
      action,
    };

    Object.assign(q, { token: token });

    if (id) {
      q = { ...q, id };
    }

    if (qs) {
      q = { ...q, ...qs };
    }
    const queryP = AbstractApiService.computeQueryString(q);
    if (isShopify) {
      return "?token=" + (store.state?.shopify?.token ?? '') + "&" + queryP;
    } else {
      return `${c}?${queryP}`;
    }
  }

  getUrl(id?: number, qs?: { [key: string]: any }): string {
    return AbstractApiService.getUrlForAction(this.action, id, qs);
  }

  filterUrl(filter: Filter): string {
    return AbstractApiService.getUrlForAction(this.action, undefined, filter);
  }

  getCountUrl(): string {
    return AbstractApiService.getUrlForAction(this.action, undefined, {
      count: 1,
    });
  }

  static getFrontendUrl(entity: string, qs?: { [key: string]: any }): string {
    const apiUrl: string = store.state.apiBaseUrl;
    let u = `${apiUrl}/client_mrshop/${entity}`;
    if (store.state.shopify != null) {
      return (
        entity +
        "?token=" +
        store.state.shopify.token +
        (qs != null ? "&" + AbstractApiService.computeQueryString(qs) : "")
      );
    } else {
      u +=
        (entity ? (entity.indexOf("?") != -1 ? "&" : "?") : "?") +
        (qs != null ? AbstractApiService.computeQueryString(qs) : "");
    }
    return u;
  }

  static computeQueryString(qs: { [key: string]: any }) {
    return Object.entries(qs)
      .filter(([key, value]) => value !== null)
      .map(
        ([key, value]) =>
          `${encodeURIComponent(key)}=${encodeURIComponent(value)}`
      )
      .join("&");
  }

  isShopify(): boolean {
    return store.getters['isShopify'];
  }
}
