
import { Component, Prop } from "vue-property-decorator";
import Carousel from "@/components/AppSimulator/UI/Carousel.vue";
import Product from "@/interfaces/Product";
import { mixins } from "vue-class-component";
import HomeSectionsComponent from "@/mixins/HomeSectionComponent";

@Component({
  components: {
    Carousel,
  },
})
export default class CarouselList extends mixins(HomeSectionsComponent) {
  @Prop() products!: Product[];
  @Prop() carouselType!: Product[];

  get productItems() {
    return this.item && this.item.banners ? this.item.banners : this.products;
  }
}
