import { Count, Filter } from "@/interfaces/Api";
import {
  AppNotificationBulkActions,
  AppScheduledNotification,
} from "@/interfaces/AppNotification";
import axios from "@/services/axios";
import { AxiosResponse } from "axios";
import AbstractApiService from "./AbstractApiService";

class NotificationService extends AbstractApiService {
  protected action = "notifications";

  async find(filter: Filter): Promise<AppScheduledNotification[]> {
    const url = this.filterUrl(filter);
    const response: AxiosResponse<AppScheduledNotification[]> = await axios.get(
      url
    );
    return response.data;
  }

  async count(): Promise<Count> {
    const url = this.getCountUrl();
    const response: AxiosResponse<Count> = await axios.get(url);
    return response.data;
  }

  async save(
    body: AppScheduledNotification
  ): Promise<AppScheduledNotification> {
    const url = this.getUrl(body.id);
    const saved: AxiosResponse<AppScheduledNotification> = await axios.post(
      url,
      body
    );
    return saved.data;
  }

  async delete(id: number): Promise<void> {
    const url = this.getUrl(id);
    await axios.delete(url);
  }

  async bulkAction(
    bulkAction: AppNotificationBulkActions,
    ids: number[]
  ): Promise<AppScheduledNotification[]> {
    const url = this.getUrl();
    const data = { bulkAction, ids };
    const response: AxiosResponse<AppScheduledNotification[]> =
      await axios.post(url, data);
    return response.data;
  }
}

export default new NotificationService();
