const mutations: MutationTree<ConfiguratorState> = {
  init(state: ConfiguratorState, data: ConfiguratorState) {
    Object.assign(state, data);
  },
  updateFormData(state: ConfiguratorState, data: Partial<FormData>) {
    if ("ageEnabled" in data && !data.ageEnabled) {
      data.minAge = 0;
    }

    state.form = { ...state.form, ...data };
  },
  setCurrentStep(state: ConfiguratorState, currentStep: 1 | 2 | 3 | 4 | 5) {
    state.currentStep = currentStep;
  },
  setAllowedStep(state: ConfiguratorState, step: 1 | 2 | 3 | 4 | 5) {
    state.allowedStep = step;
  },
  setQrCodeUrl(state: ConfiguratorState, qrCodeUrl: string | null) {
    state.qrUrl = qrCodeUrl;
  },
};

const actions: ActionTree<ConfiguratorState, any> = {
  async init(
    context: ActionContext<ConfiguratorState, any>
  ): Promise<ConfiguratorState> {
    const config = await ConfigurationService.init(1);
    if (config) {
      config.form.mainColor =
        config.form.mainColor === "" ? "#46709b" : config.form.mainColor;

      config.form.secondaryColor =
        config.form.secondaryColor === ""
          ? "#fc5721"
          : config.form.secondaryColor;

      config.form.textMainColor =
        config.form.textMainColor === "" ? "#FFFFFF" : config.form.textMainColor;

      config.form.textSecondaryColor =
        config.form.textSecondaryColor === ""
          ? "#FFFFFF"
          : config.form.textSecondaryColor;

      if(config.isStepper && config.allowedStep! > 2){
        config.currentStep = config.allowedStep;
      }
    }

    context.dispatch("updateConfiguration", config);
    return config;
  },
  async saveStep(
    context: ActionContext<ConfiguratorState, any>,
    updateRequired: boolean
  ) {
    const config = await ConfigurationService.saveStep(
      context.state.currentStep,
      {
        form: context.state.form,
        updateRequired,
      }
    );

    context.dispatch("updateConfiguration", config);
    // return config; -> Non usato
  },
  async saveConfiguration(
    context: ActionContext<ConfiguratorState, any>,
    updateRequired: boolean
  ): Promise<ConfiguratorState> {
    const config = await ConfigurationService.save({
      form: context.state.form,
      updateRequired,
    });
    context.dispatch("updateConfiguration", config);
    return config;
  },
  updateConfiguration(
    context: ActionContext<ConfiguratorState, any>,
    configuration: ConfiguratorState
  ) {
    context.commit("init", configuration);
    const inter = configuration.isStepper ? "lazy" : "aggressive";
    setInteractionMode(inter);
  },
  async requireApp(
    context: ActionContext<ConfiguratorState, any>
  ): Promise<{ requestAppUrl?: string, appPlansUrl?: string }> {
    const response = await ConfigurationService.requireApp();
    if (!response.requestAppUrl) {
      context.dispatch("init");
    }
    return response;
  },
  async sendAppRequestEdit(): Promise<{ requestAppUrl?: string | null }> {
    return await ConfigurationService.sendRequestEdit();
  },
  async getAppQrCode({ commit }) {
    const data = await ConfigurationService.getQrCode();
    commit("setQrCodeUrl", data.url);
  },
};

const getters: GetterTree<ConfiguratorState, any> = {
  isSideNav(state: ConfiguratorState): boolean {
    return state.form.navigationType === NavigationType.sidebar;
  },
  defaultLanguage(state: ConfiguratorState): string {
    if (state.form.defaultLanguage && state.languages.find(d => d.value === state.form.defaultLanguage)) {
      return state.form.defaultLanguage;
    }

    if (state.languages.length > 0) {
      return state.languages[0].value;
    }

    return "en";
  },
  hasQr(state: ConfiguratorState): boolean {
    return !!state.qrData;
  },
  qrUrl(state: ConfiguratorState): string | null {
    return state.qrUrl;
  },
};
const module: Module<ConfiguratorState, any> = {
  state,
  actions,
  mutations,
  getters,
  namespaced: true,
};
export default module;
