import {
  AppScheduledNotification,
  ScheduleNotificationType,
} from "@/interfaces/AppNotification";
import { mixins } from "vue-class-component";
import { Component } from "vue-property-decorator";
import ShopifyManager from "./ShopifyManager";

@Component
export default class AppNotificationManager extends mixins(ShopifyManager) {
  isGeneric(notification: AppScheduledNotification): boolean {
    return notification.notificationType === ScheduleNotificationType.Generic;
  }

  haveProductSearch(notification: AppScheduledNotification): boolean {
    return [ScheduleNotificationType.ProductDetail].includes(
      notification.notificationType
    );
  }

  haveCollectionSearch(notification: AppScheduledNotification): boolean {
    return [ScheduleNotificationType.Collection].includes(
      notification.notificationType
    );
  }

  isCategory(notification: AppScheduledNotification): boolean {
    return notification.notificationType === ScheduleNotificationType.Catalog;
  }

  isCms(notification: AppScheduledNotification): boolean {
    return notification.notificationType === ScheduleNotificationType.Cms;
  }

  isBestSeller(notification: AppScheduledNotification): boolean {
    return (
      notification.notificationType === ScheduleNotificationType.BestSeller
    );
  }
}
