
import { mixins } from "vue-class-component";
import ConfigurationStep from "@/mixins/ConfigurationStep";
import { Component } from "vue-property-decorator";
import { namespace } from "vuex-class";
import Message from "@/components/AppSettings/UI/Message.vue";
import SimpleFileUploader from "@/components/AppSettings/UI/SimpleFileUploader.vue";
import { PaymentModule } from "@/interfaces/Module";
import { Data } from "@/store/modules/data";
import ValidableForm from "@/mixins/ValidableForm";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";
import MultilangField from "@/components/AppSettings/UI/MultilangField.vue";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";
import ExpandableCardMaterial from "@/components/AppSettings/UI/ExpandableCardMaterial.vue";

const appData = namespace("data");
const configuration = namespace("configuration");

const data = namespace('data')
@Component({
  components: {
    ExpandableCardMaterial,
    Message,
    SimpleFileUploader,
    CardMaterial,
    MultilangField,
  },
})
export default class SettingTab extends mixins(
  ConfigurationStep,
  ValidableForm
) {
  @appData.State((state: Data) => state.paymentModules)
  paymentModules!: PaymentModule[];
  @configuration.State((s: ConfiguratorState) => s.isStepper)
  isStepper!: boolean;

  @data.Action('updatePaymentModuleInApp') updatePaymentModuleInApp!: (item: PaymentModule) => Promise<void>;

  firebaseSteps: string[] = [];
  private validationIndex: number | null = null;
  shake = false;
  timeout: number | null = null;
  nameOfFile: string | null = null;

  mounted() {
    this.validationIndex = this.pushValidable(this.checkFirebase);
    if (!this.isShopify) {
      this.setNavigation({name: "payment_modules"});
    }
  }

  destroyed() {
    if (this.validationIndex) {
      this.removeValidation(this.validationIndex);
    }
  }

  get sandBoxMode() {
    return this.$store.state.isPaypalSandbox;
  }

  get showPaypal(){
      return !!this.paymentModules.find((item) => item.moduleName.includes('paypal') && item.use_in_app);
  }

  private async checkFirebase(): Promise<boolean> {
    const valid =
      (this.nameOfFile !== "" && this.nameOfFile !== null) ||
        (this.form.firebaseConfiguration?.name !== "" &&
            this.form.firebaseConfiguration?.name !== null);

    if (!valid) {
      this.shake = true;
      if (this.timeout !== null) {
        clearTimeout(this.timeout as number);
      }

      this.timeout = setTimeout(() => {
        this.shake = false;
      }, 1500);
    }
    return valid;
  }

  async uploadFirebase(file: any) {
    const files: FileList = file.target.files;
    const reader = new FileReader();
    this.nameOfFile = files[0].name;

    reader.onloadend = (e) => {
      let json: string;

      if (e.target && e.target.result && typeof e.target.result === "string") {
        json = e.target.result as string;

        this.updateFormData({
          firebaseConfiguration: { value: json, name: files[0].name },
        });
      } else {
        this.updateFormData({
          firebaseConfiguration: { value: null, name: null },
        });
      }
    };

    reader.readAsText(files[0]);
  }

  get firebaseStatusText() {
    const key = this.form.firebaseConfiguration?.active
      ? "static.firebaseEnable"
      : "static.firebaseDisable";

    return this.$t(key);
  }

  get firebaseText() {
    const filename = this.form.firebaseConfiguration?.name;
    return this.form.firebaseConfiguration?.active
      ? this.$t("hints.firebase", { filename })
      : null;
  }

  get isFirebaseConfigured(): boolean {
    const filename = this.form.firebaseConfiguration?.name;
    return !!(filename && this.form.firebaseConfiguration?.active);
  }

  get tokenValidators() {
    return {
      required: true,
      regex: /^\S*$/,
    };
  }

  get tableCols(): string[] {
    return [
      'moduleName',
      'version',
      'status',
      'supported',
      'useInAppAction'
    ]
  }

  icon(isTrue: boolean) {
    return isTrue ? "checkbox-marked-circle" : "close-circle";
  }

  iconColor(isTrue: boolean) {
    return isTrue ? 'green' : 'yellow';
  }

  async toggleUseInAppModal(paymentModule: PaymentModule) {
    await this.updatePaymentModuleInApp(paymentModule);
  }
}
