import { productCatalog } from "./mockData";
import Vue from "vue";
import store from "./store";
import AppConfigurator from "@/AppConfigurator.vue";
import Vuetify from "vuetify";
import "vuetify/dist/vuetify.min.css";
import i18n from "@/plugins/translations";
import "@/plugins/vee-validate";
import FileUploadService from "@/services/FileUploadService";
import AppDataService from "@/services/AppDataService";
import axios from '@/services/axios';

Vue.prototype.$uploader = FileUploadService;
Vue.prototype.$cache = AppDataService;

const vuetify = new Vuetify({
  icons: { iconfont: "mdi" },
});
Vue.config.productionTip = false;
Vue.use(Vuetify);

new Vue({
  vuetify,
  store,
  i18n,
  beforeMount() {
    window.widget = this;

    const attributeApiBaseUrl: string | null = this.$el.getAttribute("api-base-url");
    const attributeApiAdminUrl: string | null = this.$el.getAttribute("api-admin-url");
    const attributePaypalSandboxMode: string | null = this.$el.getAttribute("paypal-sandbox-mode");
    const attributeShopDescription: string | null = this.$el.getAttribute("shop-description");
    const attributeIsMultistore: string | null = this.$el.getAttribute("is-multistore");

    const apiBaseUrl: string = attributeApiBaseUrl != null ? attributeApiBaseUrl : process.env.VUE_APP_BASE_URL;
    const apiAdminUrl: string = attributeApiAdminUrl != null ? attributeApiAdminUrl : process.env.VUE_APP_API_ADMIN_URL;
    const paypalSandboxMode: string = attributePaypalSandboxMode || process.env.VUE_PAYPAL_SANDBOX_MODE;
    const shopDescription: string = attributeShopDescription || process.env.VUE_SHOP_DESCRIPTION;
    const isMultistore: string = attributeIsMultistore || process.env.VUE_IS_MULTISTORE;
    const isPsBilling: string | null = this.$el.getAttribute("is-ps-billing");
    const shopifyToken: string | null =
      this.$el.getAttribute("is-shopify") != null
        ? this.$el.getAttribute("is-shopify")
        : process.env.VUE_APP_IS_SHOPIFY;

    // Update axios base URL for prevent error on first load
    axios.defaults.baseURL = apiAdminUrl;
    this.$store.commit('setApiAdminUrl', apiAdminUrl);
    this.$store.commit('setApiBaseUrl', apiBaseUrl);

    // If there is shopify token
    if (shopifyToken != null) {
      this.$store.commit('setShopify', {
        token: shopifyToken
      });
    } else {
      this.$store.commit('setShopify', null);
    }

    this.$store.commit('setPaypalSandbox', paypalSandboxMode === 'true');
    this.$store.commit('setShopDescription', shopDescription);
    this.$store.commit('setIsMultistore', isMultistore === 'true');

    // Disable ps billing
    if (isPsBilling == null || !isPsBilling) {
      window.psBillingContext = null;
    }
  },
  render(h) {
    return h(AppConfigurator, { props: { navigator: this.navigator } });
  },
  data: {
    navigator: true
  },
  mounted() {
    if (process.env.VUE_APP_SKELETON_MODE == true) {
      this.$store.commit("simulator/setProductsCatalog", productCatalog);
      this.navigator = true;
    }
    this.$store.dispatch("init");
  },
}).$mount("#mrshop-widget");
