
import { Component, Vue, Prop } from "vue-property-decorator";
import { Getter } from "vuex-class";
import ValidableForm from "@/mixins/ValidableForm";
import { mixins } from "vue-class-component";
import ConfigurationStep from "@/mixins/ConfigurationStep";

@Component({})
export default class Carousel extends mixins(ConfigurationStep, ValidableForm) {
  @Prop({ default: "0.00" }) defaultPrice!: string;
  @Prop({ default: true, type: Boolean }) actionClick!: boolean;
  @Getter appDarkMode!: boolean;

  goToPaymentsPage() {
    if (!this.actionClick) return;
    if (!this.isShopify) {
      this.setNavigation({ name: "payment_modules" });
    }
  }
}
