import AbstractApiService from "@/services/AbstractApiService";
import axios from "@/services/axios";
import { AxiosResponse } from "axios";
import HomeSection, {
  HomeBulkActions,
} from "@/interfaces/HomeSection";
import { SearchableEntity } from "@/interfaces/SearchableEntity";
import { Count, Filter } from "@/interfaces/Api";

type SectionDoc = {
  title: string;
  message: string;
};

class SectionService extends AbstractApiService {
  protected action = "homes";

  async count(): Promise<Count> {
    const url = this.getCountUrl();
    const response: AxiosResponse<Count> = await axios.get(url);
    return response.data;
  }

  async find(filter: Filter): Promise<HomeSection[]> {
    const url = this.filterUrl(filter);
    const response = await axios.get(url);
    return response.data;
  }

  async all(): Promise<HomeSection[]> {
    try {
        const url = this.getUrl();
        const response: AxiosResponse<HomeSection[]> = await axios.get(url);
        return response.data;
    } catch (error) {
        console.log(error)
        return []
    }

  }

  async one(id: number): Promise<HomeSection> {
    const url = this.getUrl(id);
    const response: AxiosResponse<HomeSection> = await axios.get(url);
    return response.data;
  }

  async deleteSection(id: number): Promise<void> {
    const url = this.getUrl(id);
    await axios.delete(url);
  }

  async saveSection(section: HomeSection): Promise<HomeSection> {
    const url = this.getUrl(section.id);
    const data: any = section;
    const squash = ["product", "cms", "manufacturer", "category"];

    for (const s of squash) {
      let id = null;
      if (data[s] && data[s].id) {
        id = data[s].id;
      }

      delete data[s];
      data["id_" + s] = id;
    }

    delete data.items;
    const saved: AxiosResponse<HomeSection> = await axios.post(url, data);
    return saved.data;
  }

  async bulkSectionAction(
    bulkAction: HomeBulkActions,
    ids: number[]
  ): Promise<HomeSection[]> {
    const url = this.getUrl();
    const data = { bulkAction, ids };
    const response: AxiosResponse<HomeSection[]> = await axios.post(url, data);
    return response.data;
  }

  async getSectionDocs(): Promise<SectionDoc> {
    return {
      title: `Doc step`,
      message: `It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. Various versions have evolved over the years, sometimes by accident, sometimes on purpose (injected humour and the like).


      Where does it come from?
      Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections 1.10.32 and 1.10.33 of "de Finibus Bonorum et Malorum" (The Extremes of Good and Evil) by Cicero, written in 45 BC. This book is a treatise on the theory of ethics, very popular during the Renaissance. The first line of Lorem Ipsum, "Lorem ipsum dolor sit amet..", comes from a line in section 1.10.32.
      
      The standard chunk of Lorem Ipsum used since the 1500s is reproduced below for those interested. Sections 1.10.32 and 1.10.33 from "de Finibus Bonorum et Malorum" by Cicero are also reproduced in their exact original form, accompanied by English versions from the 1914 translation by H. Rackham.
      
      Where can I get some?
      There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which don't look even slightly believable. If you are going to use a passage of Lorem Ipsum, you need to be sure there isn't anything embarrassing hidden in the middle of text. All the Lorem Ipsum generators on the Internet tend to repeat predefined chunks as necessary, making this the first true generator on the Internet. It uses a dictionary of over 200 Latin words, combined with a handful of model sentence structures, to generate Lorem Ipsum which looks reasonable. The generated Lorem Ipsum is therefore always free from repetition, injected humour, or non-characteristic words etc.`,
    };
  }
}

export default new SectionService();
