
import { mixins } from "vue-class-component";
import ConfigurationStep from "@/mixins/ConfigurationStep";
import { Component } from "vue-property-decorator";
import { namespace } from "vuex-class";
import { Language } from "@/interfaces/Language";
import ImageUploader from "@/components/AppSettings/UI/ImageUploader.vue";
import { FileType } from "@/interfaces/AppSteppers";
import Message from "@/components/AppSettings/UI/Message.vue";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";
import MultilangField from "@/components/AppSettings/UI/MultilangField.vue";
import ValidableForm from "@/mixins/ValidableForm";
import { UploadedImage } from "@/interfaces/Upload";
import NavigationManager from "@/mixins/NavigationManager";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";

const configuration = namespace("configuration");
@Component({
  components: {
    ImageUploader,
    Message,
    MultilangField,
    CardMaterial,
  },
})
export default class StoreTab extends mixins(
  ConfigurationStep,
  ValidableForm,
  NavigationManager
) {
  @configuration.State((state: ConfiguratorState) => state.languages)
  languages!: Array<Language>;
  widthHeight = 80;

  updateIcon(icon: UploadedImage | null) {
    this.updateFormData({ [FileType.icon]: icon });
    this.setNavigation({ name: "spring-board" });
  }

  updateSplash(splash: UploadedImage | null) {
    this.updateFormData({ [FileType.splash]: splash });
    this.setNavigation({ name: "splash" });
  }

  updateDarkSplash(darkSplash: UploadedImage | null) {
    this.updateFormData({ [FileType.darkSplash]: darkSplash });
    this.setNavigation({ name: "splash" });
  }
}
