
import { Component, Prop } from "vue-property-decorator";
import AppSteppers from "./components/AppSettings/AppSteppers.vue";
import AppSettings from "@/components/AppSettings/AppSettings.vue";
import { namespace } from "vuex-class";
import DialogModal from "@/components/AppSettings/UI/DialogModal.vue";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";
import Notifications from "@/components/AppSettings/UI/Notifications.vue";
import Message from "@/components/AppSettings/UI/Message.vue";
import HeaderActions from "@/components/AppSettings/UI/HeaderActions.vue";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";
import AppDarkModeButton from "@/components/AppSimulator/UI/AppDarkModeButton.vue";
import AppSimulator from "@/components/AppSimulator/AppSimulator.vue";
import { mixins } from "vue-class-component";
import ShopifyManager from "./mixins/ShopifyManager";
import TranslationsService from "./services/TranslationsService";
import i18n from "./plugins/translations";

const configuration = namespace("configuration");
const customer = namespace("customer");
@Component({
  components: {
    Message,
    AppSteppers,
    AppSettings,
    DialogModal,
    Notifications,
    HeaderActions,
    CardMaterial,
    AppDarkModeButton,
    AppSimulator,
  },
})
export default class AppConfigurator extends mixins(ShopifyManager) {
  @Prop() navigator!: boolean;
  @configuration.State((s: ConfiguratorState) => s.isStepper)
  stepperMode!: boolean;
  @configuration.Action("getAppQrCode") getAppQrCode!: () => void;
  @configuration.Getter("hasQr") hasQr!: boolean;
  @configuration.Getter("qrUrl") qrUrl!: string;
  @customer.Action("checkStatus") checkStatus!: () => Promise<void>;

  contentLoaded = false;
  showAppPreview = true;

  showQrCodeModal = false;
  loadingQr = false;
  clearingCache = false;

  modalStatus = true;

  async beforeMount() {
    try {
      await this.checkStatus();
    } catch (error) {
      console.log("error");
    }
  }

  loadLocaleMessages(currentLocale) {
    const locales = require.context(
      "./locales",
      true,
      /[A-Za-z0-9-_,\s]+\.json$/i
    );

    let messages = null;
    let enKey: string | null = null;

    locales.keys().forEach((key) => {
      const matched = key.match(/([A-Za-z0-9-_]+)\./i);

      if (matched && matched.length > 1) {
        const locale = matched[1];
        if (currentLocale == locale) {
          messages = locales(key);
        } else if (locale == "en") {
          enKey = key;
        }
      }
    });

    if (messages == null) {
      messages = enKey ? locales(enKey) : {};
    }

    return messages;
  }

  async mounted() {
    TranslationsService.getAll().then((value) => {
      let browserLocale: string | null = navigator.language;
      if (browserLocale.includes("-")) {
        browserLocale = browserLocale.split("-")[0];
      }

      const messages = Object.assign(value, this.loadLocaleMessages("ci"));

      i18n.setLocaleMessage(browserLocale, messages);
      i18n.locale = browserLocale;
    }),
      await Promise.all([
        this.$store.dispatch("data/getPaymentModules"),
        this.$store.dispatch("configuration/init"),
        this.$store.dispatch("simulator/init"),
        this.$store.dispatch("simulator/getProducts"),
        this.$store.dispatch("data/getSelectItems"),
      ]);
    this.contentLoaded = true;
  }

  toggleQrCodeModal() {
    this.showQrCodeModal = !this.showQrCodeModal;
  }

  toggleAppPreview(event: boolean) {
    this.showAppPreview = event;
  }

  async onClickTestApp() {
    await this.getAppQrCode();
    this.showQrCodeModal = this.qrUrl != null;
    // await this.clearCache();
  }

  async clearCache() {
    this.clearingCache = true;
    try {
      await this.$cache.clearCache();
    } finally {
      this.clearingCache = false;
    }
  }

  get testAppButton(): string {
    return this.$t("stepperButtons.testAppButton") as string;
  }
}
