import SectionService from "@/services/SectionService";
import HomeSection from "@/interfaces/HomeSection";
import {CmsPageState, SimulatorState} from "@/interfaces/Simulator";
import {ActionTree, GetterTree, Module, MutationTree} from "vuex";
import {Pagination} from "@/interfaces/Pagination";
import AppDataService from "@/services/AppDataService";
import Manufacturer from "@/interfaces/Manufacturer";
import CategoryService from "@/services/CategoryService";
import {CatalogLayoutType} from "@/interfaces/ConfigurationsType";
import Category from "@/interfaces/Category";
import Product from "@/interfaces/Product";

const state: SimulatorState = {
  categories: [],
  sections: [],
  products: [],
  productsCatalog: [],
  manufacturers: [],
  webpath: null,
  mutation: null,
  categoryId: null,
  manufacturer_id: null,
  cmsPage: null,
  groupToShow: 0,
  pagination: {
    count: 0,
    page: 1,
    refresh: false,
    context: "catalog_manufacturers",
  },
};

const mutations: MutationTree<SimulatorState> = {
  setSections(state: SimulatorState, sections: HomeSection[]) {
    state.sections = sections;
  },
  removeSection(state: SimulatorState, section: HomeSection) {
    state.sections = state.sections.filter(
      (e: HomeSection) => e.id !== section.id
    );
  },
  pushSection(state: SimulatorState, section: HomeSection) {
    const foundIndex = state.sections.findIndex(
      (e: HomeSection) => e.id == section.id
    );
    if (foundIndex > -1) {
      state.sections[foundIndex] = section;
    } else {
      state.sections = [...state.sections, section];
    }

    state.sections = [...state.sections].sort(
      (a: HomeSection, b: HomeSection) => {
        return a.order - b.order;
      }
    );
  },
  setManufacturers(state: SimulatorState, manufactuers: Manufacturer[]) {
    state.manufacturers = [...state.manufacturers, ...manufactuers];
  },
  setCategories(state: SimulatorState, categories: Category[]) {
    state.categories = [...categories];
  },
  setProducts(state: SimulatorState, products: Product[]) {
    state.products = [...products];
  },
  setProductsCatalog(state: SimulatorState, productsCatalog: Product[]) {
    state.productsCatalog = productsCatalog;
  },
  setSelectedProducts(state: SimulatorState, productsCatalog: Product[]) {
    state.productsCatalog = [...productsCatalog];
  },
  setPagination(state: SimulatorState, pagination: Pagination) {
    state.pagination = { ...state.pagination, ...pagination };
  },
  setWebPath(state: SimulatorState, webpath: string) {
    state.webpath = webpath;
  },
  setCategoryId(state: SimulatorState, categoryId: string) {
    state.categoryId = categoryId;
  },
  setManufacturerId(state: SimulatorState, manufacturer_id: string) {
    state.manufacturer_id = manufacturer_id;
  },
  setMutation(state: SimulatorState, mutation: any) {
    state.mutation = mutation;
  },
  setGroupToShow(state: SimulatorState, groupToShow: number) {
    state.groupToShow = groupToShow;
  },
  setCMSPage(state: SimulatorState, cmsPage: CmsPageState){
    state.cmsPage = {...cmsPage, url: cmsPage.url + 'q=' + new Date().getTime()}
  },
  reset(state: SimulatorState) {
    state.categories = [];
    state.productsCatalog = [];
    state.manufacturers = [];
    state.pagination = {
      count: 0,
      page: 1,
      refresh: false,
      context: "",
    };
  },
};

const getters: GetterTree<SimulatorState, any> = {
  activeSections(state: SimulatorState): HomeSection[] {
    let sections = state.sections.filter((s) => s.active);
    if (state.groupToShow) {
      sections = sections.filter(
        (s) => s.groupsToShow?.includes(state.groupToShow) || s.showToAllGroups);
    }
    return sections;
  },
  getPagination(state: SimulatorState): Pagination {
    return state.pagination;
  },
  getCategoryId(state: SimulatorState): any {
    return state.categoryId;
  },
  getWebPath(state: SimulatorState): any {
    return state.webpath;
  },
  products(state: SimulatorState) {
    return state.products;
  },
  productsCatalog(state: SimulatorState) {
    return state.productsCatalog;
  },
  cmsPage(state: SimulatorState): { id: number; url: string | null } | null {
    return state.cmsPage;
  },
};

const actions: ActionTree<SimulatorState, any> = {
  async init({ commit }) {
    const sections = await SectionService.all();
    commit("setSections", sections);
  },
  async getManufacturers({ commit, state }) {
    if (
      state.manufacturers.length < state.pagination.count ||
      state.pagination.count == 0
    ) {
      const res = await AppDataService.getManufacturers(state.pagination.page);
      commit("setPagination", {
        page: state.pagination.page + 1,
        count: res.count,
      });
      commit("setManufacturers", res.result);
    }
  },
  async getCategories({ commit }, idLayout: CatalogLayoutType) {
    const categories = await CategoryService.getCategories(idLayout);
    commit(
      "setCategories",
      categories.map((item) => ({ ...item, open: false }))
    );
  },

  async getProducts({ commit }) {
    try {
      const products = await AppDataService.getProducts();
      commit("setProducts", products);
      commit("setProductsCatalog", products);
    } catch (exception) {
      console.log(exception);
    }
  },

  async getProductsFromWebPath({ commit, state }) {
    if (
      state.productsCatalog.length < state.pagination.count ||
      state.pagination.count == 0
    ) {
      try {
        const res = await AppDataService.getProductsFromWebPath(
          state.pagination.page,
          state.webpath
        );
        commit("setPagination", {
          page: state.pagination.page + 1,
          count: res.count,
        });
        commit("setProducts", res.result);
        commit("setProductsCatalog", res.result);
      } catch (exception) {
        console.log(exception);
      }
    }
  },
  async getProductsFromManufacturer({ commit, state }) {
    if (
      state.productsCatalog.length < state.pagination.count ||
      state.pagination.count == 0
    ) {
      try {
        const res = await AppDataService.getProductsFromManufacturer(
          state.pagination.page,
          state.manufacturer_id
        );
        commit("setPagination", {
          page: state.pagination.page + 1,
          count: res.count,
        });
        commit("setProducts", res.result);
        commit("setProductsCatalog", res.result);
      } catch (exception) {
        console.log(exception);
      }
    }
  },

  async getProductsCategoryId({ commit, state }) {
    if (
      state.productsCatalog.length < state.pagination.count ||
      state.pagination.count == 0
    ) {
      try {
        const res = await AppDataService.getAllProducts(
          state.pagination.page,
          state.categoryId
        );
        commit("setPagination", {
          page: state.pagination.page + 1,
          count: res.count || 0,
        });
        commit("setProducts", res.result);
        commit("setProductsCatalog", res.result);
      } catch (exception) {
        console.log(exception);
      }
    }
  },

  async getCategoryProducts({ commit }, categoryId) {
    const res = await AppDataService.getCategoryProduct(
      categoryId,
      state.pagination.page
    );
    commit("setProductsCatalog", res);
  },

  async setCMSPage({ commit }, id) {
    commit("setCMSPage", await AppDataService.getCMSPage(id));
  },

  async getSingleProduct({ commit }, id) {
    return await AppDataService.getSingleProduct(id);
  },

  async getGeneric({ dispatch, commit, state }, mutation?) {
    if (mutation) {
      commit("setMutation", mutation);
    }
    switch (state.pagination.context) {
      case "catalog_manufacturers":
        dispatch("getManufacturers");
        break;
      case "catalog":
        dispatch(state.mutation.action, state.mutation.catalogLayoutType);
        break;
      case "list":
        dispatch(state.mutation.action);
        break;
    }
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
  namespaced: true,
} as Module<SimulatorState, any>;
