import { MutationPayload, Store } from "vuex";
const VuexStepper = (store: Store<any>) => {
  store.subscribe((mutation: MutationPayload) => {
    switch (mutation.type) {
      case "configuration/init":
        if (mutation.payload.isStepper) {
          store.commit('setStepKey', mutation.payload.currentStep as number);
        } else {
          store.commit('setStepKey', 1);
        }
        break;
      case "configuration/setCurrentStep":
        store.commit('setStepKey', mutation.payload as number);
        break;
    }
  });
};

export default VuexStepper;
