import { Vue, Component } from "vue-property-decorator";
import { Mutation, Getter, namespace } from "vuex-class";
import { Navigation } from "@/interfaces/Store";
import { ConfiguratorState } from "@/interfaces/ConfigurationsType";

const configuration = namespace("configuration");

@Component
export default class NavigationManager extends Vue {
  @Getter currentNavigation!: Navigation;
  @Mutation setNavigation!: (payload: Navigation) => void;
  @configuration.State((e: ConfiguratorState) => e.form.name) title!: string;

  get style() {
    return (tab: string) => ({
      color: this.isTabActive(tab)
        ? this.$store.state.appSteppers.data.form.secondaryColor
        : null,
    });
  }

  get isTabActive() {
    return (tab: string) => tab == this.tab;
  }

  get tab(): string {
    return this.currentNavigation.selectedIcon !== undefined
      ? this.currentNavigation.selectedIcon
      : this.currentNavigation.name;
  }
}
