import AbstractApiService from "@/services/AbstractApiService";
import axios from "@/services/axios";
import { AxiosResponse } from "axios";
import { ChangeFile } from "@/interfaces/AppSteppers";
import { UploadedImage } from "@/interfaces/Upload";

export interface FileUploader {
  uploadFile(fileData: ChangeFile): Promise<UploadedImage>;
}

class FileUploadService extends AbstractApiService implements FileUploader {
  protected action = "image";
  async uploadFile(fileData: ChangeFile): Promise<UploadedImage> {
    const data = new FormData();
    data.append("file", fileData.file as File);
    data.append("type", fileData.type);

    if (this.isShopify()) {
      const response: AxiosResponse<UploadedImage> = await axios.put(
        this.getUrl(),
        data,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
          type: "file",
        }
      );

      return response.data;
    } else {
      const response: AxiosResponse<UploadedImage> = await axios.post(
        this.getUrl(),
        data,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
          type: "file",
        }
      );

      return response.data;
    }
  }
}

export default new FileUploadService();
