
import { Vue, Component, Prop } from "vue-property-decorator";
import { SearchableEntity } from "@/interfaces/SearchableEntity";
import AppDataService from "@/services/AppDataService";
const DEBOUNCE_INTERVAL = 750;

@Component({})
export default class MultipleEntitySearcher extends Vue {
  @Prop() value!: SearchableEntity[];
  @Prop() entityType!: string;
  @Prop() label!: string;

  loading = false;
  timeout: any = null;
  currentSearch: string | null = null;
  foundItems: SearchableEntity[] = [];

  mounted() {
    this.foundItems = this.value;
  }

  search(term: string | null) {
    if (this.timeout != null) {
      clearTimeout(this.timeout as any);
    }

    this.currentSearch = term;
    this.timeout = setTimeout(() => {
      this.runSearch();
    }, DEBOUNCE_INTERVAL);
  }

  async runSearch() {
    if (!this.currentSearch) {
      this.foundItems = [];
      return;
    }

    this.loading = true;
    try {
      this.foundItems = [
        ...this.foundItems,
        ...(await AppDataService.searchItems(
          this.entityType,
          this.currentSearch
        )),
      ];
    } catch (e) {
      console.log(e);
    }

    this.loading = false;
    clearTimeout(this.timeout);
  }

  emitItem(items: SearchableEntity[]) {
    this.$emit("input", items);
    this.currentSearch = null;
    this.foundItems = items;
  }
}
