
import { Component, Prop, Vue } from "vue-property-decorator";
import moment from "moment";
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";

@Component({
  components: {
    datepicker: DatePicker as any,
  },
})
export default class DateTimePicker extends Vue {
  @Prop() value!: string;
  @Prop({ default: "" }) label!: string;
  @Prop() disabled!: boolean;
  @Prop({ default: "date" }) type!: "date" | "datetime";
  @Prop({ default: true, type: Boolean }) isTimePicker!: boolean;
  @Prop({ default: false }) forceTime!: boolean;
  @Prop() min: any;
  @Prop() max: any;
  showTimePanel = false;
  date = 0;

  updateValue(event: string) {
    let val = moment(event).valueOf();
    if (this.forceTime) {
      val = moment(event).endOf("day").valueOf();
    }

    this.$emit("onValueChange", val);
  }

  toggleTimePanel() {
    // this.showTimePanel = !this.showTimePanel;
  }

  mounted() {
    this.date = this.value ? moment(this.value).valueOf() : moment().valueOf();
  }

  get datetime() {
    if (!this.value) {
      return null;
    }

    return moment(this.value).format("DD/MM/YYYY HH:mm");
  }

  disabledDate(date: any) {
    if (this.max) {
      return (
        date > moment(this.max).startOf("day") || date < moment().startOf("day")
      );
    } else {
      return (
        date < moment(this.min).startOf("day") || date < moment().startOf("day")
      );
    }
  }

  get isDateTime(): boolean {
    return this.type === "datetime";
  }

  // disabledTime(date: any) {
  // 	return date < moment().startOf('minute');
  // }
}
