
import { Component, Prop, Vue } from "vue-property-decorator";
import {
  SubscriptionModuleVersion,
  SubscriptionModule,
} from "@/interfaces/Module";
import ConfigurationService from "@/services/ConfigurationService";
import GroupText from "@/components/AppSettings/UI/GroupText.vue";
import { GroupTextItem } from "@/interfaces/GroupText";

@Component({
  components: {
    GroupText,
  },
})
export default class ModuleVersionModal extends Vue {
  @Prop({ default: null }) item!: SubscriptionModule | null;

  loading = false;
  moduleVersions: SubscriptionModuleVersion[] = [];

  async mounted() {
    await this.getModuleVersions();
  }

  onClickOutside() {
    this.$emit("close");
  }

  async getModuleVersions() {
    if (this.item) {
      this.loading = true;
      this.moduleVersions = await ConfigurationService.getModuleVersions(
        this.item.moduleName
      );
      this.loading = false;
    }
  }

  get items(): GroupTextItem[] {
    return this.moduleVersions.map((mv) => {
      return {
        text: mv.version,
        value: mv.notes,
        type: "text",
      };
    });
  }

  onDownload() {
    if (this.item) {
      const link = document.createElement("a");
      link.href = this.item.url;
      link.setAttribute("download", "file.zip");
      document.body.appendChild(link);
      link.click();
    }
  }
}
