
import { Component, Vue } from "vue-property-decorator";

import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";
import { namespace } from "vuex-class";
import { CustomerState } from "@/store/modules/customer";
import { SubscriptionModule } from "@/interfaces/Module";
import ModuleVersionModal from "@/components/AppSettings/UI/ModuleVersionModal.vue";

const customer = namespace("customer");

@Component({
  components: {
    CardMaterial,
    ModuleVersionModal,
  },
})
export default class ModulesTab extends Vue {
  @customer.State((state: CustomerState) => state.modules)
  modules!: SubscriptionModule[];

  currentModule: SubscriptionModule | null = null;

  get tableCols(): string[] {
    return ["image", "modulename", "installed", "updated", "changelog"];
  }

  async onDownload(downloadUrl: string) {
    const link = document.createElement("a");
    link.href = downloadUrl;
    link.setAttribute("download", "file.zip");
    document.body.appendChild(link);
    link.click();
  }

  onOpenVersion(item: SubscriptionModule) {
    this.currentModule = item;
  }

  onCloseModalVersion() {
    this.currentModule = null;
  }

  installIconColor(item: SubscriptionModule) {
    return item.needInstall ? "grey" : "green";
  }

  installIcon(item: SubscriptionModule) {
    return item.needInstall ? "close-circle" : "checkbox-marked-circle";
  }

  get hasTableVisible() {
    return this.modules != null && this.modules.length > 0;
  }
}
