
import { Component } from "vue-property-decorator";
import ProductsList from "@/components/AppSimulator/UI/ProductsList.vue";
import CategoriesList from "@/components/AppSimulator/UI/CategoriesList.vue";
import { Getter, Mutation, namespace } from "vuex-class";
import Category from "../../../interfaces/Category";
import {
  CatalogLayoutType,
  ConfiguratorState,
} from "@/interfaces/ConfigurationsType";
import { Navigation } from "@/interfaces/Store";
import { mixins } from "vue-class-component";
import AppStatusManager from "@/mixins/AppStatusManager";
import Product from "@/interfaces/Product";
import { SimulatorState } from "@/interfaces/Simulator";

const configuration = namespace("configuration");
const simulator = namespace("simulator");

@Component({
  components: {
    ProductsList,
    CategoriesList,
  },
})
export default class CatalogPage extends mixins(AppStatusManager) {
  @simulator.State((s: SimulatorState) => s.categories) categories!: Category[];
  @Mutation setNavigation!: (payload: Navigation) => void;
  @Getter appDarkMode!: boolean;
  @simulator.Mutation("setProductsCatalog") setProductsCatalog!: (
    productsCatalog: Product[]
  ) => void;
  @simulator.Mutation setCategoryId!: (category_id: any) => void;
  @configuration.State(
    (state: ConfiguratorState) => state.form.catalogLayoutType
  )
  catalogLayoutType!: CatalogLayoutType;

  setOpened(index) {
    if (this.categories[index].categories) {
      this.categories[index].open = !this.categories[index].open;
    } else {
      this.navigateTo("list", this.categories[index]);
    }
  }

  mounted() {
    this.$emit("initComponent", {
      action: "getCategories",
      catalogLayoutType: this.form.catalogLayoutType,
    });
  }

  async navigateTo(name: string, category: Category) {
    this.setNavigation({
      name: name,
      listType: "catalog",
      context: { name: category.name } as any,
    });
    this.setCategoryId(category.id);
  }

  get isList(): boolean {
    return this.catalogLayoutType == CatalogLayoutType.list;
  }
}
