
import { Component } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import ConfigurationStep from "@/mixins/ConfigurationStep";
import ColorPicker from "@/components/AppSettings/UI/ColorPicker.vue";
import { namespace } from "vuex-class";
import ValidableForm from "@/mixins/ValidableForm";
import CardMaterial from "@/components/AppSettings/UI/CardMaterial.vue";

const appSteppers = namespace("appSteppers");
@Component({
  components: {
    ColorPicker,
    CardMaterial,
  },
})
export default class ColorsTab extends mixins(
  ConfigurationStep,
  ValidableForm
) {
  required = true;

  onUpdateColor(color: any) {
    this.updateFormData({ [color.name]: color.value });
    if (color.name === "mainColor" || color.name === "textMainColor") {
      this.setNavigation({
        name: "home",
      });
    } else {
      this.toExampleProduct();
    }
  }

  setColor(value: string | null, c: number): string {
    let colorUp = "";
    switch (c) {
      case 0: {
        colorUp = "#46709b";
        break;
      }
      case 1: {
        colorUp = "#FFFFFF";
        break;
      }
      case 2: {
        colorUp = "fc5721";
        break;
      }
      case 3: {
        colorUp = "#FFFFFF";
        break;
      }
      default: {
        colorUp = "#60788B";
        break;
      }
    }

    return value ? value : colorUp;
  }
}
