
import { Component, Vue, Watch } from "vue-property-decorator";
import { Filter, PaginationState, Count } from "@/interfaces/Api";
import { namespace } from "vuex-class";

const pagination = namespace("pagination");
@Component
export default class PaginationController extends Vue {
  @pagination.State((state: PaginationState) => state.total) total!: Count;
  @pagination.State((state: PaginationState) => state.filter) filter!: Filter;
  @pagination.State((state: PaginationState) => state.selectedPage)
  selectedPage!: number;
  @pagination.Mutation("setLimit") setLimit!: (limit: number) => void;
  @pagination.Mutation("setSelectedPage") setSelectedPage!: (
    page: number
  ) => void;
  @pagination.Mutation("reset") reset!: () => void;

  limit: number = 10;
  supportedLimitRange: any[] = [
    {
      text: "10",
      value: 10,
    },
    {
      text: "25",
      value: 25,
    },
    {
      text: "50",
      value: 50,
    },
    {
      text: "100",
      value: 100,
    },
  ];

  mounted() {
    this.reset();
  }

  @Watch("total")
  onCountChange() {
    if (this.total.count > 0) {
      this.onReload();
    }
  }

  @Watch("selectedPage")
  onChangeSelectedPage() {
    if (this.total.count > 0) {
      this.onReload();
    }
  }

  onChangeLimit(val: number) {
    if (this.total.count > 0) {
      this.setLimit(val);

      if (this.selectedPage !== 1) {
        this.setSelectedPage(1);
      } else {
        this.onReload();
      }
    }
  }

  // Start from 1
  get page(): number {
    const p = +this.total.count / this.filter.limit;
    if (p > 1) {
      const isFloat = Number(p) === p && p % 1 !== 0;
      if (!isFloat) {
        return p;
      }

      return (+(p).toFixed(0) + 1);
    }

    return 1;
  }

  onReload() {
    this.$emit("load", this.filter);
  }
}
