import { ShopifyException } from "@/interfaces/ShopifyException";
import AbstractApiService from "@/services/AbstractApiService";
import { Component, Vue } from "vue-property-decorator";

@Component
export default class ShopifyManager extends Vue {
  get isShopify(): boolean {
    return this.$store.getters['isShopify'];
  }

  get shopifyPlansUrl() {
    return AbstractApiService.getUrlForAction("plans");
  }

  get exceptions(): ShopifyException {
    return {
      tableCols: [
        'groups'
      ]
    }
  }
}
