
import { Component, Prop } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import { namespace, Getter } from "vuex-class";
import Product from "../../../interfaces/Product";
import WishlistAware from "@/mixins/WishlistAware";
import ProductsList from "@/components/AppSimulator/UI/ProductsList.vue";
import { Navigation } from "@/interfaces/Store";
import { SimulatorState } from "@/interfaces/Simulator";

const simulator = namespace("simulator");

@Component({
  components: {
    ProductsList,
  },
})
export default class ListPage extends mixins(WishlistAware) {
  @Getter appDarkMode!: boolean;
  @simulator.State((state: SimulatorState) => state.productsCatalog) productsCatalog!: Product[];
  @Getter currentNavigation!: Navigation;
  @simulator.Getter getCategoryId!: any;
  @simulator.Getter getWebPath!: any;
  @Prop({ default: false }) getResults!: boolean;

  mounted() {
    let action = "";
    if (this.currentNavigation.listType === "catalog") {
      action = "getProductsCategoryId";
    } else if (this.currentNavigation.listType === "manufacturers") {
      action = "getProductsFromManufacturer";
    } else if (this.currentNavigation.listType === "product") {
      action = "getProductsFromWebPath";
    } else {
      action = "getProducts";
    }

    this.$emit("initComponent", { action });
  }
}
