
import { Component } from "vue-property-decorator";
import { Mutation, namespace } from "vuex-class";
import Manufacturer from "@/interfaces/Manufacturer";
import { Navigation } from "@/interfaces/Store";
import {
  CatalogLayoutType,
  ConfiguratorState,
  FormData,
} from "@/interfaces/ConfigurationsType";
import HomeSectionsAware from "@/mixins/HomeSectionsAware";
import { mixins } from "vue-class-component";
import { SimulatorState } from "@/interfaces/Simulator";

const configuration = namespace("configuration");
const simulator = namespace("simulator");

@Component
export default class CatalogManufacturersPage extends mixins(
  HomeSectionsAware
) {
  @configuration.State((state: ConfiguratorState) => state.form)
  form!: FormData;
  @Mutation setNavigation!: (payload: Navigation) => void;
  @simulator.Mutation setManufacturerId!: (payload: any) => void;
  @simulator.State((state: SimulatorState) => state.manufacturers)
  manufacturers!: Manufacturer[];

  async mounted() {
    this.$emit("initComponent");
  }

  async goTomManufacturerPage(manufacturer) {
    this.setManufacturerId(manufacturer.id);
    this.setNavigation({
      name: "list",
      listType: "manufacturers",
      context: { name: manufacturer.name } as any,
    });
  }

  get layoutClass(): string {
    return this.form.catalogLayoutType == CatalogLayoutType.image
      ? "image"
      : "list";
  }
}
